@import '../../../../../../styles/variables';

.checklist-group-status {
  display: flex;
  justify-content: center;

  width: 94px;
  font-size: 11px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 1px;
  white-space: nowrap;
  border-radius: 0.5rem;
}

.checklist-group-status-archived {
  background-color: $wk-primary-gray-tint1;
  color: $wk-primary-white;
}

.checklist-group-status-completed {
  background-color: $wk-primary-green;
  color: $wk-primary-black;
}

.checklist-group-status-in-progress {
  background-color: $wk-secondary-orange;
  color: $wk-primary-black;
}

.checklist-group-status-not-started {
  background-color: $wk-primary-gray-tint4;
  color: $wk-primary-black;
}
