.flat-list-card {
  .flat-list-card-body {
    margin-bottom: 24px;
  }
  .primary-title {
    margin-bottom: 24px;
    display: flex;
    span {
      margin-right: 5px;
      margin-top: 1px;
    }
  }
  .card-footer .wk-button {
    text-align: left;
  }
}

.email-alerts-card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #dadada;
  border-top: 4px solid #dadada;
  background-image: linear-gradient(#ededed, #ffffff 50%);
}
