@import '~@wk/fundamentals/color.utils';
@import '~@wk/fundamentals/typography';
@import '~@wk/fundamentals/helpers';

.ac-list-item.ac-list-item_separated {
  @extend .wk-h6;
  @extend .wk-weight-500;
  color: wk-primary-color(gray, tint1);
  padding: 10px 0;
}

.ac-list-item.ac-list-item_separated .disabled {
  @include wk-is-disabled(1);
  color: wk-primary-color(gray, tint1);
}

.ac-list-item.ac-list-item_separated + .ac-list-item.ac-list-item_separated {
  border-top: solid wk-primary-color(gray, tint4) 1px;
}
