@import '/app-react/src/styles/variables';

:global {
  .warning-message-wrapper {
    @media screen and (min-width: 600px) {
      display: none;
    }
  }

  :local(.combinable-page) {
    .wide-header-page__main {
      padding-top: 0;
      padding-bottom: 0;
      display: block;

      &.wk-page-container {
        max-width: 100%;
        padding: 0;

        @media screen and (min-width: 75rem) {
          max-width: unset;
          padding-left: 44px;
          padding-right: 44px;
        }

        > .wk-row {
          display: block;
          margin: 0;
        }
      }
    }

    .combinable-page-header {
      background: $wk-primary-gray-tint5;

      & > .wk-row > * {
        @media screen and (max-width: 768px) {
          flex: 0 0 100%;
          width: 100%;
          max-width: 100%;
        }
      }
    }

    [class*='cg-tree-'] [class*='-tree-container'] {
      padding: 12px 16px 12px 8px;
    }

    .hidden {
      display: none;
    }

    .header_infoBannerVisible ~ .main-container {
      .wide-header-page__header {
        top: calc(89px + 76px);

        @media (max-width: 600px) {
          top: 184px;
        }
      }
    }

    .wide-header-page__header {
      position: sticky;
      top: 89px;
      z-index: 3;

      @media (max-width: 767px) {
        top: 56px;
      }

      @media (max-width: 600px) {
        top: 48px;
      }

      > .wk-page-container:not(.wk-row) {
        @media screen and (min-width: 75rem) {
          max-width: unset;
          padding-left: 44px;
          padding-right: 44px;
        }
      }
    }

    &.wk-page-container {
      max-width: 100%;
      padding: 0;

      > .wk-row {
        display: block;
        margin: 0;
      }
    }

    .search-term-navigation-wrapper {
      position: sticky;
      top: 161px;
      z-index: 2;

      .search-terms-navigation_button:not(.disabled) {
        cursor: pointer;
      }

      .document-search-terms-control_container {
        .back-to-search-link_container {
          padding-left: 10px;
        }

        .document-search-terms-control {
          min-height: 36px;
        }

        .search-terms-navigation {
          padding-right: 20px;
        }

        @media screen and (max-width: 1024px) {
          .back-to-search-link_container {
            padding-left: 0;
          }

          &.wk-page-container {
            padding-left: 25px;
            padding-right: 15px;
          }
        }

        @media screen and (min-width: 1024px) {
          &.wk-page-container {
            max-width: 100%;
          }
        }
      }
    }

    :local(.browse-tree-with-document),
    .comb-browse-tree-wrapper {
      position: relative;
      display: flex;
      flex-wrap: nowrap;
      width: 100%;

      @media screen and (min-width: 75rem) {
        &.wk-page-container:not(.wk-row) {
          max-width: 100%;
          padding: 0;
        }
      }

      .comb-browse-tree {
        height: 100%;
        position: sticky;
        z-index: 1;

        .adaptive-browse-tree {
          .top-panel {
            .cg-segmented-control-body {
              width: 97%;
              min-width: 300px;
              max-width: 423px;
            }
          }
        }
      }

      .tabs-panel-wrapper {
        position: sticky;
        height: 60px;
        z-index: 2;
        overflow: hidden;

        .tabs-panel {
          padding-top: 20px;
          flex: 0 0 100%;
          width: 100%;

          position: static;
          background-color: #fff;

          & > .wk-page-container {
            max-width: unset;
            padding-left: 0;
            padding-right: 0;
          }

          .tab-button {
            white-space: nowrap;
          }
        }
      }
    }
  }
}
