.filing-entire-view {
  width: 75%;
  margin: 0 auto;
  margin-top: 50px;

  :global {
    .highlight {
      background-color: #fffb00;

      &.current {
        background-color: #0768a9;
        color: #fff;
      }
    }

    a[href^="#"] * {
      pointer-events: none;
    }
  }
}
