@import '/app-react/src/styles/mixins';
@import '/app-react/src/styles/helpers';
@import '/app-react/src/styles/variables';

.search-terms-control {
  &:global(.document-search-terms-control_container.document-search-terms-control_sticky-container) {
    position: sticky;
    z-index: 1;
    top: 89px;

    @media screen and (max-width: 768px) {
      top: 48px;
    }
  }
}

:global {
  .search-terms-control.standalone-page-search-terms {
    position: sticky;
    z-index: 2;
    top: 89px;

    @media (max-width: 767px) {
      top: 56px;
    }

    ~ .standalone-document-page {
      .standalone-document-page__actions {
        top: 128px;

        @media (max-width: 767px) {
          top: 88px;
        }
      }
    }
  }

  .standalone-document-page {
    @include search-terms;

    .action-panel {
      position: sticky;
      top: 86px;
      height: 200px;
    }
  }

  .standalone-document-page__actions {
    position: sticky;
    top: 88px;
    z-index: 1;
    padding: 4px 0;
  }

  .current-developments__content-container {
    .standalone-document-page__actions {
      top: 204px;

      @media (max-width: 767px) {
        top: 170px
      }
    }
  }

  .document-actions,
  .document-content {
    margin-top: 24px;
  }

  .document-content {
    margin-bottom: 20px;
  }

  .header_infoBannerVisible ~ .main-container {
    .standalone-document-page__actions {
      top: 167px;
    }

    .current-developments__content-container {
      .standalone-document-page__actions {
        top: 280px;

        @media (max-width: 767px) {
          top: 300px
        }
      }
    }

    .search-terms-control.standalone-page-search-terms {
      top: 165px;
      z-index: 2;

      @media (max-width: 767px) {
        top: 133px;
      }

      ~ .standalone-document-page {
        .standalone-document-page__actions {
          top: 203px;

          @media (max-width: 767px) {
            top: 218px;
          }
        }
      }
    }
  }
}

.standalone-document-page {
  :global {
    .hidden {
      display: none;
    }
  }

  > :global(.wk-row) {
    @media screen and (max-width: 768px) {
      flex-direction: column-reverse;

      > [class*='wk-col-'] {
        flex: 0 0 100%;
        width: 100%;
        max-width: 100%;
      }
    }
  }
}
