@mixin section-with-list {
  margin-bottom: 36px;

  &:last-child {
    margin-bottom: 0;
  }
}

@mixin section-with-list-in-sidebar {
  margin-bottom: 26px;

  &:last-child {
    margin-bottom: 0;
  }
}

.section-card {
  padding: 32px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  &_app-section_sidebar {
    @extend .section-card;
    border: 0;
    padding: 34px 28px;
  }

  &__header {
    text-align: left;
    margin-bottom: 24px;

    &_app-section_sidebar {
      @extend .section-card__header;
      font-size: 18px;
    }
  }

  .link-section {
    display: flex;

    &_contains_list {
      @include section-with-list;

      &_app-section_sidebar {
        @include section-with-list-in-sidebar;
      }
    }

    &__title {
      color: #353535;
      font-size: 14px;
      margin-bottom: 18px;
      display: flex;
      align-items: center;

      &-text:hover {
        text-decoration: underline;
      }

      &_last {
        @extend .link-section__title;
        margin-bottom: 0;

        &_app-section_sidebar {
          @extend .link-section__title_last;
          @extend .link-section__title_app-section_sidebar;
        }
      }

      &_app-section_sidebar {
        @extend .link-section__title;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
      }

      &-wrapper {
        display: flex;
      }

      &:hover {
        color: #005b92;
        text-decoration: none;
      }
    }
  }

  .text-section {
    @include section-with-list;

    &_app-section_sidebar {
      @include section-with-list-in-sidebar;
    }

    &__title {
      color: #353535;
      font-size: 14px;
      margin-bottom: 18px;

      &_app-section_sidebar {
        @extend .text-section__title;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
      }
    }
  }

  &__list {
    list-style: none;
    padding-left: 0;

    &-item {
      margin-top: 0;

      &_last {
        @extend .section-card__list-item;
        margin-bottom: 0;
      }

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &_app-section_sidebar {
        @extend .section-card__list-item;

        &:not(:last-child) {
          margin-bottom: 14px;
        }
      }

      &-title-wrapper {
        font-weight: 400;
      }

      &-title {
        color: #005b92;
        display: flex;
        align-items: center;

        &-text:hover {
          text-decoration: underline;
        }

        &_app-section_sidebar {
          @extend .section-card__list-item-title;
          font-size: 14px;
          line-height: 18px;
        }

        &:hover {
          color: #003d61;
          text-decoration: none;
        }
      }
    }
  }
}

.section-card-footer {
  padding: 24px 32px;
  border-top: 1px solid #a6d1ea;
  outline-offset: -1px;
  background: #fff;

  &_app-section_sidebar {
    @extend .section-card-footer;
    padding: 28px;
  }

  &__link {
    padding: 0;
    text-align: left;
  }
}
