@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';

.account-button {
  &:global(.wk-fishbowl) {
    padding-right: 0;
  }

  .caution-icon {
    color: $wk-primary-red;
    font-size: inherit;
    vertical-align: inherit;
  }

  .account-icon {
    &::before {
      color: $wk-primary-gray-shade1;
    }
  }

  :global {
    .wk-fishbowl-user-name {
      max-width: 200px;
      width: auto;
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: right;
      white-space: nowrap;
    }
  }
  @include breakpoint(max-width $wk-laptop-breakpoint) {
    :global(.wk-fishbowl-description-hide-on-mobile) {
      display: none;
    }
  }

  @include breakpoint(min-width $wk-laptop-breakpoint) {
    :global(.wk-fishbowl-description-hide-on-mobile) {
      display: flex;
    }
  }
}
