.topic-doc-list-section {
  margin-bottom: 30px;

  .topic-doc-list-section__title {
    font-style: italic;
    font-family: Franziska, 'Times New Roman', Times, serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 15px;
  }

  .topic-doc-list-section__link,
  .topic-doc-list-section__link_disabled {
    display: inline-block;
    cursor: pointer;
    font-family: 'Fira Sans', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 15px;
  }

  .topic-doc-list-section__link_disabled {
    color: #757575;
  }
}
