.document-action {
  a.wk-button,
  button {
    width: 1.75rem;
    height: 1.75rem;
    background-color: #bfbfbf; // $wk-primary-gray-tint3
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: inline-block;
    position: relative;
    text-align: center;

    &:hover {
      background-color: #85bc20; // $wk-primary-green
      text-decoration: none;
    }

    span {
      color: #fff; // $wk-primary-white
      font-size: 1.4375rem;
      line-height: 1.82rem;
    }
  }
}
