:global {
  .wide-header-page__main.wnc-last-3m-content,
  .wnc-last-3m-content {
    display: block;

    @media (max-width: 37.4375rem) {
      .wk-column-12-xs {
        flex-basis: 100%;
        max-width: 100%;
        min-width: 100%;

        flex-basis: calc(12 * (calc(calc(1 / 12 * 100%) - 1rem * 11 / 12) + 1rem) - 1rem);
        max-width: calc(12 * (calc(calc(1 / 12 * 100%) - 1rem * 11 / 12) + 1rem) - 1rem);
      }
    }

    .vertical-navigation {
      position: sticky;
      top: 100px;

      @media (max-width: 37.4375rem) {
        margin-bottom: 20px;
      }

      .vertical-navigation__title {
        font-size: 14px;
        line-height: 18px;
        font-weight: 500;
        color: #000;
        text-transform: capitalize;
      }

      .vertical-navigation__list {
        margin-top: 15px;

        .vertical-navigation__list-item {
          margin: 0;

          &.active,
          &:hover {
            > .vertical-navigation__link {
              background-color: #bfbfbf;
              color: white;
              text-decoration: none;
            }
          }

          > .vertical-navigation__link {
            display: block;
            color: #353535;
            background: var(--gray-tint-6-f-6-f-6-f-6, #f6f6f6);
            padding: 12px 16px;

            font-family: Fira Sans;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 18px;
            cursor: pointer;
          }

          &:not(:first-child) {
            margin-top: 1px;
          }
        }
      }
    }

    .author-section {
      margin-top: 40px;

      &:nth-child(2) {
        /* first section after page content title */
        margin-top: 24px;
      }

      > .author-section__title {
        text-align: center;
        font-family: 'Fira Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 26px;
        text-decoration-line: underline;
        color: #000000;

        > .title-text {
          margin-left: 8px;
        }
      }

      > .no-content-message {
        margin-top: 24px;
        text-align: center;
        font-size: 14px;
        line-height: 18px;
      }

      .book-section {
        margin-top: 16px;

        &:nth-child(2) {
          /* first section after author title */
          margin-top: 24px;
        }

        > .book-section__title {
          color: #353535;
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;

          &.disabled {
            color: #a3a3a3;
          }
        }

        .document-links__list {
          margin-top: 4px;

          .document-link__list-item:not(:first-child) {
            margin-top: 8px;
          }
        }

        .document-link {
          color: #007ac3;
          font-weight: 500;
          font-size: 14px;
          line-height: 18px;
          cursor: pointer;

          &.disabled {
            color: #a3a3a3;
          }
        }
      }
    }
  }
}
