.admin-group [data-e2e='cg-accordion-item'] {
  border: 0;

  [aria-hidden='false'] [data-e2e='cg-accordion-item-body'] {
    max-height: 300vh;
  }

  .cg-accordion-item-header + .cg-accordion-item-element {
    margin: 5px 0;
    padding: 0;
  }

  .cg-accordion-item-header .cg-accordion-item-header-label {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    padding-right: 8px;
  }

  .admin-group__header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-right: 8px;
  }

  .wk-field-input {
    font-size: 100%;
  }
}
