.navigation-dynamic-card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  .wk-card-title {
    margin-bottom: 24px;
    text-align: left;
  }

  .card-footer {
    margin-top: 24px;
    font-size: 14px;
    line-height: 18px;
  }

  &:not(:last-child) {
    border-bottom: none;
  }

  .items-list {
    list-style: none;
    padding-left: 0;

    .widget-item {
      display: flex;
      cursor: pointer;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &.bottom .primary-title {
        font-size: 14px;
        font-weight: initial;
      }
    }

    .primary-title {
      display: flex;
      color: #005b92;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
    }

    .item-icon {
      font-size: 24px;
      color: #005b92;
      margin-left: 0;
      margin-right: 8px;
    }
  }
}
