@import '~@wk/fundamentals/color.utils';
@import '~@wk/fundamentals/helpers';

@import 'components/deleted-item-type/deleted-item-type.scss';

.history-list .ac-list {
  margin-top: 16px;
  margin-bottom: 48px;
}

.history-item:first-child {
  border-top: solid wk-primary-color(gray, tint2) 1px;
}

.history-item:last-child {
  border-bottom: solid wk-primary-color(gray, tint2) 1px;
}

.history-item-column_title {
  overflow-wrap: anywhere;
  word-break: break-word;
}

@include breakpoint(max-width $wk-laptop-breakpoint) {
  .history-item-column {
    line-height: 1.9rem;
    display: block;
  }
}
@include breakpoint(min-width $wk-laptop-breakpoint) {
  .history-item {
    display: flex;
  }

  .history-item-column + .history-item-column {
    margin-left: 1rem;
  }

  .history-item-column_date {
    //styles from wk-column-1
    flex-basis: 8.33%;
    max-width: calc(8.33% - 1rem);
    min-width: calc(8.33% - 1rem);
  }

  .history-item-column_type {
    //styles from wk-column-3
    flex-shrink: 1;
    flex-basis: 8.33%;
    max-width: calc(24.99% - 1rem);
    min-width: calc(24.99% - 1rem);
    flex-grow: 3;
  }

  .history-item-column_title {
    //styles from wk-column-8
    flex-shrink: 1;
    flex-basis: 8.33%;
    max-width: calc(66.64% - 1rem);
    min-width: calc(66.64% - 1rem);
    flex-grow: 8;
  }
}
