:global {
  [data-e2e='cg-tooltip'][class*='cg-position'].cg-tooltip-interactive.cg-tooltip-open {
    z-index: 1;
  }
  [data-e2e*='cg-tooltip-body'] .spinner {
    width: 100%;
  }

  .citation-popover-tooltip {
    [data-e2e="cg-tooltip"] {
      position: relative;

      .cg-tooltip-content {
        &:after {
          content: '';
          display: none;
          position: absolute;
          left: 0;
          right: 0;
          height: 12px;
        }
      }

      &[class*='cg-position'] {
        .cg-tooltip-content:after {
          display: inline-block;
        }
      }

      &[class*="cg-position-top"] {
        .cg-tooltip-content:after {
          top: 100%;
        }
      }

      &[class*="cg-position-bottom"] {
        .cg-tooltip-content:after {
          bottom: 100%;
        }
      }
    }
  }
}
