.freemium-content-container {
  max-width: 590px;
  font-size: 14px;

  .freemium-content-header {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .freemium-content-list {
    list-style: inside;
    margin: 20px 10px;
  }

  .freemium-content-default-footer-row {
    display: flex;
    justify-content: center;

    .register-button {
      font-weight: 300;
      padding: 10px 58px;
    }

    .login-button {
      padding-top: 20px;
      padding-bottom: 0;
    }
  }

  .freemium-content-qa-footer {
    display: flex;
    margin-top: 15px;

    .button-separator {
      padding: 0 8px;
      line-height: 40px;
    }

    .qa-button {
      padding: 10px 0;
    }
  }
}
