@import '~@wk/fundamentals';

.client-impact-premium-popup-content {
  .wk-modal-body * + * {
    margin-top: 1.5rem;
  }
  .wk-modal-body {
    max-width: 601px;

    .wk-icon-news {
      margin: 0 8px 5px 0;
    }
  }

  .wk-modal-footer {
    [data-e2e-element-id='cancel'] {
      color: #fff;
      background-color: #007ac3;

      &:hover {
        background-color: #003d61;
        color: #fff;
        cursor: pointer;
        text-decoration: none;
      }
    }
  }
}

.grayed-out-link {
  color: $wk-primary-gray-tint3;

  &:hover {
    text-decoration: none;
    color: $wk-primary-gray-tint3;
  }
}
