@import '~@wk/fundamentals';

.applied-filters-panel-list {
  display: flex;
  flex-wrap: wrap;

  .applied-filters-panel-list__item {
    max-width: 100%;
    margin-top: 0;

    @include breakpoint(max-width $wk-smartpad-breakpoint) {
      max-width: calc(100vw - 32px);
    }

    @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-tablet-breakpoint) {
      max-width: calc(100vw - 48px);
    }

    @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-desktop-breakpoint) {
      max-width: 55vw;
    }

    div {
      margin: 0.5rem 12px 3px 0;
      max-width: 100%;

      .cg-pill {
        max-width: 100%;
      }
    }

    .wk-button {
      margin-top: 0.5rem;
      padding: 0.4rem 1rem;
    }
  }
}

.clear-all-filters-button {
  padding-left: 0;
}
