@import '@wk/components-react16/dist/button-group.css';
@import '@wk/components-react16/dist/button-field.css';
@import '@wk/components-react16/dist/iconsvg.css';
@import '@wk/components-react16/dist/input-field.css';
@import '@wk/components-react16/dist/fonts.css';
@import '@wk/components-react16/dist/forms.css';
@import '@wk/components-react16/dist/modal.css';
@import '@wk/components-react16/dist/notification.css';

.share-document__form {
  padding-top: 10px;
  padding-right: 12px;
  max-width: calc(600px - 4rem);
}

.recipients-input {
  position: relative;
  margin-bottom: 8px;

  .email-suggestions-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1040;
    width: calc(100% - 48px);
    max-width: calc(100% - 48px);
  }
}

.recipients-input__row {
  display: flex;
  align-items: flex-end;

  > [data-e2e='cg-field'] {
    flex: auto;
    margin-bottom: 0;
  }

  > [data-e2e='cg-button-field-icon'] {
    margin: 0 4px;
  }

  ~ .validation-error {
    margin-top: 2px;
  }
}

.validation-error {
  color: #e5202e; // $wk-primary-red
  font-size: 14px;
}

[data-e2e-element-type='list'].recipients-list {
  padding-left: 6px;
  color: #474747; // $wk-primary-gray
  font-size: 14px;
  margin-bottom: 25px;

  &.recipients-list--not-empty {
    margin-bottom: 40px;
  }
}

.recipients-list__item {
  display: flex;
  align-items: center;
  line-height: 1;

  + .recipients-list__item {
    margin-top: 2px;
  }

  [data-e2e='cg-button-field-icon'] {
    margin-left: 4px;
  }

  .recipients-list__item-email {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 2px 0;
  }
}

.share-modal__footer-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 16px;

  .format-info {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #474747; // $wk-primary-gray
  }

  [data-e2e='cg-button-field'] .share-document__send-button {
    padding: 0.625rem 0.95rem;
  }
}
