.special-jump-to-topics-card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  .wk-card-title {
    margin-bottom: 24px;
    text-align: left;
  }

  .card-footer {
    margin-top: 24px;
    font-size: 14px;
    line-height: 18px;
  }
}

.items-list {
  list-style: none;
  padding-left: 0;

  .widget-item {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &.bottom .primary-title {
      font-size: 14px;
      font-weight: initial;
    }
  }

  .primary-title {
    color: #005b92;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
  }
}

.flag-icon-background {
  background-repeat: no-repeat;
}

.flag-icon {
  @extend .flag-icon-background;
  border: 1px solid #a3a3a3;
  position: relative;
  display: inline-block;
  line-height: 1em;
  box-sizing: content-box;
  margin-right: 11px;
  &:before {
    content: '\00a0';
  }
}
