@import '../../../../../../styles/variables';

.advanced-search-filters {
  & &__title {
    display: flex;
    align-items: center;

    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 20px;
    color: $wk-primary-gray-shade2;
  }
  & &__title-hint {
    margin-left: 10px;
  }

  & &__suggestions-text {
    margin-left: 5px;
    color: $wk-primary-green-shade1;
  }

  & &__input {
    margin-bottom: 10px;

    & > [class^='cg-field'] {
      margin-bottom: 0px;
    }
  }

  & &__input-trading {
    padding-right: 5px;
    margin-bottom: 0;
  }

  & &__input-cik {
    padding-left: 5px;
    margin-bottom: 0;
  }
}
