@import '~@wk/fundamentals';

.jumpstart-tooltip-white {
  @include breakpoint(max-width $wk-smartpad-breakpoint) {
    display: none;
  }

  .cg-tooltip.cg-tooltip-open.cg-tooltip-description {
    background: $wk-primary-white;
    color: $wk-primary-gray-shade2;
    max-width: 400px;
    border-color: $wk-primary-gray-tint4;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);

    &.cg-position-bottom,
    &.cg-position-bottom-left,
    &.cg-position-bottom-right {
      &::before {
        border-bottom-color: $wk-primary-gray-tint4;
      }

      &::after {
        border-bottom-color: $wk-primary-white;
      }
    }

    &.cg-position-top,
    &.cg-position-top-left,
    &.cg-position-top-right {
      &::before {
        border-top-color: $wk-primary-gray-tint4;
      }

      &::after {
        border-top-color: $wk-primary-white;
      }
    }

    &.cg-position-left {
      &::before {
        border-left-color: $wk-primary-gray-tint4;
      }

      &::after {
        border-left-color: $wk-primary-white;
      }
    }

    &.cg-position-right {
      &::before {
        border-right-color: $wk-primary-gray-tint4;
      }

      &::after {
        border-right-color: $wk-primary-white;
      }
    }
  }
}
