@use '../../widgets/document-view/document-processors/arm-content-styles/arm-content';

.arm-topic-page {
  :global {
    .wide-header-page__main {
      padding-top: 0;
      padding-bottom: 0;
      display: block;
    }

    .wide-header-page__header {
      position: sticky;
      top: 89px;
      z-index: 2;

      @media (max-width: 767px) {
        top: 56px;
      }

      @media (max-width: 600px) {
        top: 48px;
      }
    }
  }

  :global(.header_infoBannerVisible ~ .main-container) & :global {
    .wide-header-page__header {
      top: calc(89px + 76px);

      @media (max-width: 1199px) {
        top: 164px;
      }

      @media (max-width: 767px) {
        top: 133px;
      }

      @media (max-width: 600px) {
        top: 125px;
      }
    }
  }

  .topic-body {
    width: 100%;
    display: flex;
    justify-content: space-between;

    .topic-content {
      margin: 30px 10px;

      :global {
        .analytical-commentary-section {
          @extend %arm-content-body;

          // lists
          p.hP + ul,
          p.hP + ol {
            margin-top: 24px;
          }
        }
      }
    }

    .document-actions {
      position: sticky;
      height: 40px;
    }

    &.mobile-view {
      flex-direction: column-reverse;

      .document-actions {
        position: static;
        height: 40px;
        margin: 15px 0;
      }
    }
  }
}
