@use '../../../../filters-common-styles';

.matrix-filter {
  @extend %styles-for-checkbox-in-filters;

  .matrix-filter__filter-wrapper {
    max-width: 100%;
    width: 100%;
    display: grid;
    grid-gap: 12px 8px;
  }

  .matrix-filter__filter-wrapper {
    &_with-1-symbol-items {
      grid-template-columns: repeat(auto-fill, 38px);
    }
    &_with-2-symbol-items {
      grid-template-columns: repeat(auto-fill, 48px);
    }
    &_with-3-symbol-items {
      grid-template-columns: repeat(auto-fill, 58px);
    }
    &_with-4-symbol-items {
      grid-template-columns: repeat(auto-fill, 70px);
    }
  }
}
