@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';

.tabbed-view {
  display: block;

  &.tabbed-view--full-width {
    > :global(.wk-page-container.wide-header-page__main) {
      width: 100%;
      padding: 0;
      margin: 0;
      max-width: 100%;

      > :global(.wk-row) {
        margin: 0;

        .tabbed-view__content {
          padding: 0;
        }
      }
    }
  }

  :global {
    .wide-header-page__main {
      padding-top: 0;

      > .wk-row {
        display: block;
        width: 100%;
      }
    }
  }

  > :global(.wide-header-page__header) {
    position: relative;
    padding-top: 24px;
    padding-bottom: 0;

    &:after {
      content: '';
      display: inline-block;
      position: absolute;
      background-color: $wk-primary-gray-tint4;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
    }

    > :global(.wk-page-container) {
      position: relative;
      z-index: 1;
    }
  }

  :global(.header_infoBannerVisible ~ .main-container) & :global {
    .as-360-page-header {
      top: calc(89px + 76px);

      @media (max-width: 767px) {
        top: 133px;
      }
    }
  }

  :global {
    .as-360-page-header {
      position: sticky;
      top: 89px;
      z-index: 3;

      @include breakpoint(max-width $wk-tablet-breakpoint) {
        top: 56px;
      }
    }

    .page-title {
      &.variant-1 {
        font-size: 32px;
      }

      &.variant-2 {
        font-size: 24px;
      }
    }
  }
}

.tabbed-view__tabs {
  border: none;

  :global {
    .wk-page-container:not(.wk-row) {
      padding-left: 97px;
    }
  }
}

.tabbed-view__content {
  padding-left: 106px;
  width: 100%;
}

.tabbed-view__search-terms-navigation {
  position: sticky;
  z-index: 2;
  top: 212px;

  @media (max-width: 767px) {
    top: 194px;
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: calc(100vw - 1px);
    transform: translateX(-50%);
    height: 1px;
    background: $wk-primary-gray-tint4;
  }

  :global {
    .search-terms-control.view360-search-terms-control {
      width: 100%;
      border-bottom-color: transparent;

      .document-search-terms-control {
        margin-left: 0;
      }

      .back-to-search-link_container {
        padding-left: 0;
      }
    }
  }

  ~ .tabbed-view__content {
    :global {
      .summary-tab__document-actions {
        top: 245px;
      }
    }
  }
}

.tabbed-view-header__top {
  display: flex;
  align-items: center;
}

.tabbed-view-header__bottom {
  position: relative;
  margin-top: 16px;
}

.tabbed-view-header__dropdown {
  margin-right: 16px;

  &:not(:global(.tabbed-view-header__dropdown--compact)) {
    min-width: 81px;
  }

  &:global(.tabbed-view-header__dropdown--compact) {
    :global(.dropdown-menu__btn-icon) {
      right: 50%;
      transform: translateX(50%);
    }
  }
}

.tabbed-view-header__switcher {
  margin-left: 16px;
}
