.tooltip-fix-position {
  display: flex;
  align-items: center;
  position: relative;

  &.bottom,
  &.top {
    flex-direction: column;
  }

  &.left,
  &.right {
    flex-direction: row;
  }

  .tooltip {
    display: block;
  }

  .wk-tooltip.wk-tooltip-open,
  .wk-tooltip-interactive.wk-tooltip-open {
    transform: translateY(0);
  }

  .wk-tooltip-body {
    margin: 0;
  }

  .tooltip-small {
    width: 100px;
  }

  .tooltip-medium-small {
    padding: 14px 11px 9px;
    width: 200px;
  }

  .tooltip-medium {
    width: 250px;
  }

  .tooltip-big {
    width: 400px;
  }

  .tooltip-large {
    padding: 14px 11px 9px;
    width: 273px;
  }

  .tooltip-hidden {
    display: none;
  }
}
