.empty-content-message {
  color: #232323;
  font-family: 'Fira Sans', sans-serif;

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 32px;
  }

  &__body {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
  }

  &__description {
    margin-bottom: 8px;
  }

  &__phone-number {
    font-weight: 500;
  }
}
