@import './variables';

@mixin search-terms() {
  .docSearchTerm {
    background-color: #ff6;
    font-weight: 700;
  }

  .current-search-term {
    background-color: $csn-blue;
    color: white;
  }

  .search-terms-off,
  &.search-terms-off {
    //rewrite cf style
    .docSearchTerm {
      background-color: transparent !important;
      font-weight: inherit !important;
    }

    .current-search-term {
      background-color: transparent !important;
      color: inherit !important;
    }
  }
}
