$wk-primary-gray-shade1: #353535;
$wk-primary-gray-tint4: #dadada;

.app-switcher {
  display: flex;
  font-size: 14px;
}

#tooltip-for-current-app,
#tooltip-for-another-app {
  > * {
    background: #fff;
    border: 0.0625rem solid #dadada;
    box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.1);
    color: #232323;
    font-size: 0.875rem;
    line-height: 1.5;
    padding: 1rem;
    width: 16.25rem;
    white-space: normal;
  }
}

.app-switcher {
  #tooltip-for-current-app,
  #tooltip-for-another-app {
    & *:before {
      border-bottom-color: #dadada;
    }
    & *:after {
      border-bottom-color: #fff;
    }
  }
}

.app-switcher-truncated {
  padding: 15px 0;
  font-size: 0.875rem;

  .app-switcher-truncated__title {
    margin-left: 18.5px;
    margin-bottom: 8px;
  }

  .app-switcher-truncated__item {
    display: flex;
    padding: 11px 18px 9px 18px;
    color: $wk-primary-gray-shade1;

    &:hover,
    &:focus {
      color: $wk-primary-gray-shade1;
    }
  }

  .app-switcher-truncated__item-icon {
    margin-right: 14px;
  }

  .app-switcher-truncated__item-text {
    font-size: 15px;
    line-height: 20px;
  }

  .app-switcher-truncated__item_active {
    background-color: $wk-primary-gray-tint4;
  }

  .app-switcher-truncated__item_inactive {
    cursor: pointer;

    &:hover {
      background-color: $wk-primary-gray-tint4;
      text-decoration: none;
    }
  }
}

.app-switcher,
.app-switcher-truncated {
  .icon-container {
    display: flex;
    border-radius: 2px;
    color: #474747;
    cursor: pointer;
    border: none;
    line-height: 1.5;

    &.icon-container_inactive {
      padding: 6px 9px;
      color: #757575;
      background-color: transparent;
      transition: text-decoration 0.3s ease-in-out;

      &:hover,
      &:focus {
        text-decoration: underline;
      }
    }

    &.icon-container_active {
      padding: 6px 9px;
      font-weight: 500;
      color: #353535;
      text-decoration: underline;
      background-color: #f2f8fc;
    }

    .icon-container__icon {
      width: 27px;
      height: 20px;
      margin-right: 5px;
      border-radius: 2px;
    }
  }
}
