:global(.document-content) {
  position: relative;

  :global(.docContent .metadataContainer) {
    display: block;

    :global(.osa-doc-metadata-link) {
      color: transparent;
    }

    :global(.osa-doc-metadata) {
      display: none;
      margin-top: 20px;
    }
  }
}

.transformable-document-content {
  &:global(.hidden) {
    display: none;
  }
}

:global {
  // https://jira.wolterskluwer.io/jira/browse/ARMAC-358
  .document-content {
    .ArchiveSection,
    .ShowSources {
      display: none;
    }

    &.arm-sources-visible {
      .ShowSources {
        display: inline;
      }
    }

    .document-action__view-archive {
      display: none;
    }

    &.document-has-archives {
      .document-action__view-archive {
        display: inline-block;
        margin-left: 8px;
      }
    }
  }
}
