@import '../../../../../styles/helpers';

.wk-logo {
  display: inline-block;
  line-height: 0;

  &:hover {
    cursor: pointer;
  }
}

.wk-logo__images {
  display: inline-block;
  line-height: 0;
}

.wk-logo__image-small {
  display: block;
}

.wk-logo__image-normal {
  display: none;
}

@include breakpoint(min-width $wk-tablet-breakpoint) {
  .wk-logo__image-small {
    display: none;
  }

  .wk-logo__image-normal {
    display: block;
  }
}
