.admin-entry-container.brand-logos {
  .wk-table {
    margin: 20px 0;

    .wk-field-secondary {
      margin: 0;
    }

    .brand-logo-row {
      height: 55px;
    }

    .association-name-col {
      width: 190px;
    }

    .logo-col {
      width: 234px;
    }

    .edit-col {
      width: 60px;
    }

    .delete-col {
      width: 75px;
    }

    .delete-control {
      color: #e5202e;
    }

    .edit-control {
      color: #005b92;
    }

    .delete-control,
    .edit-control {
      font-size: 18px;
    }

    .add-association-control {
      justify-content: flex-start;
      align-items: center;
    }
  }

  .error-message {
    margin-bottom: 20px;
  }
}
