:global {
  .user-subscriptions_container {
    background-color: #757575;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    color: white;
    padding-top: 32px;
    padding-left: 32px;
    padding-bottom: 8px;
    height: 200px;
    width: 100%;
  }

  .user-subscriptions_title {
    flex-shrink: 0;
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 16px;
  }

  .user-subscriptions_list {
    flex-grow: 2;
    overflow-y: auto;
    width: 100%;
    scrollbar-width: thin;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-thumb {
      background: #dadada;
      border-radius: 3px;
    }
  }

  .user-subscriptions_list-item {
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 10px;
  }
}
