.fiscal-year-selection {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.fiscal-year-selection__months {
  $field-width: calc(164 / 352 * 100%);
  flex: 0 0 $field-width;
  width: $field-width;
}

.fiscal-year-selection__days {
  $field-width: calc(178 / 352 * 100%);
  flex: 0 0 $field-width;
  width: $field-width;
}
