$wk-primary-gray-shade1: #353535 !default;
$wk-primary-white: #fff !default;
$wk-primary-red: #e5202e !default;
$wk-primary-red-shade1: #ac1822 !default;

.expired-banner-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px 9px;
  width: 100%;
  background-color: $wk-primary-gray-shade1;
  color: $wk-primary-white;
  font-size: 15px;

  .expired-banner-text-container {
    font-size: 16px;
    line-height: 1.5;

    .expired-banner-header {
      color: $wk-primary-white;
      margin: 2px 0 1px;
    }

    .expired-banner-header,
    .expired-banner-support-info,
    .manage-account-link {
      line-height: 1;
      font-size: 15px;
    }

    .expired-banner-support-info {
      margin: 10px 0 1px;
    }

    .manage-account-link {
      color: #a4cd58;
      margin-top: 7px;
      display: inline-block;

      &:hover {
        color: #85bc20;
      }
    }
  }

  .wk-icon-filled-caution {
    color: $wk-primary-white;
    margin: 0;

    &::before {
      font-size: 2.4rem;
    }
  }

  &.expiring {
    background-color: $wk-primary-red-shade1;
    padding: 10px 12px;
  }
}
