@import '../../../../../../styles/variables';

.checklist-table-header-row {
  padding: 11px 0;

  &__column {
    display: flex;
    align-items: center;
    flex-shrink: 0;

    height: 18px;
  }

  &__title {
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    color: $wk-primary-gray-shade1;

    padding: 0;
    border: 0;
    background: transparent;
    cursor: pointer;
  }

  &__icon {
    margin-left: 7px;
  }
}
