////
/// @group grid
/// @author [*/dxg](https://dxg.wolterskluwer.io/)
/// @since 0.0.1
/// @access public
////

/// Total ammount of columns
///
/// @type Number
$wk-global-mobile-columns: 1 !default;

/// Total ammount of columns
///
/// @type Number
$wk-global-columns: 12 !default;

/// Grid column quontity
///
/// @type Number
$wk-grid-column-qw: 12 !default;

/// Grid column padding
///
/// @type Unit
$wk-grid-gap-padding: $wk-grid-padding !default;

/// Grid column gutter
///
/// @type Unit
$wk-grid-gap-gutter: $wk-grid-gap-padding * 2 !default;

/// Grid slicer breakpoints
///
/// @type Number
$wk-grid-from-breakpoints: (
    mobile: 1,
    phablet: 2,
    smartpad: 3,
    tablet: 4,
    laptop: 5,
    desktop: 6,
    hd: 7,
    hd-plus: 8
) !default;

/// Grid column width for column base
///
/// @type Unit
$wk-grid-column-base-flex-basis: 8.33% !default;

/// Grid column width for column base
///
/// @type Unit
$wk-grid-column-base-flex-basis-mobile: 50% !default;

/// Grid column width map for main breakpoints
///
/// @type Unit
$wk-grid-col-width: (
    mobile: $wk-grid-column-base-flex-basis-mobile,
    phablet: $wk-grid-column-base-flex-basis-mobile,
    smartpad: $wk-grid-column-base-flex-basis-mobile,
    tablet: $wk-grid-column-base-flex-basis-mobile,
    laptop: $wk-grid-column-base-flex-basis,
    desktop: $wk-grid-column-base-flex-basis,
    hd: $wk-grid-column-base-flex-basis,
    hd-plus: $wk-grid-column-base-flex-basis
) !default;

/// Page Container

/// Page Container paddings
///
/// @type Unit
$wk-page-container-paddings: (
    mobile: wk-rem(16px),
    hd: wk-rem(48px),
    hd-plus: wk-rem(56px)
) !default;

/// Page Container paddings if it is grid container
///
/// @type Unit
$wk-page-container-paddings-in-grid: (
    mobile: wk-rem(8px),
    hd: wk-rem(40px),
    hd-plus: wk-rem(48px)
) !default;

/// Page Container desktop max-width
///
/// @type Unit
$wk-page-container-max-width-desktop: $wk-global-max-width !default;

/// Page Container max-width for main brekpoints
///
/// @type Unit
$wk-page-container-max-width: (
    mobile: wk-rem(320px),
    phablet: wk-rem(375px),
    smartpad: wk-rem(600px),
    tablet: wk-rem(768px),
    laptop: wk-rem(1024px),
    desktop: wk-rem(1200px),
    hd: wk-rem(1440px)
) !default;

$wk-grid-class-enabled: true !default;
$wk-grid-column-class: 'wk-col' !default;
$wk-grid-column-group-class: 'wk-row' !default;

$wk-page-container-class: 'wk-page-container' !default;
$wk-page-container-in-grid-class: 'wk-page-container-in-grid' !default;
