@import '~@wk/fundamentals';

.option {
  margin: 0;
  padding: 7px 16px 7px;
  cursor: pointer;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $wk-primary-gray-shade1;

  &:hover {
    background: $wk-primary-blue;
    color: $wk-primary-white;
  }
}
