@import '@wk/components-react16/dist/iconsvg.css';
@import '@wk/components-react16/dist/fonts.css';
@import '@wk/components-react16/dist/notification.css';
@import '@wk/components-react16/dist/modal.css';

@import '~@wk/fundamentals';

.save-to-favorites-container {
  position: relative;
  padding-right: 5px;

  .save-to-favorites-button {
    padding: 0;
    width: 28px;
    height: 28px;
    border: none;
    border-radius: 1.25rem;
    background-color: #c4c4c4;

    &:hover:not(:disabled) {
      background-color: $wk-primary-green;
    }
  }
}

.save-to-favorites-document-action-tooltip {
  .cg-tooltip.cg-tooltip-open {
    z-index: 1 !important;
  }
}
