.topic-tile {
  display: inline-block;
  width: 237px;
  height: 135px;
  background-color: #f6f6f6;
  border: 1px solid #dadada;
  font-family: 'Fira Sans', sans-serif;
  margin: 7px 15px 7px 0;
  cursor: pointer;

  .topic-tile__link {
    padding: 15px;
    display: block;
    width: 100%;
    height: 100%;

    &:hover {
      text-decoration: none;
    }
  }

  .topic-tile__green-label {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: #86ba31;
  }

  .topic-tile__title {
    color: #005688;
    font-size: 16px;
    line-height: 20px;

    &:hover {
      color: #86ba31;
    }
  }

  .topic-tile__link_disabled {
    opacity: 0.5;
    padding: 15px;
    width: 100%;
    height: 100%;

    .topic-tile__title {
      &:hover {
        color: #005688;
      }
    }
  }
}
