@import '../../../../../../styles/variables';

.checklist-status {
  &__progress-bar {
    position: relative;
    display: flex;

    width: 280px;
    height: 4px;
    border-radius: 4px;
    overflow: hidden;
    background-color: $wk-primary-gray-tint4;

    margin-bottom: 8px;

    &__completed-part {
      position: absolute;
      height: 4px;
      left: 0;
      border-radius: 4px;
      background-color: $wk-primary-green;
    }
    &__review-part {
      border-radius: 4px;
      background-color: $wk-primary-blue;
    }
  }

  &__note {
    display: flex;
    justify-content: space-between;

    color: $wk-primary-gray-tint1;
    font-size: 12px;
    line-height: 16px;

    &__review {
      display: flex;
      align-items: center;
    }

    &__icon {
      margin-right: 4px;
      line-height: 14px;
      color: $wk-primary-blue-shade1;
    }
  }
}
