// Typography Placeholders

@mixin wk-heading {
    color: $wk-heading-color;
}

%wk-heading {
    @include wk-heading;
}

@mixin wk-h1 {
    font-size: $wk-h1-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

%wk-h1 {
    @include wk-h1;
}

@mixin wk-h2 {
    font-size: $wk-h2-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

%wk-h2 {
    @include wk-h2;
}

@mixin wk-h3 {
    font-size: $wk-h3-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.3;
}

%wk-h3 {
    @include wk-h3;
}

@mixin wk-h4 {
    font-size: $wk-h4-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.333333333333333;
}

%wk-h4 {
    @include wk-h4;
}

@mixin wk-h5 {
    font-size: $wk-h5-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

%wk-h5 {
    @include wk-h5;
}

@mixin wk-h6 {
    font-size: $wk-h6-font-size;
    font-weight: map-get($wk-font-weight, regular);
    line-height: 1.25;
}

%wk-h6 {
    @include wk-h6;
}

@mixin wk-eyebrow {
    font-size: $wk-size-1;
    font-weight: map-get($wk-font-weight, medium);
    letter-spacing: wk-rem(1px);
    line-height: 1.333333333333333;
    text-transform: uppercase;
}

%wk-eyebrow {
    @include wk-eyebrow;
}

@mixin wk-display-1 {
    font-size: $wk-display-1-font-size;
    font-weight: map-get($wk-font-weight, light);
    line-height: 1.263157894736842;
}

%wk-display-1 {
    @include wk-display-1;
}

@mixin wk-display-2 {
    font-size: $wk-display-2-font-size;
    font-weight: map-get($wk-font-weight, regular);
    line-height: 1.25;
}

%wk-display-2 {
    @include wk-display-2;
}

@mixin wk-display-3 {
    font-size: $wk-display-3-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.277777777777778;
}

%wk-display-3 {
    @include wk-display-3;
}

@mixin wk-display-4 {
    font-size: $wk-display-4-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

%wk-display-4 {
    @include wk-display-4;
}
