$wk-primary-blue-tint5: #e6f2f9;
$wk-primary-blue-shade2: #007ac3;
$wk-primary-black: #000;

.select-all-panel {
  position: relative;
  padding: 0 40px 0 25px;
  width: 100%;
  line-height: 20px;

  &:hover {
    .select-all-panel-text {
      background-color: $wk-primary-blue-tint5;
    }
  }

  .select-all-panel-lable {
    position: absolute;
    height: 24px;
    top: 2px;
    left: 25px;
    width: 100%;
    cursor: pointer;
  }

  .select-all-panel-text {
    margin-left: 20px;
    padding: 4px 0 4px 4px;
    display: inline-block;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-weight: 500;
    font-size: 14px;
    color: $wk-primary-black;
  }
}
