@use '../sections-card/sections-card';

.sections-and-groups-card {
  width: 100%;
  border: 1px solid #a6d1ea;
  border-top: 4px solid #007ac3;
  background: linear-gradient(#e6f2f9 0%, #fff 100%);

  &__header {
    text-align: left;
    margin-bottom: 24px;
    padding: 32px 32px 0 32px;
  }

  &__group {
    padding: 32px;

    &:not(:last-child) {
      border-bottom: 1px solid #a6d1ea;
    }

    &_first {
      padding-top: 0;
    }
  }

  .link-section {
    display: flex;

    &_contains_list {
      @include sections-card.section-with-list;
    }

    &__title {
      color: #353535;
      margin-bottom: 18px;

      &_last {
        @extend .link-section__title;
        margin-bottom: 0;
      }

      &-wrapper {
        display: flex;
      }

      &:hover {
        color: #005b92;
        text-decoration: underline;
      }
    }
  }

  .text-section {
    @include sections-card.section-with-list;

    &__title {
      color: #353535;
      margin-bottom: 18px;
    }
  }

  &__list {
    list-style: none;
    padding-left: 0;

    &-item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &-title {
        color: #005b92;

        &:hover {
          color: #003d61;
        }
      }
    }
  }
}

.sections-and-groups-card-footer {
  padding: 24px 32px;
  border: 1px solid #a6d1ea;
  border-top: 0;
  outline-offset: -1px;
  background: #fff;

  &__link {
    padding: 0;
    text-align: left;
  }
}
