@import '../../../../styles/variables';

.groups {
  .group {
    align-items: baseline;
    scroll-margin: 240px;

    @media (max-width: 1380px) {
      scroll-margin: 285px;
    }
    @media (max-width: 1248px) {
      scroll-margin: 295px;
    }
    @media (max-width: 1199px) {
      scroll-margin: 345px;
    }
    @media (max-width: 767px) {
      scroll-margin: 300px;
    }
    @media (max-width: 630px) {
      scroll-margin: 56px;
    }

    .group-index {
      padding: 0 20px 0 45px;
      font-family: 'Fira Sans', sans-serif;
      font-size: 20px;
      font-weight: 500;
      line-height: 26px;
      text-align: left;
      color: $wk-primary-gray;
      text-transform: uppercase;
    }

    .link-item {
      font-size: 18px;
      font-weight: 400;
      line-height: 30px;
    }
  }

  .groups_with_tabs {
    align-items: baseline;
    scroll-margin: 288px;

    @media (max-width: 1380px) {
      scroll-margin: 295px;
    }
    @media (max-width: 1248px) {
      scroll-margin: 324px;
    }
    @media (max-width: 1199px) {
      scroll-margin: 386px;
    }
    @media (max-width: 767px) {
      scroll-margin: 326px;
    }
    @media (max-width: 630px) {
      scroll-margin: 56px;
    }
  }
  .disabled {
    color: $wk-primary-gray-tint4;
    display: inline-block;
    cursor: pointer;
  }
}

.list {
  padding: 16px 10px 14px;
  border-bottom: 1px solid #dadada;

  .top-content-list {
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    flex-wrap: wrap;

    .list-item {
      font-size: 28px;
      font-weight: 500;
      text-transform: uppercase;
      padding: 0 12px;

      &:hover {
        text-decoration: underline;
      }

      &.disable {
        color: $wk-primary-gray-tint4;
        cursor: default;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}
