@import '../../../../../styles/variables';

.related-content {
  margin-top: 32px;
  padding: 0 5px;

  [data-e2e='cg-button-field'] {
    button {
      align-items: center;
      font-size: 16px;
      line-height: 20px;
      padding: 10px 15px;
    }
  }
}

.related-content-modal {
  [data-e2e='cg-modal-footer'] {
    padding: 2.25rem 1rem;
    margin-top: 32px;
  }
}

.modal-header__title {
  font-weight: 500;
  letter-spacing: -0.02em;
  margin-bottom: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.modal-header__tabs {
  margin: 0 -32px;
  width: calc(100% + 32px * 2);
}

.modal-tabs {
  :global {
    .wk-page-container,
    .wk-page-container:not(.wk-row) {
      padding-left: 15px;
      padding-right: 15px;
    }

    .tab-button {
      text-transform: uppercase;
    }
  }
}

.modal-content {
  :global {
    .ac-list__wrapper {
      + .ac-list__wrapper {
        border-top: none;
        padding-top: 8px;
      }
    }
    .ac-list-item {
      letter-spacing: -0.02em;

      + .ac-list-item {
        margin-top: 16px;
      }

      a {
        text-decoration: underline;
        color: $wk-primary-blue;
        font-weight: 500;
      }
    }

    .ac-list__caption {
      margin-bottom: 16px;
    }
  }
}
