@import '../../../../../styles/variables';

.action-to-card {
  display: flex;
  flex-direction: column;
  gap: 16px;

  width: 448px;
  transition: box-shadow 0.3s ease-in-out;
  padding: 24px;
  border: 1px solid $wk-primary-gray-tint5;
  background: $wk-primary-white;

  &:hover {
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
  }

  @media (max-width: 500px) {
    max-width: 448px;
    width: auto;
  }

  & &__title {
    color: $wk-primary-blue;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 20px;
    font-weight: 500;
    line-height: 26px;
  }

  & &__text {
    color: $wk-primary-black;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 16px;
    line-height: 26px;
  }
}
