.delayed-applying-search-filters-container {
  padding: 24px 0 0 24px;
  display: flex;
  flex-direction: column;
  height: 100%;

  &__content {
    flex-grow: 1;
    overflow-y: auto;
    margin-left: -4px;
    padding-left: 4px;
  }

  &__links-bar {
    display: flex;
    justify-content: space-between;
    padding: 15px 24px 15px 0;

    // TODO: overriding Jumpstart
    &__button.wk-button {
      padding-left: 0;
    }
  }
}
