@import '~@wk/fundamentals/helpers';
@import '~@wk/fundamentals/motion.utils';

.ac-list-item.ac-list-item_with-active-state {
  @extend .wk-link-muted;
  padding: 7px 4px 7px 16px;
  transition: background-color map-get($wk-speed, normal) map-get($wk-easings, standard),
    color map-get($wk-speed, normal) map-get($wk-easings, standard);

  &:hover {
    background-color: wk-primary-color(blue, tint6);
    cursor: pointer;
  }
}

.ac-list-item.ac-list-item_with-active-state.ac-list-item_is-active {
  @extend .wk-is-active;
  background-color: wk-primary-color(blue, tint6);
}
