@import '../../../../../styles/variables';

.exceeded-message-container {
  padding: 36px 0;

  :global {
    .title {
      font-size: 24px;
      font-weight: 500;
      line-height: 30px;
      text-align: left;
      color: $wk-primary-blue-shade1;
      padding-bottom: 29px;
    }

    .message {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      text-align: left;
      color: #232323ff; // $wk-primary-gray-tint7;
      padding-bottom: 36px;
    }

    .concurrents-table {
      margin-bottom: 29px;
      border-collapse: collapse;

      td,
      th {
        padding: 11px 16px;
        color: #353535;
        border: 1px solid $wk-primary-gray-tint4;
        min-width: 192px;
      }

      .table-titles {
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        text-align: left;
      }

      .table-data {
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        text-align: left;
      }
    }

    .go-to-home-button {
      width: 100%;
      text-align: center;

      button {
        span {
          padding-right: 9px;
          margin-bottom: 1px;
        }
      }
    }
  }
}
