@import '~@wk/fundamentals/typography';
@import '~@wk/fundamentals/reset';
@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';
@import '../pages/SearchResultsContent';

.question-n-answer-box {
  border: 1px solid $wk-primary-gray-tint5;
  background: $wk-primary-gray-tint6;
  padding: $inner-space;
  margin-bottom: $search-content-vertical-space-between-blocks;

  @include breakpoint(max-width $wk-desktop-breakpoint) {
    $inner-space: 20px;
    padding: $inner-space;
  }

  .question-n-answer-box__title {
    color: $wk-primary-green-shade2;
    font-size: $wk-size-2;
    line-height: 18px;
  }

  :global {
    .qa-text {
      margin-top: 6px;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;

      @include breakpoint(max-width $wk-smartpad-breakpoint) {
        margin-top: 8px;
      }
    }

    ol ul,
    ul ol {
      margin-top: 0;
    }

    li + li {
      margin-top: 0;
    }
    .circles-list {
      list-style-type: disc;
    }

    .qa-text {
      ul,
      ol {
        margin-left: 24px;
        padding-left: 0;
      }
    }

    .qa-text p {
      margin-top: 6px;
    }

    .dashes-list > li {
      position: relative;
      &:before {
        position: absolute;
        left: -15px;
        content: '-';
      }
    }

    .numbers-list,
    .letters-list {
      counter-reset: list;
    }

    .numbers-list > li {
      list-style-type: none;
      &:before {
        position: relative;
        left: -25px;
        counter-increment: list;
        content: counter(list) ') ';
        min-width: 20px;
        margin-right: -20px;
        display: inline-block;
        text-align: right;
      }
    }

    .letters-list > li {
      list-style-type: none;
      position: relative;
      padding-left: 20px;
      &:before {
        position: absolute;
        left: 0;
        counter-increment: list;
        content: counter(list, lower-alpha) ') ';
      }
    }
  }
}
