@import '../../../../../../styles/variables';

.dropdown-item-with-tooltip {
  height: 32px;
  margin: 0;
  display: block;
  text-align: left;
  padding: 7px 16px;
  font-size: 14px;
  line-height: 1.29;
  white-space: nowrap;
  width: 100%;
  color: $wk-primary-gray-shade1;
  text-decoration: none;
  cursor: pointer;

  &.dropdown-item-with-tooltip_disabled {
    cursor: default;
    opacity: 0.5;

    &:hover,
    &:active {
      color: inherit;
      text-decoration: none;
      background-color: transparent;
    }
  }

  &:hover {
    color: inherit;
    text-decoration: none;
    background-color: $wk-primary-blue-tint5;
  }

  &:active {
    color: $wk-primary-blue-shade1;
    text-decoration: none;
    background-color: $wk-primary-blue-tint5;
  }
}
