.favorite-jurisdictions {
  padding: 32px 26px 32px 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  .wk-card-title {
    margin-bottom: 24px;
    text-align: left;
  }

  .state-code {
    margin-top: -2px;
    margin-right: 8px;
    min-width: 33px;
    text-align: center;
    padding-right: 0;
    padding-left: 1px;
  }

  .favorite-icon {
    margin-top: -2px;
    margin-left: -2px;
    margin-right: 6px;
    text-decoration: none;
  }

  .card-footer {
    margin-top: 24px;
    font-size: 14px;
    line-height: 18px;
  }

  .wk-icon-arrow-right {
    margin-left: 7px;
  }

  .no-favorites {
    font-size: 14px;
    line-height: 18px;
    color: #757575;

    .all-link {
      color: #005b92;
      font-weight: 500;

      &:hover {
        color: #003d61;
      }
    }
  }

  .items-list {
    list-style: none;
    padding-left: 0;

    .widget-item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &.bottom .primary-title {
        font-size: 14px;
        font-weight: initial;
      }
    }

    .primary-title {
      color: #005b92;
    }
  }
}
