.navigation {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 41px;
  text-align: center;
  background-color: #fff;
  border-bottom: 1px solid #0768a9;
  box-shadow: 0 0 5px 0 grey;
  user-select: none;
}

.navigation__logo {
  position: absolute;
  left: 12.5%;
  width: 220px;
  height: 100%;
  display: flex;
  align-items: center;
}

.navigation__logo-wk {
  margin-right: 12px;
}

.navigation-controls {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation-controls__results-count {
  margin: 0 20px;
  font-size: 13px;
  font-weight: 700;
  cursor: pointer;
  user-select: none;
  color: #000;

  &:hover {
    color: #64a137;
  }
}

.navigation-controls__prev {
}

.navigation-controls__next {
}
