@import '../../../styles/variables';

.search-page {
  position: relative;

  &__search-panel-wrapper {
    position: sticky;
    top: 0;
    z-index: 12;

    & > :global(.wk-page-container) > :global(.wk-row) {
      align-items: baseline;
    }
  }

  &__content-wrapper {
    position: relative;
    min-height: 200px;
  }

  &__spinner-background {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__spinner {
    position: sticky;
    top: 50%;
    margin: 100px;
  }

  &__save-to-favorites-container:global() {
    padding: 0;
    margin-left: 22px;

    @media (max-width: $wk-desktop-breakpoint) {
      margin-left: 0;
    }

    @media (max-width: $wk-tablet-breakpoint) {
      margin: 15px 0 15px 22px;
    }

    @media (max-width: $wk-smartpad-breakpoint) {
      margin: 7px 0 15px 2px;
    }
  }

  :global(.search-error-message) {
    line-height: 26px;
    font-size: 20px;
    font-weight: 500;
    color: $wk-primary-gray-shade1;
    display: block;
    overflow-wrap: anywhere;
    word-break: break-word;
  }

  :global(.no-results-error-message) {
    margin-bottom: 32px;
  }

  // TODO: temporary solution for mobile view
  @media (max-width: $wk-tablet-breakpoint) {
    &__search-panel {
      flex-basis: 100%;
      max-width: 100%;
      min-width: 100%;
    }
  }

  @media (min-width: $wk-tablet-breakpoint) {
    &__search-panel:global([class^='wk-col-10-desktop']) {
      max-width: 83%;
      min-width: 83%;
    }
  }
}
