$wk-primary-gray-shade1: #353535;
$wk-primary-black: #000;

.browse-tree-item-label-wrapper {
  width: 100%;
  overflow: hidden;

  &.active {
    font-weight: 500;
  }

  .browse-tree-item-label {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: $wk-primary-black;
    font-size: 14px;
  }
}

.browse-tree-item-icon-wrapper {
  color: $wk-primary-gray-shade1;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    color: $wk-primary-black;
  }
}
