@import '../../../styles/variables';

.header {
  background: $wk-primary-gray-tint5;
  padding: 15px 0;
  position: sticky;
  top: 89px;
  z-index: 7;

  @media (max-width: 767px) {
    top: 56px;
  }
  @media (max-width: 630px) {
    position: relative;
    z-index: 0;
    top: 0;
  }

  :global(.header_infoBannerVisible ~ .main-container) & {
    top: 165px;

    @media (max-width: 767px) {
      top: 133px;
    }

    @media (max-width: 630px) {
      position: sticky;
      top: 164px;
    }
  }

  .header-wrapper {
    justify-content: space-between;
    align-items: center;
    padding: 0 3rem;

    @media (max-width: 1199px) {
      flex-wrap: wrap;
    }

    @media (max-width: 767px) {
      max-width: 90%;
      padding: 0;

      .header-title {
        font-size: 24px;
      }
    }
  }
}
