.title {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

.modal-body {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.body-text {
  padding-bottom: 15px;

  &:last-child {
    padding-bottom: 20px;
  }
}

.contact-card {
  display: inline-block;
  padding: 13px 12px;
  margin: 15px 0 30px;
  background-color: #f6f6f6;
  border: 1px solid #dadada;
  font-family: 'Fira Sans';
  font-style: normal;

  .contact-card__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
  }

  .contact-card__phone {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    padding-top: 10px;
  }

  .contact-card__support {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    padding-top: 10px;
  }
}
