:global(.widgets-panel .wk-card-group) {
  display: flex;
  flex-wrap: wrap;
}

:global(.main-background) {
  position: relative;
  padding-top: 30px;
  padding-bottom: 32px;
  background: linear-gradient(to right, rgba(246, 246, 246, 1) 28%, rgba(246, 246, 246, 0.16) 73%, rgba(0, 0, 0, 0) 73%),
    linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 9%,
      rgba(98, 98, 98, 0) 11%,
      rgba(246, 246, 246, 0.14) 11%,
      rgba(246, 246, 246, 1) 32%
    ),
    url('./../../../../../../app/img/code-connect-dot-bkgd-dark.png'), #f6f6f6;

  // experimental size
  min-height: 58vh;

  .content-loading {
    width: 100%;
  }
}

:global(.main.not-ready) {
  border: 2px solid red;
}

:global(.home-search-widget-container) {
  margin-bottom: 24px;
}

:global(.home-tab-content-container) {
  display: flex;
}
