:global {
  .user-administration_container {
    background-color: #dadada;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 29px 36px 32px 32px;

    .user-administration_title {
      color: #353535;
      font-size: 20px;
      line-height: 26px;
      font-weight: 500;
      margin-bottom: 16px;
    }

    .user-administration_description {
      color: #353535;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 20px;
    }

    .user-administration_button-container {
      .user-administration_button {
        background-color: #85bc20;
        border-color: #85bc20;
        color: #fff;
        font-size: 14px;
      }
    }
  }

  .user-administration_popup_iframe {
    width: 100%;
    height: 100%;
  }
}
