/// Default max pixel width for mobile breakpoint
///
/// @type Unit
$wk-mobile-breakpoint: wk-rem(319px) !default;

/// Default max pixel width for phtablet breakpoint
/// @deprecated Use $wk-phablet-breakpoint instead
///
/// @type Unit
$wk-phtablet-breakpoint: wk-rem(374px) !default;

/// Default max pixel width for phablet breakpoint
///
/// @type Unit
$wk-phablet-breakpoint: wk-rem(374px) !default;

/// Default max pixel width for smartpad breakpoint
///
/// @type Unit
$wk-smartpad-breakpoint: wk-rem(599px) !default;

/// Default max pixel width for tablet breakpoint
///
/// @type Unit
$wk-tablet-breakpoint: wk-rem(767px) !default;

/// Default max pixel width for laptop breakpoint
///
/// @type Unit
$wk-laptop-breakpoint: wk-rem(1023px) !default;

/// Default max pixel width for desktop breakpoint
///
/// @type Unit
$wk-desktop-breakpoint: wk-rem(1199px) !default;

/// Default max pixel width for hd breakpoint
///
/// @type Unit
$wk-hd-breakpoint: wk-rem(1439px) !default;

/// Default max pixel width for hd-plus breakpoint
///
/// @type Unit
$wk-hd-plus-breakpoint: wk-rem(1599px) !default;

/// Breakpoints for breakpoint slicer
/// must be set before @import 'breakpoint-slicer'
/// count roughly by 100’s
/// rationale for this definition is
/// 1. better readability for developers = slice 5 is 500px
/// 2. lots of “in between” insertion points for formatting requirements based on content

$slicer-breakpoints: 0 $wk-mobile-breakpoint $wk-phablet-breakpoint
    $wk-smartpad-breakpoint $wk-tablet-breakpoint $wk-laptop-breakpoint
    $wk-desktop-breakpoint $wk-hd-breakpoint $wk-hd-plus-breakpoint;
//                     └────┬────┘    └─────────┬────────────┘ └─────────┬───────────────┘ └─────────┬────────────┘ └─────────┬─────────┘ └────────────┬────────────┘ └──────┬──────┘└────────────┬─────────────┘
// Slice numbers:           1                   2                        3                           4                        5                        6                     7                    8
$slicer-breakpoint-names: 'mobile' 'phablet' 'smartpad' 'tablet' 'laptop'
    'desktop' 'hd' 'hd-plus';

$amount-of-slices: length($slicer-breakpoint-names);

$slicer-anti-overlap-corrections: 1px, 0.0625rem;

/// Media query off/on flag.
/// True by default
/// @type Boolean
$wk-mq: true !default;

/// Default max pixel width of super grid
///
/// @type Unit
$wk-global-max-width: wk-rem(1440px) !default;

/// The upper limit for a mobile, then it becomes tablet
///
/// @type Unit
$wk-mobile-max-breakpoint: wk-rem(600px) !default;

/// The upper limit for a tablet, then it becomes desktop
///
/// @type Unit
$wk-tablet-max-breakpoint: wk-rem(1040px) !default;

/// The layout stops growing after this maximum pixel limit
///
/// @type Unit
$wk-desktop-max-width: $wk-global-max-width !default;

/// Strict mobile is between 0 and mobile max
///
/// @type List
$wk-target-mobile: 0 $wk-mobile-max-breakpoint !default;

/// Tablet is between mobile max and tablet max
///
/// @type List
$wk-target-tablet: $wk-mobile-max-breakpoint $wk-tablet-max-breakpoint !default;
//$wk-target-tablet: $wk-mobile-max-breakpoint + 0.0001rem $wk-tablet-max-breakpoint - 0.0001rem !default; //donâ€™t think this is required but might be useful later

/// For tablet and above just name the mobile max
///
/// @type Unit
$wk-target-tablet-desktop: $wk-mobile-max-breakpoint !default;

/// For desktop just name the tablet max
///
/// @type Unit
$wk-target-desktop: $wk-tablet-max-breakpoint !default;

/// Default mobile content gutter
///
/// @type Unit
$wk-content-gutter-mobile: 0.5rem;

/// Default tablet content gutter
///
/// @type Unit
$wk-content-gutter-tablet: 0.5rem;

/// Default desktop content gutter
///
/// @type Unit
$wk-content-gutter-desktop: 0;

/// Mobile page content horizontal padding - used with the wk-page-content mixin
///
/// @type Unit
$wk-grid-padding: wk-rem(8px) !default;

/// Page content horizontal padding - used with the wk-page-content mixin
///
/// @type Unit
$wk-grid-padding-desktop: wk-rem(48px) !default;

/// Default static gutter width for super grid
///
/// @type Unit
$wk-global-gutters: wk-rem(32px) !default;

/// Elements z-index map
///
/// @type Map
///
/// @prop {Number} modal [1050] - Modal component z-index value
/// @prop {Number} popover [1060] - Popover component z-index value
/// @prop {Number} header [1000] - Header component z-index value
/// @prop {Number} navbar [1000] - Navbar component z-index value
///
$wk-z-index: (
    subzero: -1,
    base: 0,
    document-bubble-after: 1,
    navbar-product-name: 2,
    document: 200,
    title-bar: 400,
    tab: 999,
    multinav: 1000,
    fixed-action: 1030,
    focus: 1044,
    focus-select: 1046,
    smoke-break: 1048,
    modal: 1050,
    popover: 1060,
    header: 1000,
    header-open: 1040,
    navbar: 1000,
    navbar-open: 1040,
    dropdown: 1040,
    breakpoint-tag: 1200,
    list: 2000,
    fixed-top: 9999,
    multiselect: 10001,
    document-bubble: 9999999
) !default;

// Base Font Size
///
/// @type Unit
$wk-global-font-size: wk-rem(16px) !default;

// Monospace Base Font Size
///
/// @type Unit
$wk-monospace-font-size: 87.5% !default;

// Global Color
///
/// @type Color
$wk-global-color: $wk-primary-gray-shade2 !default;

/// Global line height
///
/// @type Number
///
$wk-global-line-height: 1.5 !default;

//TODO: find a better place:
/// Default icon right margin to allow space between an icon and text
///
/// @type Unit
$wk-icon-text-margin: 0.5rem !default;

/// Font size step 11, 76px (4.75rem)
///
/// @type Unit
$wk-size-11: wk-rem(76px) !default; // 4.75rem

/// Font size step 10, 48px (3rem)
///
/// @type Unit
$wk-size-10: wk-rem(48px) !default; // 3rem

/// Font size step 9, 36px (2.25rem)
///
/// @type Unit
$wk-size-9: wk-rem(36px) !default; // 2.25rem

/// Font size step 8, 32px (2rem)
///
/// @type Unit
$wk-size-8: wk-rem(32px) !default; // 2rem

/// Font size step 7, 28px (1.75rem)
///
/// @type Unit
$wk-size-7: wk-rem(28px) !default; // 1.75rem

/// Font size step 6, 24px (1.5rem)
///
/// @type Unit
$wk-size-6: wk-rem(24px) !default; // 1.5rem

/// Font size step 5, 20px (1.25rem)
///
/// @type Unit
$wk-size-5: wk-rem(20px) !default; // 1.25rem

/// Font size step 4, 18px (1.125rem)
///
/// @type Unit
$wk-size-4: wk-rem(18px) !default; // 1.125rem

/// Font size step 3, 16px (1rem)
///
/// @type Unit
$wk-size-3: $wk-global-font-size !default; // 1rem

/// Font size step 2, 14px (0.875rem)
///
/// @type Unit
$wk-size-2: wk-rem(14px) !default; // 0.875rem

/// Font size step 1, 12px (0.75rem)
