$wk-size-1: wk-rem(12px) !default; // 0.75rem

/// Font Scale
///
/// @type Map
$wk-font-scale: (
    1: $wk-size-1,
    2: $wk-size-2,
    3: $wk-size-3,
    4: $wk-size-4,
    5: $wk-size-5,
    6: $wk-size-6,
    7: $wk-size-7,
    8: $wk-size-8,
    9: $wk-size-9,
    10: $wk-size-10,
    11: $wk-size-11
) !default;

// TODO: remove
/// Contexts map
///
