//TODO: find a proper place
/// Default transition property
///
/// @type List
$wk-transition-property: all !default;

/// Default transition timing function
///
/// @type List
$wk-transition-function: ease-out !default;

/// Default transition duration
/// 250ms is minimum most people will notice
/// @type List
$wk-transition-duration: 0.2s !default;

/// Box shadow raised
///
/// @type List
$wk-box-shadow-raised: wk-rem(0 4px 8px 0) rgba(black, 0.1);

/// Box shadow overlay
///
/// @type List
$wk-box-shadow-overlay: wk-rem(0 6px 12px 0) rgba(black, 0.1);

/// Box shadow popout
///
/// @type List
$wk-box-shadow-popout: wk-rem(0 12px 24px 0) rgba(black, 0.1);

/// Box shadow map
///
/// @type Map
$wk-box-shadow: (
    raised: $wk-box-shadow-raised,
    overlay: $wk-box-shadow-overlay,
    popout: $wk-box-shadow-popout
) !default;

