$wk-disabled-opacity: 0.5 !default;

/// WK Focus outline recieve these HTML Elements:
///
/// @type List
$wk-focusable-elements: 'a[href]', 'area[href]', 'button', 'details', 'input',
    'iframe', 'select', 'textarea', '[contentEditable]',
    '[tabindex]:not([tabindex^="-"])', ':not([disabled])' !default;
