// combinable search
@import '../../../../styles/variables';
@import '../../../../styles/helpers';

:local(form.combinable-search) {
  &:global(.combinable-search) {
    button[data-e2e='cg-search-button'] {
      padding: 4px;
      padding-right: 8px;
    }

    button[data-e2e='cg-clear-button'] {
      padding: 8px;
    }
  }
}

// no matches message
:local(.no-matches-message) {
  overflow: auto;
  position: absolute;
  width: 100%;
  top: 100%;
  background-color: #fff;
  box-shadow: -6px 8px 15px rgba(0, 0, 0, 0.16);
  z-index: 9;
  padding: 18px 16px;

  :global {
    .main-message {
      line-height: 26px;
      font-size: 20px;

      .text {
        padding-left: 10px;
      }
    }

    .secondary-message {
      margin-top: 16px;
      line-height: 18px;

      .secondary-message-text {
        font-weight: 500;
        font-size: 14px;
        color: #007ac3;
      }
    }
  }
}
