.client-impact-card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  .card-title {
    display: flex;
    color: #353535;
    font-size: 20px;
    font-weight: 500;
    line-height: 26px;
    margin-bottom: 15px;
  }

  .title-icon {
    font-size: 24px;
    margin-left: 0;
    margin-right: 8px;
    padding-top: 2px;
  }

  .list {
    color: #005b92;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    list-style: none;
    margin-bottom: 24px;
    padding-left: 32px;
  }

  .bottom-link {
    padding-left: 32px;
  }
}
