$allowed-icons-container-width: 300px;

.allowed-icons-container {
  width: $allowed-icons-container-width;

  .allowed-icons-list-header {
    margin-bottom: 16px;
  }

  .allowed-icons-card-list {
    max-height: 418px;
    overflow-y: scroll;
    border: 0;

    .allowed-icons-card-list-item {
      padding: 8px 2rem;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .btn-delete {
        padding: 4px 8px;
        font-size: 14px;
      }
    }
  }
}

.list-icon,
.preview-icon {
  color: #005b92;
  font-size: 20px;
}

.allowed-icons-control {
  width: $allowed-icons-container-width;
  display: flex;
  align-items: center;
  justify-content: space-around;

  .add-icon-input,
  .add-icon-input:hover,
  .add-icon-input:focus {
    padding-left: 30px;
  }

  .preview-icon {
    position: absolute;
    padding: 10px 0;
  }
}
