:global {
  .edit-alert-dialogue {
    [data-e2e='cg-field'] {
      &:not(:first-child) {
        margin-top: 36px;
      }

      &:last-child {
        margin-bottom: 13px;
      }
    }

    .spinner {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      left: 0px;
    }
  }
}
