@import '../../../../styles/variables';

.arm2-link-back__container {
  background-color: $wk-primary-white;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 32px 32px 20px;
  width: 100%;

  :global {
    .arm2-link-back__title {
      flex-shrink: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 30px;
    }

    .arm2-link-back__list {
      flex-grow: 2;
      overflow-y: auto;
      width: 100%;
    }

    .arm2-link-back__list-item {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: $wk-primary-blue-shade1;

      .wk-icon {
        margin-left: 9px;
        text-decoration: none;
        vertical-align: middle;
        line-height: 20px;
      }
    }

    .thin {
      font-size: 14px;
      font-weight: 400;
    }
  }
}
