@import '../../../../../styles/variables';

.recently-viewed-widget {
  border: 1px solid #dadada;
  padding: 16px;

  &__title {
    padding-bottom: 14px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: $wk-primary-black;
  }

  &__items-list {
    &:not(:last-of-type) {
      margin-bottom: 10px;
    }

    a {
      font-family: 'Fira Sans', sans-serif;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: $wk-primary-blue-shade1;
    }
  }
}
