.suggestions {
  font-size: 0;
  overflow: auto;
  position: absolute;
  width: 100%;
  top: 40px; //height of search input
  background-color: #fff;
  box-shadow: -6px 8px 15px rgba(0, 0, 0, 0.16);
  z-index: 9;

  &.hidden {
    display: none;
  }
}

.suggestion-section-title {
  padding: 0 16px;
  color: #425e10;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 18px;
}

.suggestion-section {
  margin-top: 26px;
}

.suggestion-list {
  margin-top: 10px;
}

.suggestion-list-item {
  margin-top: 0px;
}

.suggestion {
  padding: 7.5px 16px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  line-height: 18px;
  color: #232323;
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;

  .suggestion-icon {
    margin: -2px 10px 0 0;
  }

  &:hover {
    background-color: #f6f6f6;
  }
}
