.freemium-banner {
  background: #85bc20;
  min-height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__title,
  &__link {
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 36px;
    color: #fff;
  }

  &__link {
    &:hover {
      color: #fff;
    }
  }
}
