@import '../../../../styles/variables';

.self-register-confirm-msg {
  display: flex;
  flex-direction: column;
  align-items: center;

  & &__box {
    padding: 25px;
    width: 589px;
    border: 1px solid $wk-primary-gray-tint4;
    background: $wk-primary-gray-tint6;

    margin-bottom: 24px;
  }

  & &__text {
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    color: $wk-primary-gray;
    font-size: 16px;
    line-height: 24px;
    word-break: break-word;

    &_bold {
      font-weight: 500;
    }

    &_margin-buttom {
      margin-bottom: 24px;
    }

    &_nowrap {
      white-space: nowrap;
    }
  }

  & &__title {
    color: $wk-primary-black;
    font-size: 22px;
    font-weight: 700;
    line-height: 25px;
  }

  & &__contact-text {
    font-size: 14px;
    line-height: 21px;
  }

  & &__list-item {
    margin: 0;
  }
}

@media (max-width: 589px) {
  .self-register-confirm-msg {
    & &__box {
      width: auto;
    }
  }
}
