.plain-card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  &__header {
    margin-bottom: 24px;
    display: flex;
    justify-content: flex-start;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    &_size_big {
      @extend .plain-card__header;
      font-size: 20px;
      line-height: 26px;
      margin-bottom: 15px;
    }

    &-icon {
      margin-right: 8px;
      margin-left: 0;
      font-size: 24px;
    }
  }

  &__external-icon {
    color: #005b92;
    font-size: 16px;
    margin-left: 8px;
    vertical-align: text-top;
    text-decoration: none;
  }

  &-list {
    list-style: none;

    &_padding_extra {
      padding-left: 32px;
    }

    &__item {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    &__item-title {
      color: #005b92;
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;

      &:hover {
        color: #003d61;

        .plain-card__external-icon {
          color: #003d61;
        }
      }
    }
  }

  &__footer {
    margin-top: 30px;
  }

  &__footer-link {
    color: #005b92;
    font-size: 14px;
    line-height: 18px;

    .wk-icon-arrow-right {
      font-size: 16px;
      margin-left: 8px;
      vertical-align: text-top;
    }

    &:hover {
      text-decoration: underline;

      .wk-icon-arrow-right,
      .plain-card__external-icon {
        text-decoration: none;
      }
    }
  }
}
