@import '../../../../styles/variables';

.self-register-form {
  width: 589px;

  & &__title {
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    color: $wk-primary-black;
    font-size: 22px;
    font-weight: 700;
    line-height: 25px;

    margin-bottom: 23px;

    &__symbol {
      font-weight: 400;
    }
  }

  & &__form {
    padding: 25px;
    border: 1px solid $wk-primary-gray-tint4;
    background: $wk-primary-gray-tint6;

    &__input {
      margin-bottom: 8px;
    }

    &__note {
      font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
      color: $wk-primary-gray;
      font-size: 14px;
      line-height: 21px;

      margin-bottom: 24px;
    }

    &__register-button {
      position: relative;

      width: 100%;
      align-items: center;
      justify-content: center;

      margin-bottom: 24px;
    }

    &__register-button-spinner {
      position: absolute;
      left: 0;
      width: 100%;
    }

    &__common-error {
      font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
      color: $wk-primary-red;
      font-size: 16px;
      font-style: italic;
      line-height: 20px;

      margin-bottom: 24px;

      &__note {
        font-weight: 500;
        margin-top: 8px;
      }
    }

    & > [class^='cg-button-field'] {
      width: 100%;
    }
  }
}
