@import '~@wk/fundamentals/typography';
@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';
@import '../pages/SearchResultsContent';

.smart-chart-box {
  border: 1px solid $wk-primary-gray-tint5;
  background: $wk-primary-gray-tint6;
  padding: $inner-space;
  margin-bottom: $search-content-vertical-space-between-blocks;

  @include breakpoint(max-width $wk-desktop-breakpoint) {
    $inner-space: 20px;
    padding: $inner-space;
  }

  &__title {
    color: $wk-primary-green-shade2;
    font-size: 14px;
    line-height: 18px;
  }

  &__topics {
    margin-top: 8px;

    &-breadcrumbs {
      margin-bottom: 8px;
    }

    &-crumb {
      display: inline-flex;
      align-items: center;

      &-title {
        font-size: 12px;
        line-height: 16px;
        color: $wk-primary-gray-shade1;
      }

      .smart-chart-box &-icon {
        font-size: 8px;
        padding: 0 8px;
        color: $wk-primary-gray-tint1;
        margin: 0;
      }
    }

    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 26px;
    }
  }

  &__jurisdictions-titles {
    height: 24px;
    overflow-x: hidden;
    word-break: break-all;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__jurisdictions {
    margin-top: 16px;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 6px;

    &-titles,
    &-prefix {
      font-size: 16px;
      line-height: 24px;
      margin: 0;
    }

    &-prefix {
      font-weight: 500;

      &::after {
        content: ' ';
      }
    }

    &-hits::after {
      content: ' ';
    }
  }

  &__branded-section {
    &-title {
      font-size: 16px;
      line-height: 24px;
      margin-top: 8px;
    }

    &-list {
      margin-top: 16px;
    }

    &-link {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
    }
  }
}
