@import '../../../../../../styles/variables';

.relations-tab {
  padding: 0 0 20px;
}

.relations-tab__tabs {
  border-bottom: none;
  padding-left: 98px;

  :global {
    .wk-page-container:not(.wk-row) {
      padding-left: 0;
    }
  }
}

.relations-tab__header {
  border-bottom: 1px solid $wk-primary-gray-tint4;
  position: sticky;
  padding-top: 65px;
  background-color: #fff;
  top: 160px;

  :global(.header_infoBannerVisible ~ .main-container) & :global {
    top: calc(160px + 76px);

    @media (max-width: 600px) {
      top: 260px;
    }
  }
}

.relations-tab__groups {
  padding-left: 106px;
  padding-top: 22px;
}

.relations-group {
  + .relations-group {
    margin-top: 30px;
  }
}

.relations-group__path {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: $wk-primary-gray-tint1;
  margin-bottom: 10px;
}

.relations-group__document {
  + .relations-group__document {
    margin-top: 20px;
  }
}

.relations-group__document-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $wk-primary-blue-shade1;
}

.relations-group__document-anchors {
  margin-top: 20px;
  list-style: disc;
  padding-left: 25px;

  li {
    font-weight: 400;
    font-size: 16px;
    line-height: 1;
    color: $wk-primary-gray-shade1;

    &:not(:last-of-type) {
      margin-bottom: 18px;
    }
  }
}
