@import '~@wk/fundamentals';

$section-font-size: 14px;

.footer-sections-card {
  margin-top: 0;
  background: $wk-primary-gray;

  .footer__copyright-section {
    padding: 1.25rem 1rem;
    background-color: $wk-primary-gray-tint6;

    &-link {
      font-weight: 500;

      &:not(:hover) {
        color: $wk-primary-blue-shade1;
      }
    }
  }

  @media (min-width: $wk-laptop-breakpoint) {
    .wk-footer-mid-col {
      min-width: 17%;

      &:nth-child(4) {
        min-width: 32%;
      }
    }
  }

  .wk-footer-mid {
    justify-content: space-between;
    flex-wrap: wrap;

    &.footer__logo-wrapper {
      padding-top: 0;
    }
  }

  .wk-footer-link-mid:focus {
    color: $wk-primary-white;
  }

  .wk-footer-mid-heading.footer-section__heading,
  .footer-subsection .wk-footer-mid-heading {
    color: $wk-primary-gray-tint6;
    font-size: $section-font-size;
  }

  .footer-subsection {
    padding-top: 16px;

    .wk-social-link {
      background-color: transparent;
      padding: 0;

      & [class*='wk-icon'] {
        font-size: 1.2rem;
      }
    }

    .wk-social-item {
      padding-right: 1rem;
      padding-bottom: 8px;
    }
  }
}
