$vertical-space-between-filter-items: 8px;
$vertical-space-between-filter-items-and-more-button: 14px;
$gray: #bfbfbf;
$green: #425e10;
$blue: #007ac3;
$black-light: #353535;
$black: #232323;

%filter-title {
  color: $green;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: $vertical-space-between-filter-items-and-more-button;
}

.filter-head-line {
  color: $green;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: $vertical-space-between-filter-items-and-more-button;
}

%filter-item {
  font-size: 12px;
  line-height: 16px;
  color: $black;
  font-weight: 400;
}

%filter-button-show-more {
  color: $blue;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  padding: 0;
  border: none; //wk-button make border
}

%filter-item-label {
  color: $black-light;
  font-size: 14px;
  line-height: 18px;
}

%filter-flex-wrapper-with-vertical-gap {
  display: flex;
  flex-wrap: wrap;
  gap: 18px 0;
}

%styles-for-checkbox-in-filters {
  $size-of-check-icon: 0.25rem;
  .wk-field-choice-text::before {
    top: 2px;
    transform: initial;
  }

  .wk-field-choice[type='checkbox']:checked + .wk-field-choice-text {
    font-weight: 500;
  }

  .wk-field-choice[type='checkbox']:checked:disabled + .wk-field-choice-text {
    font-weight: 400;
  }

  .wk-field-choice[type='checkbox']:disabled + .wk-field-choice-text {
    opacity: 1;
    color: $gray;

    &:before {
      border-color: $gray;
    }
  }

  .wk-field-choice[type='checkbox']:checked + .wk-field-choice-text::after {
    left: $size-of-check-icon;
    top: calc(#{$size-of-check-icon} + 2px);
    transform: rotate(-45deg);
  }

  .wk-field-choice[type='checkbox']:focus + .wk-field-choice-text {
    &::before {
      left: 2px; //to prevent left border cutting on focus state
      top: 1px; //to prevent top border cutting on focus state
    }

    &::after {
      left: calc(#{$size-of-check-icon} + 2px);
    }
  }
}
