@import '../../filters-common-styles';

.flat-list-filter {
  .ac-list__caption {
    @extend %filter-title;
  }

  .ac-list-item {
    @extend %filter-item;
    display: flex;
    padding: 5px 5px 5px 0;
    margin: 0;
    max-width: fit-content;

    &:not(.is-disabled):hover {
      cursor: pointer;
      color: #000;
      background: #f6f6f6;
    }

    .ac-list-item__label {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      display: inline-block;
      max-width: 100%;
    }

    .ac-list-item__badge {
      font-size: inherit;
      line-height: inherit;
      font-weight: inherit;
    }

    &:last-of-type {
      margin-bottom: $vertical-space-between-filter-items-and-more-button;
    }
  }

  .is-active {
    font-weight: 600;
    color: #000000;

    &:hover {
      color: #000000;
    }
  }

  .is-disabled {
    color: #bfbfbf;

    &:hover {
      color: #bfbfbf;
    }
  }

  .flat-list-filter__button.wk-button {
    @extend %filter-button-show-more;
  }
}
