@import '../../../styles/variables';
@import '../../../styles/helpers';

.header {
  background: $wk-primary-gray-tint5;
  padding: 15px 0;
  position: sticky;
  top: 88px;
  z-index: 7;

  @include breakpoint(max-width $wk-tablet-breakpoint) {
    top: 56px;
  }

  @media (max-width: 630px) {
    position: relative;
    z-index: 0;
    top: 0;
  }

  :global(.header_infoBannerVisible ~ .main-container) & {
    top: 165px;

    @media (max-width: 767px) {
      top: 133px;
    }

    @media (max-width: 630px) {
      position: sticky;
      top: 172px;
    }
  }

  .header-wrapper {
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;

    @media (max-width: 1199px) {
      flex-wrap: wrap;
    }

    @media (max-width: 767px) {
      max-width: 90%;
      padding: 0;

      .header-title {
        font-size: 24px;
      }
    }
  }
}

.tab-panel {
  width: 100%;
  background: $wk-primary-gray-tint5;
  padding: 0 3.5rem;
  top: 164px;
  z-index: 4;
  position: sticky;

  @media (max-width: 1199px) {
    top: 224px;
  }
  @include breakpoint(max-width $wk-tablet-breakpoint) {
    top: 176px;
  }
  @media (max-width: 630px) {
    position: relative;
    top: 0;
  }

  :global(.header_infoBannerVisible ~ .main-container) & {
    top: 241px;

    @media (max-width: 1199px) {
      top: 301px;
    }

    @media (max-width: 767px) {
      top: 253px;
    }

    @media (max-width: 630px) {
      position: sticky;
      top: 292px;
    }
  }

  :global {
    .tab-button {
      color: $wk-primary-blue-shade1;

      &.tab-button__active {
        color: $wk-primary-gray-shade1;
        background-color: white;
      }
    }
  }
}
