@import '../../../../../../styles/variables';
@import '../../../../../../styles/helpers';

.alerts-page {
  margin-bottom: 45px;

  &__header {
    margin-top: 38px;
  }

  &__table {
    margin-top: 30px;

    &-heading,
    &-data {
      border: 1px solid $wk-primary-gray-tint4;
    }
  }

  @include breakpoint(min-width $wk-tablet-breakpoint) {
    &__date-col {
      width: 175px;
    }

    &__edit-col,
    &__delete-col {
      width: 100px;
    }
  }

  &__edit-icon {
    color: $wk-primary-blue;
  }

  &__delete-icon {
    color: $wk-primary-red;
  }

  &__edit-icon,
  &__delete-icon {
    font-size: 20px;
  }

  &__pagination {
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
  }
}
