.tabs-list-wrapper {
  &.tabs-list-classic {
    border-bottom: 0.0625rem solid #dadada;

    .tabs-list-content {
      display: flex;
      margin-top: 16px;

      .wk-dropdown.wk-tab.wk-dropdown-toggle {
        border: none;

        &:hover {
          border: none;
        }
      }

      .wk-dropdown.wk-dropdown-menu {
        z-index: 10;
      }

      @media (min-width: 37.4rem) {
        .wk-dropdown.wk-dropdown-menu {
          z-index: 1;
          right: 0;
          left: auto;
        }
      }
    }
  }

  &.gray-theme {
    .active-tab {
      background-color: #f6f6f6;
    }

    .wk-tab:hover:not(.wk-dropdown-toggle) {
      background-color: #f6f6f6;
    }
  }

  &.light-theme {
    .wk-is-active.active-tab {
      border-bottom: 0.0625rem solid #ffffff;
    }

    .wk-tab {
      &:hover:not(.wk-dropdown-toggle) {
        border-bottom: 0.0625rem solid #ffffff;
      }
    }
  }

  &.tabs-list-classic,
  &.tabs-list {
    .active-tab {
      border-bottom: 0.0625rem solid #f6f6f6;
      pointer-events: none;

      &:hover {
        border-bottom: 0.0625rem solid #f6f6f6;
      }
    }
  }

  .tabs-list-content {
    margin-top: 20px;
  }

  .wk-tabs.wk-tabs-classic {
    flex: 1 1 auto;
    border-bottom: 0;
    margin-bottom: -0.0625rem;
    min-width: auto;
    outline: none;

    &::after {
      border-bottom: 0;
    }
  }

  .wk-tabs {
    min-width: auto;
  }
}
