@import 'helpers';

:root {
  --z-index-header: 1041; //.document-search-terms-control_container has z-index 1040 on FABS
  --z-index-home-tab-panel-view-all: var(--z-index-header);
  --z-index-history-sidebar-mobile-only: var(--z-index-header);
  --z-index-toasters: var(--z-index-header);
  --z-index-subheader: 7;
}

body {
  min-width: 320px;
}

a {
  cursor: pointer;
}

ul {
  list-style: none;
  padding-left: 0;
}

:global(.clear_padding) {
  padding: 0;
}

[class*='wk-icon-'] {
  width: auto;
}

:global {
  .wk-page-container {
    flex-basis: 100%;
  }
  .sidebar-root-big-width.wk-smoke-break .wk-smoke-break-content .wk-smoke-break-body {
    overflow-y: hidden;
    position: initial;
  }
}

@include breakpoint(min-width $wk-smartpad-breakpoint) {
  :global(.sidebar-root-big-width.wk-smoke-break .wk-smoke-break-content) {
    width: 27.5rem;
  }
}

@media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-tablet-breakpoint) {
  :global(.sidebar-root-full-screen.wk-smoke-break .wk-smoke-break-content) {
    width: 100vw;
  }
}

:global {
  .wk-notification-toast-container {
    z-index: var(--z-index-toasters);
  }
}

:global(.display-none) {
  display: none !important;
}

// overriding for search components
:global {
  .search-filter__title_gray {
    .filter-head-line,
    .ac-list__caption,
    .filter-head-line.complex-filter__title {
      color: $wk-primary-gray-shade1;
      font-weight: 500;
      letter-spacing: normal;
    }
  }

  .search-filter__title_gray-medium {
    .filter-head-line,
    .ac-list__caption,
    .filter-head-line.complex-filter__title {
      color: $wk-primary-gray-shade1;
      font-size: 12px;
      font-weight: 500;
      line-height: 16px;
    }
  }

  .search-filter__title_gray-small {
    .filter-head-line,
    .ac-list__caption,
    .filter-head-line.complex-filter__title {
      color: $wk-primary-gray-shade1;
      font-size: 12px;
      line-height: 16px;
      font-weight: 500;
      margin-bottom: 14px;
    }
  }

  .search-filter__trancated_small-mode {
    .grouped-list-filter-truncated__applied-filter:last-of-type,
    .complex-filter__applied-filter:last-of-type {
      margin-bottom: 8px;
    }
  }

  .search-filter__small-mode {
    // full complex filter small mode
    .full-complex-filter__filter-wrapper {
      gap: 10px 0;
    }
    .complex-filter__grouped-item {
      .complex-filter__grouped-item-title {
        font-weight: 500;
        font-size: 12px;
        margin-bottom: 7px;
      }
      .complex-filter__grouped-item__items-wrapper {
        margin-left: 24px;

        .complex-filter__grouped-item__select-button {
          font-size: 12px;
        }
        .complex-filter__floating-item__label {
          font-size: 12px;
          line-height: 20px;
        }
        .complex-filter__grouped-item__items {
          gap: 8px 0;

          .complex-filter__floating-item:last-child {
            margin-bottom: 24px;
          }
        }
      }
    }
  }

  .search-filter__medium-mode {
    // full flat list medium mode
    &.flat-list-filter {
      .is-active {
        font-weight: 500;
      }

      .ac-list__caption {
        margin-bottom: 6px;
      }
    }

    // complex filter small mode
    .full-complex-filter__filter-wrapper {
      gap: 12px 0;
    }
  }

  .search-filter__large-mode {
    // full flat list large mode
    &.flat-list-filter {
      .ac-list-item__label {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }

  .search-filter__one-column-mode {
    // full complex filter one column mode
    .grouped-list-filter .grouped-list-filter__container {
      grid-template-columns: 1fr;
      grid-row-gap: 14px;
      position: relative;
    }
  }
}
