.suggestion-tooltip {
  position: absolute;
  background-color: white;
  display: flex;
  justify-content: center;
  width: 100%;

  .tooltip-small {
    width: 100px;
  }

  .tooltip-medium {
    width: 250px;
  }

  .tooltip-big {
    width: 400px;
  }

  // styles for qa-answers in suggestions' tooltips (copied from app\common\directives\qaAnswer\scss\_qaAnswer.scss)
  .suggestion-quick-key {
    word-break: break-word;
    word-wrap: break-word;
    overflow: hidden;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 16px;

    abbr[title] {
      border-bottom: none;
      cursor: inherit;
      text-decoration: none;
    }

    .qa-bold {
      font-weight: 700;
    }

    .qa-italic {
      font-style: italic;
    }

    .qa-list > *:not(:first-child):before {
      content: ' ';
      display: block;
      line-height: 0.44em;
      white-space: pre;
    }

    .qa-text {
      font-size: 16px;
    }

    .qa-text,
    .qa-list {
      margin-top: 0.85em;
    }

    .qa-link {
      font-weight: 600;
    }

    .qa-premium {
      p,
      ul,
      ol {
        font-size: 16px;
      }

      ul,
      ol {
        padding-left: 24px;
        margin-top: 0;

        > li {
          position: relative;
        }
      }

      ul {
        list-style-type: circle;

        &.circles-list {
          list-style-type: disc;
        }

        &.dashes-list {
          > li {
            list-style-type: none;

            &::before {
              position: absolute;
              left: -15px;
              content: '-';
            }
          }
        }
      }

      ol {
        &.numbers-list {
          counter-reset: list;
          margin-bottom: 0;

          > li {
            list-style-type: none;

            &::before {
              position: relative;
              left: -25px;
              counter-increment: list;
              content: counter(list) ') ';
              min-width: 20px;
              margin-right: -20px;
              display: inline-block;
              text-align: right;
            }
          }
        }

        &.letters-list {
          counter-reset: list;
          padding-left: 48px;

          > li {
            list-style-type: none;

            &::before {
              position: absolute;
              left: -20px;
              counter-increment: list;
              content: counter(list, lower-alpha) ') ';
            }
          }
        }
      }
    }

    & > *:first-child {
      margin-top: 0;
    }
  }
}
