.icons-dropdown {
  padding: 5px 0;

  &__switcher,
  &__item {
    color: #005b92;
  }

  &__switcher {
    font-size: 30px;
  }

  &__menu.wk-dropdown-menu {
    width: inherit;
    min-width: 36px;
    left: 4px;
  }

  &__list {
    column-count: 3;
    column-gap: 0;
  }

  &__item {
    font-size: 24px;

    &:hover {
      color: #85bc20;
    }
  }

  &__item_color_red {
    color: #e5202e;
    font-size: 24px;

    &:hover {
      color: #721017;
    }
  }
}
