@import '~@wk/fundamentals';

.history-search {
  position: relative;
  width: 100%;
  max-width: 320px;

  &.wk-field {
    margin-bottom: 0;
  }

  .wk-field-body {
    .wk-field-input.history-search__input {
      font-size: 16px;
      font-weight: 400;
      padding-left: 1rem;
      padding-right: 4.25rem;
      height: 3rem;
      border: 1px solid $wk-primary-gray-tint1;
      color: $wk-primary-gray-shade2;
    }
  }

  .wk-field-inlay {
    margin: 0 0.5rem;
  }

  .wk-field-inlay > .wk-button {
    // @TODO ask jumpstart team about that default z-index
    z-index: unset;
  }

  .wk-button-icon:active .wk-icon-filled-close-circle,
  .wk-button-icon:active .wk-icon-filled-search {
    color: $wk-primary-blue-shade1;
  }

  .wk-button &__search-icon {
    font-size: 21px;
  }
}
