.configuration-box-container {
  margin: 20px 60px;
  position: -webkit-sticky;
  position: sticky;
  top: 80px;

  .configuration-box-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    .configuration-box-close-control {
      font-size: 18px;
      color: #005b92;

      &:hover {
        color: #003d61;
        cursor: pointer;
      }
    }
  }

  .wk-card-container-body .wk-card-container-body-cell {
    padding: 16px 32px;
  }

  .configuration-box-header-controls {
    display: flex;
    justify-content: space-evenly;

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      justify-content: space-around;
    }
  }

  .configuration-box-field {
    margin-bottom: 0.5rem;
  }

  .configuration-box-subheader {
    margin-top: 1.25rem;
  }

  .configuration-box-paired-field {
    display: flex;
    gap: 8px;
  }
}
