@import '../../../../filters-common-styles';

.complex-filter__floating-item {
  width: 100%;
  overflow: hidden;
  font-size: 0;

  .complex-filter__label-wrapper {
    display: flex;
    align-items: center;
  }

  .complex-filter__floating-item__label {
    @extend %filter-item-label;
    display: inline-flex;
    align-items: center;
    position: relative;
  }

  .complex-filter__floating-item__right-action-bar {
    margin-left: 10px;
  }

  .complex-filter__floating-item__left-action-bar {
    margin-right: 11px;
  }

  .wk-field-choice[type='radio'] {
    & + .wk-field-choice-text {
      line-height: 20px;
    }
    &:checked + .wk-field-choice-text::after {
      top: 10px;
    }
    &:focus + .wk-field-choice-text::before {
      box-shadow: none;
    }
  }
}
