@import '../../../../styles/variables';

.group-list {
  background-color: $wk-primary-white;
  position: sticky;
  top: 165px;

  @media (max-width: 1199px) {
    top: 225px;
  }
  @media (max-width: 767px) {
    top: 176px;
  }
  @media (max-width: 630px) {
    position: relative;
    top: 0;
  }

  :global(.header_infoBannerVisible ~ .main-container) & {
    top: 241px;

    @media (max-width: 1199px) {
      top: 301px;
    }
    @media (max-width: 767px) {
      top: 252px;
    }
    @media (max-width: 630px) {
      position: sticky;
      top: 283px;
    }
  }

  :global(.header_infoBannerVisible ~ .main-container [data-e2e-widget-id="tabPanel"]) ~ & {
    top: 280px;

    @media (max-width: 1199px) {
      top: 340px;
    }
    @media (max-width: 767px) {
      top: 293px;
    }
    @media (max-width: 630px) {
      position: sticky;
      top: 332px;
    }
  }
}

.with-tabs {
  top: 204px;

  @media (max-width: 1199px) {
    top: 264px;
  }
  @media (max-width: 767px) {
    top: 216px;
  }
  @media (max-width: 630px) {
    position: relative;
    top: 0;
  }
}

.wrapper {
  padding: 0 2rem 1rem;

  @media (max-width: 767px) {
    padding: 0;
  }
}

.empty-result {
  font-family: 'Fira Sans', sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  padding: 20px 10px;
  color: $wk-primary-black;
}

.form-input {
  position: relative;

  @media (max-width: 1199px) {
    width: 100%;
    padding: 10px 0;
  }
}

.search-input {
  min-width: 267px;
}

.search-input__button-clear-icon:active {
  &[class*='wk-icon']::before {
    color: $wk-primary-gray-tint4;
    background-color: $wk-primary-blue-shade1;
    border-radius: 50%;
  }
}

.close-button {
  &:hover {
    background-color: transparent !important;
  }
  &:active {
    background-color: transparent !important;
  }
}
