@import '../../../../../styles/helpers';

.product-name {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.25;
  color: #000;

  @include breakpoint(min-width $wk-smartpad-breakpoint) {
    font-size: 1.125rem;
    font-weight: 500;
    line-height: 1.3333333333;
  }
}
