@import '../../../../filters-common-styles';

.grouped-list-filter {
  @extend %styles-for-checkbox-in-filters;
  padding-right: 14px;

  .grouped-list-filter__group + .grouped-list-filter__group {
    margin-top: 16px;
  }

  .grouped-list-filter__group-title {
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    margin-bottom: 14px;
  }

  .grouped-list-filter__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 14px;
  }

  .grouped-list-filter__container_one-column {
    grid-template-columns: 1fr;
  }

  .grouped-list-filter__list-item {
    font-size: 0;
  }

  .grouped-list-filter__list-item + .grouped-list-filter__list-item {
    margin-top: 16px;
  }

  .grouped-list-filter__list-item-label {
    padding: 0;

    .wk-field-choice[type='radio'] {
      + .wk-field-choice-text {
        &::before {
          top: 2px;
          width: 16px;
          height: 16px;
        }
      }
      &:focus {
        & + .wk-field-choice-text::before {
          box-shadow: none;
        }
      }
      &:checked {
        & + .wk-field-choice-text {
          &::after {
            left: 8px;
            top: 10px;
            border: none;
            width: 4px;
            height: 4px;
            background-color: white;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }

  .grouped-list-filter__list-item-text {
    @extend %filter-item-label;
  }
}
