/// @group grid
/// @author [*/dxg](https://gpo.wolterskluwer.com/)
/// @since 0.0.1
/// @access public

@import './grid.variables';

/// Add width styles of $n columns size and default-container paddings - left and right,
/// which by default are setted to $wk-grid-padding.
///
/// @require {Mixin} $wk-default-container-padding
///
/// @param {Number} $n [1] - width value.
/// @param {Number} $columnNumber [12] - number of columns in 100% width
///
/// @example scss - Usage
///   .row {
///     @include wk-make-n-units-column(2);
/// }

@mixin wk-make-n-units-column($n: 1, $columnNumber: 12) {
    width: calc(#{$n} / #{$columnNumber} * 100%);

    @include wk-default-container-padding;
}

/// Add width styles of $n columns size excluding default-container paddings - left and right,
/// which by default are setted to $wk-grid-padding.
///
/// @param {Number} $n [1] - width value.
/// @param {Number} $columnNumber [12] - number of columns in 100% width
///
/// @example scss - Usage
///   .row {
///     @include wk-make-n-units-column-no-paddings(2);
///   }

@mixin wk-make-n-units-column-no-paddings($n: 1, $columnNumber: 12) {
    width: calc(#{$n} / #{$columnNumber} * 100% - #{$wk-grid-padding} * 2);
}

/// Creates 3 columns grid
///
/// @require {Mixin} from
/// @require {Mixin} $wk-make-n-units-column
///
/// @param {String} $wk-grid-name - grid wrapper class
///
/// @example scss - Usage
///   .row {
///     @include wk-make-three-columns-grid('wk-grid');
///   }

@mixin wk-make-three-columns-grid($wk-grid-name) {
    .#{$wk-grid-name} {
        display: flex;
        flex-direction: column;

        @include from(tablet) {
            flex-wrap: wrap;
            flex-direction: row;
        }

        @include from(desktop) {
            flex-wrap: nowrap;
        }

        .#{$wk-grid-name}-column {
            @include from(tablet) {
                @include wk-make-n-units-column(6);
            }

            @include from(desktop) {
                @include wk-make-n-units-column(4);
            }
        }
    }
}

/// Creates $n columns, one of which is main column,
/// others ordinary columns has width $width/12 including $gutter - gutters-like padding.
/// Main column takes the remaining space.
///
/// If applied to `.wk-page-content` or similar should be used with `wk-viewport-insulate` mixin.
///
/// If it is 2-column layout it is needed to apply additional `.wk-to-left` or
/// `.wk-to-right` classes for main column to define its alignment.
///
/// @require {Mixin} $wk-make-n-units-column
///
/// @param {String} $column-name [wk-page-column] - column class
/// @param {Number} $n [3] - number of total page columns
/// @param {Number} $width [3] - width of ordinary page columns
/// @param {Number} $gutter [.5]- width of gutter-like padding for ordinary page columns
///
/// @example scss - Usage
///   .wrapper {
///     @include wk-page-n-column-grid();
///   }
///

@mixin wk-page-n-column-grid(
    $column-name: 'wk-page-column',
    $n: 3,
    $width: 3,
    $gutter: 0.5
) {
    display: flex;

    .#{$column-name} {
        @include wk-make-n-units-column($width);

        &:not(.#{$column-name}-main) {
            padding-right: calc(100% / 12 * #{$gutter});
        }

        &.#{$column-name}-main ~ .#{$column-name}:not(.#{$column-name}-main) {
            padding-left: calc(100% / 12 * #{$gutter});
            padding-right: $wk-grid-padding;
        }

        &.#{$column-name}-main {
            @include wk-make-n-units-column(12 - ($n - 1) * $width);

            &:last-child {
                margin-right: auto;
            }

            &:first-child {
                margin-left: auto;
            }

            // for 2 columns grid with empty ordinary column
            &.wk-align-left {
                margin-left: 0;
            }

            &.wk-align-right {
                margin-right: 0;
            }
        }
    }
}

// Grid mixins

/// Container for columns. Inside it the columns will be aligned in a row.
///
/// @require {Mixin} from
/// @require {Variable} $wk-grid-gap-padding
///
/// @example scss - Usage
///   .row {
///       @include wk-column-group;
///       .col-12 {
///           @include wk-column(12)
///       }
///   }
///
@mixin wk-column-group {
    display: flex;
    flex-wrap: wrap;
    margin-left: (-$wk-grid-gap-padding);
    margin-right: (-$wk-grid-gap-padding);

    @include from(laptop) {
        flex-wrap: nowrap;
    }
}

/// In case there are few columns in a row their width will be equal to each other.
/// @require {Mixin} to
/// @require {Variable} $wk-grid-gap-padding
///
/// @example scss - Usage
/// .col {
///     @include wk-column-base;
/// }
///

@mixin wk-column-base {
    flex-shrink: 1;
    flex-grow: 0;
    flex-basis: $wk-grid-column-base-flex-basis;
    padding-left: $wk-grid-gap-padding;
    padding-right: $wk-grid-gap-padding;

    @include to(tablet) {
        flex-basis: $wk-grid-column-base-flex-basis-mobile;
        max-width: $wk-grid-column-base-flex-basis-mobile;
        min-width: $wk-grid-column-base-flex-basis-mobile;
    }
}

// Utils

/// Checks max-width of a breakpoint and puts @content if it fits into page container's max width
/// @require {Variable} $wk-page-container-max-width
/// @require {Variable} $wk-page-container-max-width-desktop
///
/// @param {String} $breakpoint - breakpoint's name
///
@mixin wk-check-max-slice($breakpoint) {
    $breakpoint-max-width: map-get($wk-page-container-max-width, $breakpoint);
    @if ($breakpoint-max-width <= $wk-page-container-max-width-desktop) {
        @content;
    }
}

/// Creates column with $n width and $from breakpoint
///
/// @require {Mixin} from
/// @require {Mixin} at
/// @require {Mixin} $wk-column-base
/// @require {Mixin} $wk-set-grid-col-width
/// @require {Variable} $slicer-breakpoint-names
/// @require {Variable} $slicer-breakpoints
/// @require {Variable} $wk-page-container-max-width-desktop
/// @require {Variable} $wk-grid-col-width
/// @require {Variable} $wk-grid-gap-gutter
///
/// @param {Number} $n [12] - width value
/// @param {String} $from [mobile] - breakpoint's name
///
/// @example - Usage
///  .col-12 {
///     @include wk-column(12);
///}
///
@mixin wk-column($n: 12, $from: mobile) {
    $slice: 1 !default;
    @if ($from != mobile) {
        $slice: index($slicer-breakpoint-names, $from);
    }
    $next-slice: $slice + 1 !default;

    @while $slice <=
        $amount-of-slices and
        nth($slicer-breakpoints, $next-slice) <=
        $wk-page-container-max-width-desktop
    {
        $slice-name: nth($slicer-breakpoint-names, $slice);
        $col-width: map-get($wk-grid-col-width, $slice-name);
        $col-not-class: '';

        @if ($slice-name != $from) {
            $col-not-class: ':not([class*=-#{$slice-name}])';
        }

        @include from($slice) {
            @include wk-column-base;

            max-width: $n * $wk-grid-column-base-flex-basis;
            min-width: $n * $wk-grid-column-base-flex-basis;

            @if $n > 1 {
                flex-grow: $n;
            }
        }

        $slice: $slice + 1;
        $next-slice: $slice + 1;
    }
}

// Page container mixins

/// Creates a container. There are two usage of container
/// - for content;
/// - for grid (columns);
///
/// @require {Mixin} $wk-column-group
/// @require {Mixin} $wk-page-container-width
/// @require {Mixin} $wk-page-container-padding
///
/// @param {Bool} $in-grid [false] - is this container for grid
///
/// @example - Usage
/// .container {
///     @include wk-page-container($in-grid: true);
///     .col {
///         @include wk-column(12);
///     }
/// }
///
@mixin wk-page-container($in-grid: false) {
    @if ($in-grid) {
        @include wk-column-group;
    }

    @include wk-page-container-width;
    @include wk-page-container-padding($in-grid);
}

/// Generates max-width based on breakpoint for a container
/// @require {Mixin} wk-check-max-slice
/// @require {Mixin} from
/// @require {Variable} $wk-page-container-max-width
///
/// @example - Usage
/// .container {
///     @include wk-page-container-width;
/// }
///
@mixin wk-page-container-width {
    width: 100%;

    @each $breakpoint, $max-width in $wk-page-container-max-width {
        @include wk-check-max-slice($breakpoint) {
            @include from($breakpoint) {
                max-width: $max-width;
            }
        }
    }
}

/// Adds padding for a container based on given device breakpoint and it's relation to grid
///
/// @require {Variable} $wk-page-container-paddings
/// @require {Variable} $wk-page-container-paddings-in-grid
///
/// @param {Bool} $in-grid [false] - is this container for grid
///
/// @example - Usage
/// .container {
///     @include wk-page-container-padding($in-grid: true);
/// }
///

@mixin wk-page-container-padding($in-grid: false) {
    $paddings-map: $wk-page-container-paddings;

    @if ($in-grid) {
        $paddings-map: $wk-page-container-paddings-in-grid;
    }

    @each $breakpoint, $padding in $paddings-map {
        @if ($breakpoint == mobile or $breakpoint == phablet) {
            padding-left: map-get($paddings-map, $breakpoint);
            padding-right: map-get($paddings-map, $breakpoint);
        } @else {
            @include from($breakpoint) {
                padding-left: map-get($paddings-map, $breakpoint);
                padding-right: map-get($paddings-map, $breakpoint);
            }
        }
    }

    margin-left: auto;
    margin-right: auto;
}

/// Creates container and aligns in center
/// @require {Mixin} $wk-page-container-width
///
/// @example - Usage
/// .container {
///     @include wk-container();
/// }
///
@mixin wk-container() {
    @include wk-page-container-width;

    margin-left: auto;
    margin-right: auto;
}

/// Give a block container horizontal padding only when smaller than the defined width as to not touch the window’s viewport edges
///
/// @param {Unit} $width [$wk-desktop-max-width]
/// @param {Unit} $padding [$wk-grid-padding]
///
/// @require {Mixin} wk-container
/// @require {Mixin} breakpoint-sass/breakpoint - from breakpoint of course
/// @require {Variable} $wk-grid-padding
/// @require {Variable} $wk-grid-padding-desktop
/// @require {Variable} $wk-desktop-max-width
///
/// @example scss - Usage
///   .my-div {
///     @include wk-viewport-insulate(wk-rem-value(300px));
///   }
@mixin wk-viewport-insulate(
    $padding: $wk-grid-padding,
    $padding-desktop: $wk-grid-padding-desktop,
    $width-tablet: $wk-tablet-max-breakpoint
) {
    @include wk-container;

    padding-left: $padding;
    padding-right: $padding;

    @include breakpoint($width-tablet) {
        padding-left: $padding-desktop;
        padding-right: $padding-desktop;
    }
}

/// Creates paddings
/// @require {Variable} $wk-grid-padding
///
/// @param {Unit} $padding [$wk-grid-padding] - container's padding
///
/// @example - Usage
/// .container {
///     @include  wk-default-container-padding();
/// }
///
@mixin wk-default-container-padding($padding: $wk-grid-padding) {
    padding-left: $padding;
    padding-right: $padding;
}

/// Give a block container horizontal padding only when smaller than the maximum desktop breakpoint width. Use wk-viewport-insulate instead.
///
/// @param {Bool} $deprecated-transition [false]
/// @deprecated
///
/// @example scss - Usage
///   .my-div {
///     @include wk-page-content;
///   }
@mixin wk-page-content($deprecated-transition: false) {
    @include wk-viewport-insulate;
}
