@import '../../../../../styles/_helpers.scss';

.wnc-topic-navigation {
  padding-top: 30px;

  @include breakpoint(max-width $wk-tablet-breakpoint) {
    display: block;
    padding-left: 16px;
    padding-bottom: 0;
  }

  h3 {
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: 1px;
    text-align: left;
    color: $wk-primary-green-shade2;
    margin-bottom: 14px;
  }

  .wnc-topic-navigation__item {
    display: block;
    line-height: 1;

    &:not(:last-of-type) {
      margin-top: 10px;
    }
  }
  .wnc-topic-navigation__btn {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    text-align: left;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &.wnc-topic-navigation__btn--active {
      font-weight: 700;
    }
  }
}

.wnc-content {
  padding-top: 58px;

  @media screen and (max-width: 1024px) {
    padding-top: 34px;
  }
  :global {
    [data-e2e-widget-id="document"] {
      a {
        font-weight: 400;
        color: $wk-primary-blue;
      }

      b > a {
        font-weight: 500;
      }
    }
  }


  &.wnc-content--gov {
    [data-e2e-widget-id="document"] {
      a {
        font-weight: 500;
      }
    }
  }

  :global {
    .standalone-document-page.wk-page-container:not(.wk-row) {
      padding-right: 0;

      > .wk-row {
        @media screen and (max-width: 1024px) {
          display: flex;
          flex-direction: column-reverse;
        }
      }
    }
  }

  :global {
    .standalone-document-page.wk-page-container:not(.wk-row) {
      padding-right: 0;
    }
  }
}
