@import '~@wk/fundamentals';

@mixin remove-press-effect-ie {
  transform: translate(0, 0);
}

.search-input {
  margin-bottom: 0;

  .hidden {
    display: none;
  }

  &.wk-field.wk-field-extra-large,
  &.wk-field.wk-field-secondary {
    margin: 0;
  }

  &.wk-field-is-disabled {
    .wk-field-input.search-input__input {
      padding-right: 6.5rem;
    }
  }

  &.search-input_has-prefilter {
    .wk-field-input.search-input__input {
      padding-left: 5.5rem;
      padding-right: 6rem;

      @media (max-width: $wk-smartpad-breakpoint) {
        padding-left: 58px;
      }
    }
  }

  .search-input__button-submit-icon,
  .search-input__button-clear-icon,
  .search-input__prefilter-icon {
    @include remove-press-effect-ie;
  }

  .search-input__button-submit-icon,
  .search-input__button-clear-icon {
    &[class*='wk-icon-filled']::before {
      color: #005b92;
    }
  }

  .search-input__prefilter {
    padding: 8px 18px 8px 15px;

    .search-input__prefilter-button {
      width: 32px;
      height: 32px;
      padding: 0;
      border-radius: 50%;
      border: 1px solid #dadada;
      background-color: transparent;
      cursor: pointer;

      .search-input__prefilter-icon {
        font-size: 18px;
        margin-top: 4px;
        margin-right: 4px;
        color: #353535;
      }

      &.search-input__prefilter-button_active {
        border-color: #c2de90;
        background-color: #d4e8b1;

        .search-input__prefilter-icon {
          color: #648d18;
        }
      }

      &:hover,
      &:focus {
        border-color: #a4cd58;

        .search-input__prefilter-icon {
          color: #85bc20;
        }

        &.search-input__prefilter-button_active {
          border-color: #a4cd58;

          .search-input__prefilter-icon {
            color: #425e10;
          }
        }
      }
    }
  }

  &.wk-field-secondary .wk-field-input.search-input__input,
  .wk-field-input.search-input__input {
    font-size: 14px;
    line-height: 18px;
    padding-right: 4.5rem;

    &:-ms-input-placeholder {
      padding-right: 3rem;
    }

    &:placeholder-shown {
      padding-right: 3rem;
    }

    @media (max-width: $wk-smartpad-breakpoint) {
      @supports (-webkit-touch-callout: none) {
        padding-left: 10px;
      }
    }
  }

  &.wk-field-secondary .wk-field-body .wk-field-inlay .wk-button.wk-button-icon {
    margin-right: 0;
  }

  .wk-field-inlay .wk-button {
    z-index: 1;
  }

  @media (max-width: $wk-smartpad-breakpoint) {
    .search-input__prefilter {
      padding: 5px;
    }

    .search-input__spinner {
      padding: 0.15rem;
    }

    &.search-input_has-prefilter.wk-field.wk-field-extra-large .wk-field-input.search-input__input {
      padding-left: 4rem;
      padding-right: 5.5rem;
    }
  }

  @media (max-width: $wk-desktop-breakpoint) {
    .wk-button .search-input__button-clear-icon {
      font-size: 16px;
    }

    .wk-button .search-input__button-submit-icon {
      font-size: 21px;
    }
  }
}
