@import '../../../../styles/variables';

.current-developments_container {
  background-color: $wk-primary-white;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 32px 32px 20px;
  width: 100%;

  :global {
    .current-developments_title {
      flex-shrink: 0;
      font-weight: 500;
      font-size: 16px;
      margin-bottom: 16px;
    }

    .current-developments_list {
      flex-grow: 2;
      overflow-y: auto;
      width: 100%;
    }

    .current-developments_list-item {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 10px;
      color: $wk-primary-blue-shade1;

      .wk-icon {
        margin-left: 9px;
        text-decoration: none;
        vertical-align: middle;
        line-height: 20px;
      }
    }

    .thin {
      font-size: 14px;
      font-weight: 400;
    }

    .framed {
      padding: 32px;
      border: 1px solid $wk-primary-gray-tint4;

      .framed-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: $wk-primary-gray-shade1;
        padding-bottom: 15px;
      }

      .framed-link {
        display: flex;
        align-items: flex-start;

        span {
          font-size: 22px;
        }

        a {
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;
          color: $wk-primary-blue;
          padding-left: 8px;
        }
      }
    }
  }
}
