@import '../../../../../../styles/variables';

.esg-tool-search-sidebar {
  height: 100%;

  display: flex;
  flex-direction: column;

  &__options-filter {
    padding-right: 65px;
    margin-bottom: 30px;
  }

  &__title {
    font-size: 20px;
    color: $wk-primary-gray-shade1;

    margin-bottom: 14px;
  }

  &__filter-wrapper {
    overflow-y: auto;

    & > *:not(:last-child) {
      margin-bottom: 34px;
    }
  }
}
