@import '@wk/fundamentals';
@import '@wk/components-react16/dist/forms.css';
@import '@wk/components-react16/dist/fonts.css';

.favorites-popup {
  position: relative;
  width: 320px;
  padding-top: 15px;
  padding-bottom: 6px;

  &__folders {
    width: 100%;
    margin-bottom: 16px;
  }

  &__folder {
    display: flex;
  }

  &__folder.favorites-popup__folder_disabled {
    color: $wk-primary-gray-tint3;
    cursor: not-allowed;
  }

  &__fields {
    [data-e2e='cg-field'] {
      padding-top: 17px;
    }
  }

  .select__button {
    display: block;

    * {
      pointer-events: none;
      align-items: flex-start;
    }
  }

  .cg-field-textarea {
    padding-top: 17px;
  }

  [data-e2e='cg-field'] .error {
    color: $wk-primary-red;
  }
}

.favorites-popup .spinner,
.save-to-favorites-content .spinner {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

@include breakpoint(min-width $wk-tablet-breakpoint) {
  .favorites-popup {
    width: 520px;
    padding-right: 12px;

    &__folders {
      max-width: 268px;

      .select__options {
        .option {
          padding: 0;
          > * {
            display: block;
            flex: 1;
            padding: 7px 16px 7px;
          }
        }
      }
    }
  }
}
