$header-height: 56px;
$footer-height: 389px;

.center-in-container {
  display: flex;
  align-items: center;
  justify-content: center;

  white-space: nowrap;
  height: calc(100vh - #{$header-height} - #{$footer-height});
  min-height: 200px;
}

.spinner {
  margin: 0;
  text-align: center;
}

.spinner-position-inline-centered {
  width: 100%;
  margin: auto;
  display: inline-block;
}

.spinner-size-big {
  font-size: 80px;
}

.spinner-size-medium {
  font-size: 48px;
}

.spinner-size-normal {
  font-size: 30px;
}

.spinner-size-mini {
  font-size: 16px;
}

.spinner-position-inline {
  display: inline-block;
}

.spinner-position-container-centered {
  @extend .center-in-container;
}

.spinner-position-screen-centered {
  @extend .center-in-container;

  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}
