@import '../../../../../styles/variables';

.master-glossary-pop-up {
  :global {
    [data-e2e='cg-tooltip'][class*='cg-position'].cg-tooltip-interactive.cg-tooltip-open {
      z-index: 1;
      width: 378px;
      height: 174px;
    }

    .cg-tooltip-interactive [data-e2e*='cg-tooltip-body'].cg-tooltip-body {
      display: flex;
      flex-direction: column;
      height: 100%;

      .spinner {
        width: 100%;
      }
    }

    .cg-tooltip-content {
      height: 100%;

      &:before {
        content: '';
        display: block;
        width: 100%;
        height: 10px;
        background-color: transparent;
        position: absolute;
        top: 100%;
        left: 0;
      }
    }

    .master-glossary-pop-up-content {
      overflow-x: hidden;
      overflow-y: auto;
      height: 100%;

      .document-content {
        margin: 0;
      }

      h1,
      .metadataContainer {
        display: none;
      }

      .docCopyrightInfo {
        margin-top: 72px;
      }
    }

    .master-glossary-tooltip__header {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      [data-e2e='cg-button-field-icon'] {
        position: relative;
        top: -16px;
        right: -16px;
      }
    }

    .master-glossary-tooltip__title {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: $wk-primary-green-shade2;
      min-height: 40px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }
}
