@import '../../../../../styles/variables';

.action-bar-button-in-dropdown {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 16px;
}

:global [class*='cg-banner-action-bar-item'] [class*='cg-button-field'] {
  :local button.action-bar-button:hover {
    color: $wk-primary-blue-shade2;
  }

  button.action-bar-button_login,
  button.action-bar-button_login[data-e2e-element-type="button"] {
    background: $wk-primary-blue;
    color: $wk-primary-white;

    &:hover {
      background: $wk-primary-blue-shade2;
      color: $wk-primary-white;
      text-decoration: none;
    }
  }
}
