@import '@wk/components-react16/dist/dropdown.css';
@import '@wk/components-react16/dist/fonts.css';
@import '~@wk/fundamentals';

.suggestions-dropdown {
  background-color: var(--cg-dropdown-menu-background-color, #fff);
  border: 0.0625rem solid;
  border-color: var(--cg-dropdown-menu-border-color, #dadada);
  box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.1);
  padding: 0;
  min-width: 11rem;
  max-width: 100%;
  max-height: 13.2rem;
  overflow-y: auto;
}

.suggestions-dropdown__list {
  list-style-type: none;
  margin: 0;

  &.ac-list {
    padding: 0.25rem 0;
  }
}

li.ac-list-item.suggestions-dropdown__item {
  padding: 0;
  margin: 0;

  [data-e2e='cg-button-field'].cg-button-field-text {
    width: 100%;
    button {
      width: 100%;
      text-align: left;
      color: $wk-primary-black;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
