@import '~@wk/fundamentals';

.srl {
  position: relative;

  &__item-wrapper {
    &:not(:last-child) {
      margin-bottom: 32px;
    }

    @media (max-width: $wk-tablet-breakpoint) {
      margin-bottom: 25px;
    }
  }

  .spinner {
    z-index: 1051; // .wk-modal has 1050
  }

  &__metadata-score-button {
    position: absolute;
    top: 2px;
    right: 80px;
    z-index: 1;
    cursor: pointer;
  }

  &__metadata-score-button_hidden {
    color: transparent;
  }
}
