@import '../../../../../styles/variables';

.search-sidebar {
  height: 100%;
  display: flex;
  flex-direction: column;

  &__title {
    font-size: 20px;
    color: $wk-primary-gray-shade1;
    margin-bottom: 32px;
  }

  &__filter-wrapper {
    overflow-y: auto;
    margin-left: -4px;
    padding-left: 4px;

    & > *:not(:last-child) {
      margin-bottom: 34px;
    }

    :global(.ac-list-item__label) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.search-international-sidebar {
  .search-sidebar__title {
    margin-bottom: 13px;
  }

  [class*='cg-tabs-ctx'] {
    overflow-y: auto;
    display: flex;
    flex-direction: column;

    & > *:not([class*='cg-tabs ']) {
      overflow-y: auto;
    }

    [class*='cg-tabs '] {
      margin-bottom: 29px;
    }

    [class*='cg-tab ']:first-child {
      [class*='cg-tab-inner-content'] {
        padding-left: 0;
      }
    }

    [class*='cg-tab-panel '] {
      margin: 0;
    }
  }
}
