.document-action.document-action__save-link {
  position: relative;

  &.left-position {
    position: static;

    .copy-link-modal,
    .error-link-modal {
      left: 0;
      right: auto;
      width: 100%;
      max-width: 412px;
    }
  }

  .copy-link-modal {
    z-index: 51;
    position: absolute;
    right: 0;
    top: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid;
    border-left: 4px solid;
    border-color: #007ac3;
    padding: 15px 15px 12px 15px;
    color: #323232;
    width: 412px;

    &.copied {
      border-color: #648d18;
    }

    .copy-link-header {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 18px;

      .copy-link-header-text {
        font-size: 14px;
        font-weight: 500;
        line-height: 14px;
      }

      .copy-link-close-button {
        color: #757575;
        cursor: pointer;
        line-height: 14px;
      }
    }

    .copy-link-body {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    .copy-link-footer {
      line-height: 16px;
      width: 100%;
      .copy-link-footer-text {
        color: #232323;
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
      }
    }

    .cg-field-small {
      display: flex;
      align-items: center;
      margin: 0;
      padding-bottom: 10px;
      width: 100%;
      padding-right: 15px;

      [data-e2e='cg-field-header'] {
        width: max-content;
        margin-right: 15px;
      }

      .wk-input {
        max-width: 298px;
        height: 32px;
        padding: 7px;
        border: 1px solid #757575;
        background-color: white;
        color: #474747;

        &:hover {
          border-color: #474747;
        }

        &:focus {
          border-color: #007ac3;
        }
      }
    }

    .copy-link-button {
      width: 64px;
      height: 32px;
      padding: 6px 15px;
      font-weight: 400;

      [data-e2e='cg-icon'] {
        line-height: 1rem;
      }

      &.copied {
        background-color: #648d18;
        border-color: #648d18;
      }

      &:hover:not([disabled]):not(.copied) {
        background-color: #007ac3;
      }
    }
  }

  .error-link-modal {
    z-index: 51;
    position: absolute;
    right: 0;
    top: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid;
    border-left: 4px solid;
    border-color: #e5202e; // $wk-primary-red
    padding: 15px 15px 16px;
    width: 336px;

    .error-link-modal__header {
      display: flex;
      justify-content: space-between;
      width: 100%;
      line-height: 14px;

      .error-link-modal__header-text {
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        padding-left: 16px;
        color: #353535; // $wk-primary-gray-shade1
      }

      .error-link-modal__close-btn {
        color: #757575; // $wk-primary-gray-tint1
        cursor: pointer;
        line-height: 14px;
      }
    }

    .error-link-modal__body {
      width: 100%;
      padding: 4px 30px 0 32px;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: #232323; // $wk-primary-gray-tint7
      text-align: left;
    }
  }
}
