@import '../../../../../../styles/variables';

.checklist-group-row {
  position: relative;
  font-size: 14px;
  line-height: 18px;
  color: $wk-primary-gray-shade1;

  min-width: 100%;

  &_expanded::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    z-index: 1;
    background-color: $wk-primary-blue;
  }

  & &__group {
    height: 56px;

    &_active {
      background-color: $wk-primary-blue-tint6;
    }
  }

  & &__column {
    display: flex;
    align-items: center;
    flex-shrink: 0;

    &_center {
      justify-content: center;
    }
  }
}

.checklist-group-action-row {
  & &__group {
    justify-content: end;
  }
}
