@import '../../../../../../../styles/variables';

.group-list {
  .group-list__item {
    + .group-list__item {
      padding-top: 17px;
      margin-top: 18px;
      border-top: 1px solid #dadada;
    }
  }

  :global {
    .ac-list__wrapper {
      + .ac-list__wrapper {
        padding-top: 17px;
        margin-top: 18px;
        border-top: 1px solid #dadada;
      }

      .ac-list_inner {
        margin-top: 0;
      }
    }

    .ac-list-item {
      line-height: 20px;

      + .ac-list-item {
        margin-top: 6px;
        line-height: 20px;
      }
    }

    .ac-list__caption {
      margin-bottom: 14px;
    }
  }

  &.subgroup-list {
    :global {
      .ac-list__wrapper {
        + .ac-list__wrapper {
          padding-top: 0;
          border-top: none;
          margin-top: 14px;
        }
      }
      .ac-list__caption {
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        font-feature-settings: 'pnum' on, 'lnum' on, 'case' on, 'cpsp' on, 'mgrk' on;
        color: $wk-primary-green-shade2;
        margin-bottom: 6px;
        text-transform: uppercase;
      }
    }
  }
}
