@import '../../../../styles/variables';

.checklist-table {
  .checklist-table__content-wrapper {
    display: flex;
  }

  .checklist-table__main-content {
    display: flex;
    flex-direction: column;
    align-items: start;

    width: 100%;
    overflow: auto;

    &__list {
      min-width: 100%;
    }
  }

  .checklist-table__action-bar {
    position: relative;

    &__header {
      height: 41px;
    }

    &_shadow {
      box-shadow: -4px 0px 8px 0px rgba(0, 0, 0, 0.1);
    }
  }

  .checklist-table__row {
    display: flex;

    min-width: 100%;
    background-color: $wk-primary-white;
    border-bottom: 1px solid $wk-primary-gray-tint4;
  }
}
