$wk-primary-white: #fff;
$wk-primary-gray-shade1: #353535;
$wk-primary-gray-tint1: #757575;
$wk-primary-gray-tint4: #dadada;

.adaptive-browse-tree-wrapper {
  display: inline-block;
  position: relative;
  user-select: none;
  height: 100%;

  &.with-tab-panel {
    .adaptive-browse-tree {
      height: calc(100% - 60px);
    }

    .expander {
      top: -60px;
    }
  }

  &.mobile-view {
    position: static;
    overflow: visible;

    .adaptive-browse-tree {
      padding-left: 20px;

      .top-panel {
        .top-panel-button {
          right: 0;
        }
      }
    }
  }

  .adaptive-browse-tree {
    height: 100%;
    width: 100%;
    margin-right: 15px;
    overflow: hidden;
    transition: width;

    .browse-tree-wrapper {
      height: 100%;

      &.collapsed {
        display: none;
      }
    }

    &.mobile-browse-tree {
      position: absolute;
      background-color: $wk-primary-white;
      width: 100vw;
      transition: width 0.2s;
      padding-left: 0;
      left: -0.5rem;

      &.collapsed {
        width: 0;
        padding: 0;
        margin: 0;
        border: none;
      }
    }

    .tab-panel {
      position: fixed;
      height: 60px;
      overflow: hidden;
      z-index: 2;
      margin-top: -1px;
    }
    .top-panel {
      border-bottom: 1px solid $wk-primary-gray-tint4;
      padding: 13px 35px 13px 0;
      height: 50px;
      position: relative;

      &::after {
        content: '';
        pointer-events: none;
        position: absolute;
        z-index: 1;
        top: 50px;
        width: 100%;
        left: 0;
        min-height: 20px;
        height: 20px;
        background-image: linear-gradient(rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
      }

      .top-panel-text {
        display: inline-block;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        padding-left: 16px;
        font-size: 14px;
        color: $wk-primary-gray-shade1;
        font-weight: 500;
        line-height: 26px;

        &.has-filter-panel {
          margin-top: -4px;
        }

        &.not-ready {
          pointer-events: none;
        }
      }

      .top-panel-button {
        position: absolute;
        top: 0;
        right: 0;
        padding: 18px 16px;
        font-size: 14px;
        color: $wk-primary-gray-tint1;
        cursor: pointer;
      }
    }
  }

  .expander {
    position: absolute;
    height: 100%;
    width: 15px;
    right: 0;
    z-index: 1;
    top: 0;
    bottom: 0;
    cursor: ew-resize;
    border-left: 1px solid $wk-primary-gray-tint4;

    .expander-icon {
      position: absolute;
      right: -3px;
      top: 50%;

      transform: translate(0, -50%);
    }
  }

  .collapser {
    position: fixed;
    bottom: 20px;
    left: 10px;
    top: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 20px;
    background-color: $wk-primary-white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    box-shadow: 0 0 5px 0 #aaa;
  }
}
