.current-developments {
  :global(.header_infoBannerVisible ~ .main-container) & :global {
    .wide-header-page__header {
      top: calc(89px + 76px);

      @media (max-width: 767px) {
        top: 172px;
      }

      @media (max-width: 600px) {
        top: 164px;
      }
    }

    .document-search-terms-control_container {
      top: calc(162px + 76px);

      @media (max-width: 600px) {
        top: 238px;
      }
    }
  }

  :global {
    .wide-header-page__main {
      padding-top: 0;
      padding-bottom: 0;
      display: block;
    }

    .wide-header-page__header {
      position: sticky;
      top: 89px;
      z-index: 2;
      padding-bottom: 0;

      @media (max-width: 767px) {
        top: 56px;
      }

      @media (max-width: 600px) {
        top: 48px;
      }
    }
  }

  .current-developments__content-container {
    &:global(.wk-page-container) {
      max-width: 100%;
      padding: 0;

      > :global(.wk-row) {
        display: block;
        margin: 0;
      }
    }
  }

  .tabs-panel {
    padding-top: 14px;
    flex: 0 0 100%;
    width: 100%;
    position: sticky;
    top: 162px;
    z-index: 2;

    @media (max-width: 767px) {
      top: 120px;
    }

    & :global(.tab-button) {
      text-transform: uppercase;
    }

    & :global(.tab-button.tab-button__active) {
      background-color: #fff;
    }
    & > :global(.wk-page-container) {
      @media screen and (min-width: 75rem) {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  .document-actions {
    position: sticky;
    height: 100px;
    top: 209px;

    @media (max-width: 768px) {
      position: relative;
      height: 200px;
      top: 0;
    }
  }

  // overrides
  :global {
    @media (max-width: 768px) {
      .wk-column-12-xs {
        flex-basis: 100%;
        max-width: 100%;
        min-width: 100%;
      }

      .page-content {
        flex-direction: column-reverse;
      }
    }

    .document-content {
      .inner-table-wrapper > table {
        border: 1px solid #dadada;

        th {
          border-bottom: 1px solid #dadada;
        }
      }
    }
  }
}
