@import '../../../../../styles/helpers';

.search-panel-wrapper {
  position: relative;
  width: 100%;

  @include breakpoint(max-width $wk-smartpad-breakpoint) {
    padding: 0 8px;

    :global(.suggestions) {
      width: calc(100% - 16px);
    }
  }

  @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-tablet-breakpoint) {
    padding: 0 16px;

    :global(.suggestions) {
      width: calc(100% - 32px);
    }
  }

  @media (min-width: $wk-tablet-breakpoint) and (max-width: $wk-desktop-breakpoint) {
    padding: 0 16px;

    :global(.suggestions) {
      width: calc(100% - 32px);
    }
  }
}

.search-panel-tabs-row {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include breakpoint(max-width $wk-smartpad-breakpoint) {
    display: block;
  }

  @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-desktop-breakpoint) {
    align-items: flex-start;
    flex-direction: column-reverse;
    padding: 0 16px;
  }
}

.choice-checkbox-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-end;

  & &__checkbox {
    margin-left: 20px;

    @media (max-width: $wk-smartpad-breakpoint) {
      margin-left: 0;
      margin-top: 5px;
    }
  }

  @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-desktop-breakpoint) {
    margin-top: 16px;
    margin-bottom: 21px;
  }

  @media (max-width: $wk-desktop-breakpoint) {
    justify-content: space-between;
  }

  @media (max-width: $wk-smartpad-breakpoint) {
    flex-direction: column;
    margin-left: 8px;
    margin-top: 16px;
  }
}

.search-tabs-wrapper {
  flex: 1;
}
