@mixin wk-reset-html {
    box-sizing: border-box;
    color: $wk-global-color;
    font-size: $wk-global-font-size;
    line-height: $wk-global-line-height;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
}

%wk-reset-html {
    @include wk-reset-html;
}

//
// 1. Remove the gray background on active links in IE 10.
//

@mixin wk-reset-a {
    @include wk-link;

    background-color: transparent; // 1
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

%wk-reset-a {
    @include wk-reset-a;
}

// Forms

//
// 1. Change the font styles in all browsers.
// 2. Remove the margin in Firefox and Safari.
//

@mixin wk-reset-form-element {
    font-family: inherit; // 1
    font-size: 100%; // 1
    line-height: 1.15; // 1
    margin: 0; // 2
}

%wk-reset-form-element {
    @include wk-reset-form-element;
}

@mixin wk-reset-box-sizing($selector) {
    #{$selector},
    #{$selector} *,
    #{$selector} *::before,
    #{$selector} *::after {
        box-sizing: border-box;
    }
}
