@import '../../../../../styles/_helpers.scss';
$search-content-vertical-space-between-blocks: 28px;
$vertical-space-between-header-and-content: 38px;
$vertical-space-between-content-and-footer: 40px;
$grid-gap: 0.5rem;
$space-between-filters-and-content: 56px;
$space-between-filter-and-filter: 36px;
$inner-space: 32px;

$search-content-bottom-margin: 38px;

.search-filters-panel {
  padding-top: $vertical-space-between-header-and-content;
  padding-bottom: $vertical-space-between-content-and-footer;
  padding-right: calc(#{$space-between-filters-and-content} - #{$grid-gap} * 2);

  & > [data-e2e-widget-id] {
    margin-bottom: 36px;
  }

  @include breakpoint(max-width $wk-tablet-breakpoint) {
    display: none;
  }

  @media (min-width: $wk-tablet-breakpoint) and (max-width: $wk-desktop-breakpoint) {
    padding-left: 16px;
  }
}

.search-filters-panel-mobile {
  padding: 10px 22px 26px 24px;

  :global(.ac-float-side-bar-mobile-only__body > [data-e2e-widget-id]):not(:last-child) {
    margin-bottom: 36px;
  }

  @include breakpoint(min-width $wk-tablet-breakpoint) {
    display: none;
  }
}

.search-results-content {
  padding-top: $vertical-space-between-header-and-content;
  padding-bottom: $vertical-space-between-content-and-footer;
}

:global {
  .applied-filters-panel-wrapper,
  .top-hit-box-wrapper {
    margin-bottom: $search-content-vertical-space-between-blocks;
  }

  .top-hit-box-wrapper--accounting {
    .srl-item__green-label,
    .srl-item__title-wrapper {
      margin-bottom: 6px;
    }
  }

  .search-pagination-panel {
    margin-top: $search-content-vertical-space-between-blocks;
  }
  .applied-filters-panel-wrapper {
    padding-left: $inner-space;
  }
}

@media (max-width: $wk-desktop-breakpoint) {
  $inner-space: 20px;
  :global {
    .applied-filters-panel-wrapper {
      padding-left: $inner-space;
    }
  }
}
