.lock-icon {
  color: #353535;
  margin-left: 0;
  vertical-align: top;
  text-decoration: none;

  &.initial {
    margin-right: 5px;
    font-size: 20px;
  }

  &.large {
    margin-right: 8px;
    font-size: 24px;
  }

  &.small {
    margin-right: 5px;
    font-size: 16px;
  }

  &.extra-small {
    margin-right: 5px;
    font-size: 14px;
  }

  &.green {
    color: #425e10;
  }
}
