@import '~@wk/fundamentals';

.limit-tooltip-content {
  color: $wk-primary-black;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  word-wrap: break-word;
}

.favorites-tooltip-content {
  width: 239px;

  &__date {
    color: $wk-primary-gray-tint1;
  }

  &__field {
    padding-top: 16px;
  }

  &__folder-link {
    color: $wk-primary-blue;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
  }

  &__title,
  &__description {
    color: $wk-primary-black;
  }
}
