:global {
  .document-actions {
    display: flex;
    padding: 5px 5px 27px;
    align-items: center;
    position: relative;

    .document-action {
      &:not(:last-child) {
        padding-right: 5px;
      }

      .wk-button {
        padding: 0;
        border-radius: 1.25rem;
        height: 28px;
        width: 28px;

        [class*='wk-icon'] {
          font-size: 18px;
        }

        .wk-icon-list-bullet-dot {
          font-size: 15.6px;
        }
      }
    }
  }
}
