@import '../../../../../styles/variables';

:global {
  .standalone-document-page {
    .document-content .docContent {
      .pending-content {
        padding: 0 32px 32px 32px;

        .pending-content__header {
          margin: 0 -32px;
          width: calc(100% + 32px + 32px);
        }

        img {
          max-width: 100%;
        }

        .inner-table-wrapper :not(li) > p.hP,
        p.hP {
          font-size: 16px;
          line-height: 24px;
          font-weight: 500;
          margin-top: 0;

          & + p.hP:not(:empty) {
            margin-top: 16px;
          }
        }

        p.hP:not(ul li.hP p.hP):not(.inner-table-wrapper p.hP):first-of-type {
          color: $wk-primary-blue-shade2;
          font-weight: 500;
          font-size: 20px;
          line-height: 26px;
          margin-top: 0;
          margin-bottom: 24px;

          b {
            font-weight: inherit;
          }
        }

        p.hP:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .document-content .docContent {
    .pending-content {
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
      border: 1px solid #dadada;
      background: white;
      margin: 32px 0;

      & > table:first-of-type {
        padding: 0 32px 32px 32px;
      }

      &.hidden {
        display: none;
      }

      .pending-content__header {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        padding: 16px;
      }

      .pending-content__close-button {
        width: 14px;
        height: 14px;
        cursor: pointer;
        pointer-events: all;
        user-select: none;
        padding: 0;
        border: none;
        background-color: transparent;
      }

      & > table:first-of-type {
        & > tbody > tr:first-of-type > td:nth-child(2) > p.hP:first-of-type {
          color: #003d61;
          font-weight: 500;
          font-size: 20px;
          line-height: 26px;
        }
      }

      td {
        text-align: start;

        & > p.hP:not(:last-child) {
          margin-bottom: 12px;
          margin-top: 12px;
        }

        & > p.hP:first-of-type {
          margin-top: 0;
        }

        & > *:not(p.hP:first-of-type) {
          font-size: 16px;
          line-height: 24px;
          font-weight: 500;
        }
      }
    }

    .pending-content-pill {
      margin: 24px 0;
      width: 129px;

      &.hidden {
        display: none;
      }
    }

    .pending-content-pill__button {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      color: white;
      background-color: #007ac3;
      overflow: hidden;
      border-radius: 30px;
      padding: 5px 10px;
      cursor: pointer;
      height: 26px;
      width: 129px;
      font-size: 11px;
      line-height: 16px;
      font-weight: 500;
      letter-spacing: 1px;
      text-transform: uppercase;
      margin: 24px 0;
      user-select: none;
      border: none;

      &.hidden {
        display: none;
      }
    }
  }
}
