@import '../../../../styles/variables';

.card {
  padding: 32px;
  min-height: 100px;
  width: 100%;
  border: 1px solid $wk-primary-gray-tint5;
  background: $wk-primary-white;

  .card-title--big {
    color: $wk-primary-gray-shade1;
    font-size: 24px;
    line-height: 30px;
  }

  .sub-title {
    margin-top: 32px;
    font-size: 14px;
    color: $wk-primary-gray-shade1;
    margin-bottom: 18px;
  }

  .card-body {
    margin-top: -24px;
  }

  .card-title {
    margin-top: 24px;
  }

  &:not(:last-child) {
    border-bottom: none;
  }
}

.items-list {
  list-style: none;
  padding-left: 0;

  :global(.widget-item) {
    display: flex;
    margin-top: 24px;

    &.bottom :global(.primary-title) {
      font-size: 14px;
      font-weight: initial;
    }

    &.horizontal {
      display: inline-block;
      box-sizing: inherit;
      margin-top: 0;

      // hard fix to remove last bullet in a row
      &:nth-last-child(3) {
        :global(.primary-title) {
          & > a {
            &::after {
              display: none;
            }
          }
        }
      }

      :global(.primary-title) {
        & > a {
          font-size: 14px;
          font-weight: 400;
          color: $wk-primary-blue;
          position: relative;
          padding-top: 8px;
          padding-right: 14px;
          line-height: 18px;
          white-space: normal;
          word-break: break-word;
          overflow-wrap: break-word;

          &::after {
            content: '•';
            pointer-events: none;
            color: $wk-primary-blue;
            width: 2px;
            padding-left: 8px;
            padding-top: 5px;
            display: inline-block;
          }

          @media (max-width: 767px) {
            &::after {
              padding-top: 3px;
            }
          }
        }
      }
    }
  }

  .item-icon {
    font-size: 24px;
    color: $wk-primary-blue-shade1;
    margin-left: 0;
    margin-right: 8px;
  }

  .secondary-title {
    margin-top: 8px;
  }
}

.group-list-item {
  margin-top: 24px;

  .item-icon {
    color: $wk-primary-gray-shade1;
  }

  .items-list {
    margin-left: 32px;

    .group-item {
      margin-top: 8px;

      .group-item-description {
        margin-top: 6px;
      }
    }
  }
}

// flat type of the Card
// (like "Latest CCH Tax Briefing" or "PwC Worldwide Tax Summaries")
// might be separated as an own component in future
:global(.flat-type) {
  :global(.primary-title) {
    font-size: 16px;
    line-height: 20px;
  }

  [class*='wk-icon-'] {
    line-height: 20px;
  }

  :global(.widget-item) {
    margin-top: 16px;
  }
}
