@import '../../../../../../styles/variables';

.tools-tab {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;
  margin-top: 25px;

  :global {
    .tools-list__item {
      position: relative;
      flex: 0 0 228px;
      width: 228px;
      max-width: 100%;
      height: 118px;
      background: $wk-primary-gray-tint6;
      border: 1px solid $wk-primary-gray-tint4;
      box-shadow: 0 0 6px rgba(0, 0, 0, 0.14);
      padding: 14px;
      margin: 5px;

      a {
        position: relative;
        font-family: 'Fira Sans', sans-serif;
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: $wk-primary-blue-shade1;
      }

      [data-e2e='cg-icon'] {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 86px;
        height: 68.8px;

        > svg {
          width: 100%;
          height: 100%;
          color: $wk-primary-gray-tint5;
        }
      }
    }
  }
}
