@import '../../../../../styles/helpers';

.search-results-tabs-panel {
  @include breakpoint(max-width $wk-smartpad-breakpoint) {
    width: calc(100vw - 16px);
  }

  & [class*='cg-tabs-ctx'] [class*='cg-tabs'] {
    margin: 0;

    @include breakpoint(max-width $wk-smartpad-breakpoint) {
      padding-left: 8px;
    }

    :global(.cg-tab-headers .cg-tab:first-child .cg-tab-inner-content) {
      padding-left: 0;
    }
  }
}
