@import '../../../../filters-common-styles';

.complex-filter-truncated {
  .complex-filter-truncated__applied-filter-list {
    margin: 0;
  }

  .complex-filter__applied-filter + .complex-filter__applied-filter {
    margin-top: $vertical-space-between-filter-items;
  }

  .complex-filter__applied-filter:last-of-type {
    margin-bottom: $vertical-space-between-filter-items-and-more-button;
  }

  .complex-filter__applied-filter {
    @extend %filter-item;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: fit-content;
  }

  .complex-filter__filter-wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    gap: 5px 0;
  }

  .complex-filter-truncated__select-button {
    @extend %filter-button-show-more;
  }
}
