.horizontal-sections-card {
  padding: 32px;
  width: 100%;
  border: 1px solid #ededed;
  background: #fff;

  .horizontal-sections-card__header {
    text-align: left;
    margin-bottom: 16px;
  }

  .link-section {
    &:last-child {
      .link-section__title {
        margin-bottom: 0;
      }
    }
    &__title {
      color: #005b92;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      font-weight: 500;

      &:hover {
        color: #003d61;
      }
    }

    &.link-section__contains-list {
      margin-bottom: 10px;

      .link-section__title {
        margin-bottom: 0px;
      }
    }
  }

  .text-section {
    &__title {
      font-size: 14px;
      color: #353535;
    }
  }

  .link-section,
  .text-section {
    .section__list-item {
      display: inline-block;
      margin: 0;

      .section__list-item-title {
        font-size: 14px;
        font-weight: 400;
        color: #007ac3;
        position: relative;
        padding-top: 8px;
        padding-right: 14px;
        line-height: 18px;
        white-space: normal;
        word-break: break-word;
        overflow-wrap: break-word;
      }
    }
  }
  .section__list-item:not(:last-child) .section__list-item-title {
    &::after {
      content: '•';
      pointer-events: none;
      color: #007ac3;
      width: 2px;
      padding-left: 8px;
      padding-top: 5px;
      display: inline-block;
    }
  }
}
