@import '../../../../../styles/variables';

%arm-content-body {
  h2,
  h3 {
    margin-top: 9px;
    margin-bottom: 15px;
  }

  // paragraphs
  p.hP + p.hP:not(:empty),
  .indented-level :not(li) > p.hP:not(:empty) {
    margin-top: 20px;
  }

  .indented-level > table td[style*='padding-right:8px'] {
    padding-right: 0 !important;
  }

  // lists
  p.hP + ul.bull,
  p.hP + ul.none {
    margin-top: 24px;
  }

  .indented-level > .indented-level {
    ul.bull + p.hP:last-of-type {
      margin-top: 0;
    }
  }

  ul {
    &.bull {
      padding-left: 24px;
      margin-bottom: 29px;
      margin-left: 20px;

      li {
        list-style-type: disc;
      }

      & > .hP:not(:empty) {
        margin-bottom: 10px;
        margin-top: 0;

        &:first-of-type {
          padding-top: 0;
        }

        .hP {
          padding-top: 0;
        }
      }
    }

    &.none {
      padding-left: 20px;
      margin-top: 24px;

      > li {
        margin-top: 10px;

        &:last-child {
          margin-bottom: 29px;
        }
      }
    }
  }

  // lists represented by a table (ARMAC-1239)
  .indented-level table {
    &:has(td.ordered-list) {
      margin-top: 24px;
      margin-bottom: 29px;
    }

    & td.ordered-list + td {
      padding-bottom: 10px;

      & p.hP:first-child {
        margin-top: 0;
      }
    }
  }

  // tables
  .outer-table-wrapper {
    padding: 36px 0;

    table {
      border-spacing: 1px;

      td .hP {
        padding: 11px 16px;
      }

      td .hP:empty {
        display: none;
      }
    }
  }

  // misc
  [style='{display:none}'] {
    display: none;
  }

  @media (max-width: 768px) {
    padding: 0 0.5rem;
  }

  .docCopyrightInfo {
    margin-top: 36px;
  }

  .noteContainer.auditor-note {
    margin: 28px 0;
    border: 1px solid $wk-primary-gray-tint4;
    padding: 24px 0;

    * {
      &:empty,
      &:blank {
        display: none;
      }
    }

    & > * {
      padding: 0 16px;
      margin-top: 0;

      &:not(:empty) + *:empty + *:not(:empty),
      &:not(:empty) + *:not(:empty) {
        margin-top: 24px;
      }
    }

    & > ul,
    & > ol {
      padding: 0 0 0 42px;
    }

    .note {
      font-size: 18px;
      color: #0668a9;

      &.display-none {
        + * {
          margin-top: 0;
        }
      }

      del {
        text-decoration: line-through;
        color: #000000;
      }
    }

    .hP.noteBody {
      del {
        display: block;
        font-size: 48px;
        line-height: 1.7;
        text-decoration: line-through;
      }
    }
  }

  // attachments links
  p.hP + .graphic {
    margin-top: 20px;
  }

  .graphic {
    + p:not([class]) {
      margin-top: 36px;
    }
  }

  a[data-attachment-format] {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    padding: 11px 16px 9px;
    font-size: 14px;
    line-height: 18px;
    color: $wk-primary-blue-shade1;
    border: 1px solid $wk-primary-blue-shade1;

    &:before {
      content: '';
      position: relative;
      top: -1px;
      display: inline-block;
      width: 16px;
      min-width: 16px;
      height: 16px;
      min-height: 16px;
      margin-right: 8px;
      background-position: center;
      background-size: contain;
    }
  }
  a[data-attachment-format^='xls'],
  a[data-attachment-format^='xlsx'] {
    &:before {
      background-image: url(/app-react/src/assets/file-ext/xls-outline.svg);
    }
  }

  a[data-attachment-format^='doc'],
  a[data-attachment-format^='docx'] {
    &:before {
      background-image: url(/app-react/src/assets/file-ext/doc-outline.svg);
    }
  }
  a[data-attachment-format^='pdf'] {
    &:before {
      background-image: url(/app-react/src/assets/file-ext/pdf.svg);
    }

    + iframe {
      margin-top: 20px;
    }
  }
  a[data-attachment-format^='zip'] {
    &:before {
      background-image: url(/app-react/src/assets/file-ext/zip-outline.svg);
    }
  }

  table {
    a[data-attachment-format] {
      border-color: transparent;
    }

    a[data-attachment-format^='pdf'][data-css-class^='inline-attachment'] {
      border: 1px solid $wk-primary-blue-shade1;
    }

    a[data-attachment-format^='xls'],
    a[data-attachment-format^='xlsx'] {
      &:before {
        background-image: url(/app-react/src/assets/file-ext/xls.svg);
      }
    }
    a[data-attachment-format^='doc'],
    a[data-attachment-format^='docx'] {
      &:before {
        background-image: url(/app-react/src/assets/file-ext/doc.svg);
      }
    }
  }
}

:global {
  span.annotation-dropdown-menu-item {
    color: $wk-primary-blue-shade1;
  }

  .document-content .docContent {
    color: $csn-gray-shade-1;
    .docDisplay.docTitle {
      padding-bottom: 15px;

      + .baselineRule {
        margin-top: 10px;
      }
    }

    .osa-doc-annotation-fragment {
      background-color: #ffff00;
      cursor: pointer;
    }

    .osa-doc-annotation.noteIcon {
      width: 40px !important;
      height: 40px;
      background-color: $wk-primary-blue-tint6;
      color: $wk-primary-blue;
      display: inline;
      left: -50px;
      position: absolute;
      font-size: 15px;
      text-decoration: none;
      pointer-events: auto;

      @media (max-width: 768px) {
        & {
          left: calc(-50px + 0.5rem);
        }
      }

      &:hover {
        background-color: $wk-primary-blue-tint5;
        color: $wk-primary-gray-shade1;
        cursor: pointer;
        text-decoration: none;
      }

      &:before {
        position: absolute;
        top: 50%;
        margin: -8.5px;
      }
    }

    .osa-rsi-citation *,
    .osa-document-attachment *,
    .osa-doc-anchor-link *,
    .link * {
      pointer-events: none;
    }

    .osa-rsi-citation {
      color: $wk-primary-blue;
    }

    .baselineRule {
      border-top: 1px solid #dadada;
      margin: 16px 0 25px;
    }

    @extend %arm-content-body;
  }
}
