@import "../../../../../../styles/variables";

.checkbox-list {
  display: flex;
  flex-direction: column;
  max-height: 100%;

  .selectAll {
    &.selectAll--sticky {
      position: sticky;
      top: 0;
      z-index: 1;
      background-color: $wk-primary-white;
    }
  }

  & &__list {
    display: flex;
    flex-direction: column;

    overflow: auto;
  }

  & &__checkbox-wrapper {
    display: flex;
    align-items: center;

    margin-bottom: 6px;
  }

  & &__icon {
    cursor: pointer;
    color: #e5202e;
    margin-left: 11px;
    padding: 0;
    border: none;
    background-color: transparent;

    &:disabled {
      cursor: default;
      opacity: 0.5;
    }
  }
}
