@import './../color/color.functions';
@import './../typography/typography.variables';
@import './helpers.variables';
@import './variables';

// Image

.wk-img-fluid {
    height: auto;
    max-width: 100%;
}

// Display

.wk-shown {
    display: block;
}

.wk-hidden {
    display: none;
}

// Alignment

.wk-is-centered {
    text-align: center;
}

// Clearfix

.wk-clearfix {
    @include wk-clearfix;
}

// List

.wk-unstyled-list {
    @include wk-unstyle-list;
}

// Accessibility

.wk-sr-only {
    @include wk-sr-only;
}

.wk-sr-only-focusable {
    @include wk-sr-only-focusable;
}

// Flexbox

.wk-justify-space-between {
    justify-content: space-between;
}

// Links

.wk-link-muted {
    @include wk-link($wk-link-muted-color, $wk-link-muted-hover-color);

    &.wk-is-active {
        color: $wk-primary-blue-shade1;
        font-weight: map-get($wk-font-weight, medium);

        &:hover {
            text-decoration: none;
        }
    }
}

.wk-link-decorated {
    @include wk-link($wk-link-decorated-color, $wk-link-decorated-hover-color);

    text-decoration: underline;

    &:hover {
        text-decoration: none;
    }

    &:visited {
        color: $wk-link-decorated-visited-color;
    }
}

.wk-link-micro {
    @include wk-link;

    font-size: $wk-size-1;

    &:hover {
        text-decoration: underline;
    }
}

// Font Weights

.wk-weight-500 {
    font-weight: map-get($wk-font-weight, medium);
}

.wk-weight-400 {
    font-weight: map-get($wk-font-weight, regular);
}

.wk-weight-300 {
    font-weight: map-get($wk-font-weight, light);
}

// Font Sizes

.wk-size-11 {
    font-size: $wk-size-11; // 4.75rem
}

.wk-size-10 {
    font-size: $wk-size-10; // 3rem
}

.wk-size-9 {
    font-size: $wk-size-9; // 2.25rem
}

.wk-size-8 {
    font-size: $wk-size-8; // 2rem
}

.wk-size-7 {
    font-size: $wk-size-7; // 1.75rem
}

.wk-size-6 {
    font-size: $wk-size-6; // 1.5rem
}

.wk-size-5 {
    font-size: $wk-size-5; // 1.25rem
}

.wk-size-4 {
    font-size: $wk-size-4; // 1.125rem
}

.wk-size-3 {
    font-size: $wk-size-3; // 1rem
}

.wk-size-2 {
    font-size: $wk-size-2; // 0.875rem
}

.wk-size-1 {
    font-size: $wk-size-1; // 0.75rem
}

// Text Colors

.wk-text-danger {
    color: $wk-primary-red;
}

.wk-text-success {
    color: $wk-primary-green-shade2;
}

.wk-text-muted {
    color: $wk-primary-gray;
}

.wk-text-muted-light {
    color: $wk-primary-gray-tint1;
}

.wk-text-inverse {
    color: $wk-primary-white;

    &:link,
    &:hover,
    &:visited {
        color: $wk-primary-white;
    }
}

// Box Shadow

.wk-box-shadow-raised {
    box-shadow: $wk-box-shadow-raised;
}

.wk-box-shadow-overlay {
    box-shadow: $wk-box-shadow-overlay;
}

.wk-box-shadow-popout {
    box-shadow: $wk-box-shadow-popout;
}

// Margins

.wk-margins {
    * + * {
        margin-top: 1rem; // Establish vertical rhythm, https://alistapart.com/article/axiomatic-css-and-lobotomized-owls/
    }

    * + {
        blockquote,
        figure,
        table {
            margin-bottom: wk-rem(32px);
            margin-top: wk-rem(32px);
        }
    }

    dd,
    br,
    th,
    td,
    figcaption {
        margin-top: 0; // Remove top margins for select flow elements
    }

    * + h1,
    * + h2,
    * + h3,
    * + h4,
    * + h5,
    * + h6 {
        margin-top: wk-rem(32px);
    }
}

.wk-margins-small * + * {
    margin-top: wk-rem(8px);
}

.wk-margins-off > * {
    margin-top: 0;
}

// Color helpers
// Generation of color helpers e.g. `.wk-primary-blue-tint-1`
@include generateColorClasses($wk-primary-colors, 'wk-primary');
@include generateColorClasses($wk-secondary-colors, 'wk-secondary');
@include generateColorClasses($wk-highlight-colors, 'wk-highlight');

// Background-color helpers
// Generation of background-color helpers e.g. `.wk-primary-blue-tint-1`
@include generateBackgroundColorClasses($wk-primary-colors, 'wk-bg-primary');
@include generateBackgroundColorClasses(
    $wk-secondary-colors,
    'wk-bg-secondary'
);
@include generateBackgroundColorClasses(
    $wk-highlight-colors,
    'wk-bg-highlight'
);

// Scroll fade helpers

.wk-scroll-fade-top::before,
.wk-scroll-fade-bottom::after {
    content: '';
    height: $wk-scroll-fade-height;
    display: block;
}

.wk-scroll-fade-top::before {
    background: $wk-scroll-fade-top-background;
}

.wk-scroll-fade-bottom::after {
    background: $wk-scroll-fade-bottom-background;
}

// Overflow hidden

.wk-is-overflow-hidden {
    overflow: hidden;
}
