.sidebar.wk-smoke-break-opened {
  .wk-smoke-break-right.slide-in {
    animation: rightSlideIn 0.2s ease-in-out;
  }

  .wk-smoke-break-right.slide-out {
    animation: rightSlideOut 0.2s ease-in-out;
    animation-fill-mode: forwards;
  }

  .wk-smoke-break-left.slide-in {
    animation: leftSlideIn 0.2s ease-in-out;
  }

  .wk-smoke-break-left.slide-out {
    animation: leftSlideOut 0.2s ease-in-out;
    animation-fill-mode: forwards;
  }
}

@keyframes rightSlideIn {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes rightSlideOut {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes leftSlideIn {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes leftSlideOut {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
