.save-as-formats-list {
  display: flex;
  flex-direction: column;

  [data-e2e='cg-button-field'] {
    display: flex;

    .save-as-button {
      border: none;
      font-size: 14px;
      font-weight: 400;
      position: relative;
      height: 40px;
      width: 191px;
      margin: 16px auto 0;
      line-height: 22px;

      [data-e2e='cg-icon'] {
        position: absolute;
        left: 16px;
        line-height: 25px;
      }
    }
  }
}
