@import '../../../../filters-common-styles';

.complex-filter__grouped-item {
  .complex-filter__grouped-item-title {
    @extend %filter-item-label;
    cursor: pointer;

    &_not-multi-select {
      margin-bottom: 12px;
    }
  }

  .complex-filter__grouped-item-title__icon {
    font-size: 12px;
    margin-right: 8px;
    padding: 0 1px;
  }

  .complex-filter__grouped-item__items-wrapper {
    margin-left: 14px;
  }

  .complex-filter__grouped-item__items {
    @extend %filter-flex-wrapper-with-vertical-gap;
  }

  .complex-filter__grouped-item__select-button {
    padding: 11px 0;
  }

  &_not-expandable {
    margin-bottom: 14px;
    margin-left: 14px;

    .complex-filter__grouped-item-title {
      font-weight: 500;
      cursor: default;
    }

    .complex-filter__grouped-item__items-wrapper {
      margin-left: 0;
    }

    .complex-filter__grouped-item__items {
      gap: 10px 0;
    }
  }
}
