.manage-synonyms-term-modal {
  [class^='cg-modal-body-'] {
    padding: 12px 32px 0 32px;
  }
}

.manage-synonyms-term-modal-body {
  & &__error {
    position: relative;
    font-size: 12px;
    color: #e5202e;
  }

  & &__input {
    margin-bottom: 8px;
  }

  & &__textarea {
    margin-bottom: 20px;
  }
}

.manage-synonyms-term-modal-footer {
  display: flex;
  justify-content: flex-end;

  & &__button {
    margin-right: 16px;
  }
}
