@import '~@wk/fundamentals';
@import '../../../../filters-common-styles';

.full-complex-filter {
  @extend %styles-for-checkbox-in-filters;

  .full-complex-filter__filter-wrapper {
    @extend %filter-flex-wrapper-with-vertical-gap;
    max-width: 300px;
    flex-direction: column;

    @media (min-width: $wk-smartpad-breakpoint) and (max-width: $wk-tablet-breakpoint) {
      max-width: 100%;
      margin-right: 24px;
    }
  }
}
