@import '~@wk/fundamentals';

.select {
  position: relative;

  &__label {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: $wk-primary-gray-shade1;
  }

  &__button {
    position: relative;
    display: flex;
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    background: $wk-primary-white;
    border: 1px solid $wk-primary-gray-tint1;
    padding: 10px 48px 10px 16px;
    text-align: left;
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;

    &:hover,
    &:focus {
      outline: none;
      border: 2px solid $wk-primary-black;
    }

    &::after {
      content: '';
      position: absolute;
      top: 33%;
      right: 20px;
      width: 11px;
      height: 11px;
      border-color: $wk-primary-gray-shade1;
      border-bottom: 2px solid;
      border-right: 2px solid;
      transform: rotate(45deg);
      pointer-events: none;
    }
  }

  &__options {
    position: absolute;
    display: block;
    padding: 4px 0;
    margin: 0;
    background: $wk-primary-white;
    border: 1px solid $wk-primary-gray-tint3;
    box-shadow: 4px 14px 19px rgba(0, 0, 0, 0.13);
    overflow-x: auto;
    width: 100%;
    max-height: 168px;
    z-index: 1;
  }

  &__options_hidden {
    display: none;
  }
}
