@import '~@wk/fundamentals';

$srl-item-inner-indent: 32px;
$search-content-vertical-indent: 28px;
$srl-item-border-color: #bfbfbf;
$srl-item-top-results-text-color: #940c72;
$srl-item-top-results-visited-text-color: #ca86b9;
$srl-item-top-green-label-color: #425e10;

.srl-item {
  padding-left: $srl-item-inner-indent;
  padding-right: $srl-item-inner-indent;
}

.srl-item_top-hit {
  padding: $srl-item-inner-indent;
  border: 1px solid $srl-item-border-color;
}

.srl-item > * + * {
  margin-top: 6px;
}

@media (max-width: $wk-desktop-breakpoint) {
  $srl-item-inner-indent: 20px;
  .srl-item {
    padding-left: $srl-item-inner-indent;
    padding-right: $srl-item-inner-indent;
  }

  .srl-item_top-hit {
    padding: $srl-item-inner-indent;
  }

  .srl-item > .srl-item__title-wrapper + * {
    margin-top: 16px;
  }
}

.srl-item__header-controls {
  position: absolute;
  right: -8px; //equal to right padding of save to fav button
  top: -8px; //equal to top padding of save to fav button
}

li.srl__item-wrapper + li.srl__item-wrapper {
  margin-top: $search-content-vertical-indent;
}

.srl-item__header {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.srl-item__title-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: $srl-item-inner-indent;
}

.srl-item__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  .srl-item__external-icon {
    vertical-align: baseline;
    font-size: 18px;
  }

  &:hover .srl-item__external-icon {
    text-decoration: none;
  }

  &.visited {
    color: $srl-item-top-results-visited-text-color;
  }
}

.srl-item__green-label {
  color: $srl-item-top-green-label-color;
  padding-right: 28px;

  &-icon {
    width: 20px;
    height: 15px;
    margin-left: 5px;
  }
}

.srl-item__summary,
.srl-item__header,
.srl-item__green-label,
.srl-item__metadata-score,
.srl__metadata-score-button {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}

.srl-item__title-wrapper,
.srl-item__summary {
  overflow-wrap: anywhere;
  word-break: break-word;
}

.srl-item__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}

.srl-item__summary wk-eip-hl {
  font-weight: 500;
}

.srl-item__top-result {
  color: $srl-item-top-results-text-color;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  margin-right: 16px;
}
