@import '../../../styles/helpers';

.home-search-widget {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;

  .home-search-widget__title {
    margin-bottom: 16px;
    font-size: 28px;
    line-height: 36px;
  }

  & &__checkbox-wrapper {
    flex-direction: row;
    justify-content: space-between;

    @media (max-width: $wk-smartpad-breakpoint) {
      flex-direction: column;
    }
  }

  @media (max-width: 37.4rem) {
    .home-search-widget__title {
      font-size: 27px;
    }
  }

  :global(.search-input-wrapper) {
    position: relative;
    margin-bottom: 1rem;
  }
}
