@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';
@import '../ac-banner/ac-banner.scss';

$wk-max-laptop-breakpoint: $wk-laptop-breakpoint + wk-rem(1px);

//search-bar with radiobuttons on arm
[class*='banner_with-app-switch'][class*='banner_with-search-bar'] {
  .combinable-secondary-search-input-wrapper.secondary-search-input-container_focused {
    @include breakpoint(max-width $wk-laptop-breakpoint) {
      min-width: calc(100% + 150px);
      transform: translateX(-150px);
    }

    @include breakpoint(min-width $wk-max-laptop-breakpoint) {
      min-width: 45vw;
    }

    @include breakpoint(min-width $wk-hd-breakpoint) {
      min-width: 600px;
    }
  }
}

.secondary-search-input-container {
  position: relative;
  transition: min-width 0.3s ease-in-out, transform 0.3s ease-in-out;
  z-index: 1;
  min-width: 100%;
}

div.secondary-search-input-container.secondary-search-input-container_focused {
  :global(input[type='search'].combinable-search-input) {
    padding-right: 300px;
  }
}

.scoped-search {
  :global(.search-input.wk-field-secondary .wk-field-input.search-input__input) {
    font-size: 16px;
  }
}

.combinable-secondary-search-input-wrapper {
  :global [class*='cg-field-'] {
    flex-wrap: nowrap;
    margin-bottom: 0;
    padding: 0;
  }
}
