@import '../../../styles/variables';

.edward-jones {
  & &__content-wrapper {
    max-width: 1144px;

    margin: auto;
    padding: 48px 0 57px 0;

    @media (max-width: 1200px) {
      padding: 48px 16px 57px 16px;
    }
  }

  & &__main-content {
    display: flex;
    gap: 24px;

    @media (max-width: 1024px) {
      flex-direction: column;
    }
  }

  & &__text {
    color: $wk-primary-black;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 16px;
    line-height: 26px;
  }

  & &__note {
    color: $wk-primary-gray-shade2;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 20px;
    font-weight: 300;
    line-height: 24px;
  }

  & &_margin-bottom24px {
    margin-bottom: 24px;
  }

  & &_margin-bottom16px {
    margin-bottom: 16px;
  }

  & &__box {
    width: 448px;
    height: 168px;
    flex-shrink: 0;

    background: $wk-primary-gray-tint6;
  }

  & &__box-wrapper {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  & &__list {
    margin-left: 24px;
    list-style-type: disc;
  }

  & &__list-item {
    color: $wk-primary-gray-shade2;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 16px;
    line-height: 24px;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  & &__list-title {
    color: $wk-primary-black;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 24px;
    font-weight: 300;
    line-height: 26px;

    margin-bottom: 24px;
  }
}
