@import '../../../styles/variables';

.header {
  background: $wk-primary-gray-tint5;
  border-bottom: 1px solid $wk-primary-gray-tint4;
  padding: 15px 0;
}

.wrapper {
  padding: 0 3rem;

  @media (max-width: 768px) {
    padding: 0;
  }
}

.flat-list-wrapper {
  flex: 0 1 60%;
}

.promo-box-wrapper {
  flex: 0 1 30%;
  margin-top: 16px;
  border: 1px solid $wk-primary-gray-tint4;
  padding: 1rem;
  margin-bottom: 15px;

  .promo-box-title {
    font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .flat-list-wrapper,
  .aside-flat-list-wrapper {
    flex: 0 0 100%;
  }
}
