.news-static-card-as-button .wk-button {
  width: 100%;
  padding: 20px 32px;
  display: flex;
  align-items: flex-start;

  &:not([disabled]):focus:not(:focus-visible) {
    outline: 2px inset #000;
  }
}

.news-static-card-as-button {
  [class*='icon'] + .news-static-card-as-button__text {
    margin-left: 8px;
  }
}

.news-static-card-as-button__text {
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  text-align: left;
}
