@import '../../../../../styles/variables';
@import '../../../../../styles/helpers';

$overage-button-width: wk-rem(116px);
$admin-button-width: 186px;
$switcher-button-width: 134px;
$burger-button-width: 40px;
$gap-width-mobile: 32px;
$gap-width-desktop: 20px;

$wk-min-tablet-breakpoint: $wk-tablet-breakpoint - wk-rem(1px);
$breakpointWithOneButtons: $wk-max-desktop-breakpoint; //because of wk-row breakpoints
$breakpointWithTwoButtons: $breakpointWithOneButtons + $overage-button-width;
$breakpointWithTreeButtons: $breakpointWithTwoButtons + $overage-button-width;

%banner {
  background-color: $wk-primary-white;
  border-bottom: 1px solid $wk-primary-gray-tint4;
}

%banner-container {
  @include wk-page-container();
  border: none;
  //bellow is imitation of wk-col-.. paddings
  & > *:first-child {
    margin-left: 0.5rem;
  }
  & > *:last-child {
    margin-right: 0.5rem;
  }
}

.banner {
  @extend %banner;

  :global {
    [data-e2e="cg-button-group"] .cg-button-group-items {
      .cg-button-group-item.cg-has-margin-left-default {
        margin-left: 0;
      }
    }
  }
}

.banner_sticky {
  position: sticky;
}

.banner__logo-container {
  display: flex;
  align-items: center;

  > * + * {
    margin-left: 1rem;

    @include breakpoint(min-width $wk-max-desktop-breakpoint) {
      margin-left: 2rem;
    }
  }
}

.banner__logo-container + * {
  margin-left: 1rem;

  @include breakpoint(min-width $wk-max-desktop-breakpoint) {
    margin-left: 2rem;
  }
}

.banner__app-switcher {
  :global {
    .wk-tooltip-interactive.wk-tooltip-open.wk-position-bottom,
    .wk-tooltip-interactive.wk-tooltip-open.wk-position-bottom-left,
    .wk-tooltip-interactive.wk-tooltip-open.wk-position-bottom-right {
      transform: none;
    }
  }
}

.banner :global([class^='cg-banner'] [class^='cg-banner-container']) {
  //in diff case of write this rule be compilation error
  @extend %banner-container;
}

.banner {
  :global {
    .cg-button-group-item.cg-has-margin-left-default {
      margin-left: 0;
    }

    [class^='cg-banner-content'] {
      [class^='cg-banner-left-content'],
      [class^='cg-banner-search-container'] {
        display: none;
      }
    }

    [class*='cg-banner-right-content'] {
      position: relative;
      width: 100%;
      gap: 0;
    }

    li[class*='cg-dropdown-menu-item'] {
      //styles for action-button in dropdown
      position: relative;
      cursor: pointer;

      &:hover {
        background-color: $wk-primary-blue-tint5;
        color: $wk-primary-gray-shade1;
        text-decoration: none;
      }
      @include breakpoint(max-width $wk-smartpad-breakpoint) {
        padding: 8px 16px;
      }
    }

    [class*='cg-button-group'] {
      .cg-button-group-items {
        align-items: center;
        position: relative;
      }
    }

    .cg-dropdown-menu-wrapper {
      //dropdown
      @include breakpoint(min-width $wk-smartpad-breakpoint) {
        width: 355px;
      }

      @include breakpoint(min-width $wk-tablet-breakpoint) {
        width: 224px;
      }
    }

    .cg-dropdown-menu-area {
      //for make suggestion visible
      overflow: visible;
    }

    [data-e2e='cg-dropdown-menu'] {
      //to make different order of items in dropdown
      display: flex;
      flex-direction: column;
    }
  }
}

.banner__secondary-search_in-dropdown {
  padding: 0 16px 10px;
}

.banner__app-switcher_mobile {
  order: 1;
}

.banner__secondary-search {
  width: 100%;
}

// + switch-button - admin
.banner_with-app-switch.banner_with-search-bar:not(.banner_with-admin-button) {
  :global {
    .cg-button-group-item:nth-of-type(2) {
      //secondary-search item
      &:not(.cg-is-hidden) {
        left: 148px;
      }

      visibility: hidden;
      position: absolute;
      min-width: 100%;

      @include breakpoint(min-width $wk-tablet-breakpoint) {
        visibility: visible;
        min-width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
        max-width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
      }

      @include breakpoint(min-width $breakpointWithOneButtons) {
        width: auto;
        min-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width}
        );
        max-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width}
        );
      }

      @include breakpoint(min-width $breakpointWithTwoButtons) {
        min-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width *
            2}
        );
        max-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width *
            2}
        );
      }

      @include breakpoint(min-width $breakpointWithTreeButtons) {
        min-width: calc(100% - #{$switcher-button-width} - #{$overage-button-width * 3});
        max-width: calc(100% - #{$switcher-button-width} - #{$overage-button-width * 3});
      }
    }
  }
}
// + switch-button + admin
.banner_with-admin-button.banner_with-app-switch.banner_with-search-bar {
  :global {
    .cg-button-group-item:nth-of-type(2) {
      //secondary-search item
      &:not(.cg-is-hidden) {
        left: 148px;
      }

      visibility: hidden;
      position: absolute;

      @include breakpoint(min-width $wk-tablet-breakpoint) {
        visibility: visible;
        width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
        max-width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
      }

      @include breakpoint(min-width $breakpointWithOneButtons) {
        width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width}
        );
        max-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width}
        );
      }

      @include breakpoint(min-width $breakpointWithTwoButtons) {
        width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} -
            #{$overage-button-width}
        );
        max-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} -
            #{$overage-button-width}
        );
      }

      @include breakpoint(min-width $breakpointWithTreeButtons) {
        width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} -
            #{$overage-button-width * 2}
        );
        max-width: calc(
          100% - #{$switcher-button-width} - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} -
            #{$overage-button-width * 2}
        );
      }
    }
  }
}
// - switch-button - admin
.banner_with-search-bar:not(.banner_with-app-switch) {
  $gap-width-desktop: 24px;
  :global {
    .cg-button-group-item:nth-of-type(1) {
      display: none;
    }
    .cg-button-group-item:nth-of-type(2) {
      //secondary-search item
      position: absolute;
      left: 0;
      visibility: hidden;
      min-width: 100%;

      @include breakpoint(min-width $wk-tablet-breakpoint) {
        visibility: visible;
        min-width: calc(100% - #{$gap-width-mobile} - #{$burger-button-width});
        max-width: calc(100% - #{$gap-width-mobile} - #{$burger-button-width});
      }

      @include breakpoint(min-width $breakpointWithOneButtons) {
        width: auto;
        min-width: calc(100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width});
        max-width: calc(100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width});
      }

      @include breakpoint(min-width $breakpointWithTwoButtons) {
        min-width: calc(100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width * 2});
        max-width: calc(100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$overage-button-width * 2});
      }

      @include breakpoint(min-width $breakpointWithTreeButtons) {
        min-width: calc(100% - #{$overage-button-width * 3});
        max-width: calc(100% - #{$overage-button-width * 3});
      }
    }
  }
}
// - switch-button + admin
