      }

      @include breakpoint(min-width $breakpointWithTwoButtons) {
        min-width: calc(
          100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} - #{$overage-button-width}
        );
        max-width: calc(
          100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} - #{$overage-button-width}
