      }

      @include breakpoint(min-width $breakpointWithTreeButtons) {
        min-width: calc(
          100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} - #{$overage-button-width * 2}
        );
        max-width: calc(
          100% - #{$gap-width-desktop} - #{$burger-button-width} - #{$admin-button-width} - #{$overage-button-width * 2}
        );
      }
