  }
}

// + switch-button + brand-logo
.banner_with-app-switch.banner_with-search-bar.banner_with-brand-logo {
  :global {
    .cg-button-group-item:nth-of-type(2) {
      @include breakpoint(max-width $breakpointWithOneButtons) {
        visibility: hidden;
        min-width: 100%;
      }
    }
  }
}

.banner_with-app-switch {
  :global {
    .cg-button-group-item:first-of-type {
      //app-switcher item
      position: absolute;
      left: 0;
    }
  }
}

// + focused search bar
.banner__secondary-search--wide-mode.banner_with-app-switch.banner_with-search-bar {
  :global {
    .cg-button-group-item:nth-of-type(2) {
      @include breakpoint(min-width $wk-tablet-breakpoint) {
        visibility: visible;
        width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
        max-width: calc(100% - #{$switcher-button-width} - #{$gap-width-mobile} - #{$burger-button-width});
      }
    }
  }
}

// + focused search bar - switch-button - admin
.banner__secondary-search--wide-mode.banner_with-search-bar:not(.banner_with-app-switch) {
  $gap-width-desktop: 24px;
  :global {
    .cg-button-group-item:nth-of-type(1) {
      display: none;
    }
    .cg-button-group-item:nth-of-type(2) {
      @include breakpoint(min-width $wk-tablet-breakpoint) {
        visibility: visible;
        min-width: calc(100% - #{$gap-width-mobile} - #{$burger-button-width});
        max-width: calc(100% - #{$gap-width-mobile} - #{$burger-button-width});
      }
    }
  }
}
