.account-widget-container {
  margin: 34px 0 24px 24px;

  .account-alerts-container {
    margin-top: 24px;
  }

  .account-widget-subtitle {
    margin-top: 24px;
    word-break: break-word;
  }

  .account-widget__title-note {
    margin-top: 8px;
    word-break: break-word;
  }

  .account-widget-list {
    margin: 27px 0;
  }

  .account-widget-list-item {
    margin: 0;

    :not(:last-child) {
      margin-bottom: 4px;
    }
  }
}
