.page-header {
  padding-top: 15px;
  padding-bottom: 15px;

  :global {
    .first-part {
      font-size: 36px;
      font-weight: 500;
      line-height: 46px;
    }

    .second-part {
      font-size: 24px;
      font-weight: 400;
      line-height: 30px;
      margin-left: 10px;
    }
  }
}

.tenk-lookup-tool-content {
  margin-top: 35px;

  :global {
    .view-options {
      .view-mode-selector {
        margin-top: 30px;
        width: 192px;
      }
    }

    .navigation-link {
      font-size: 18px;
      font-weight: 500;
      text-decoration: underline;
      line-height: 24px;
    }

    .export-actions {
      margin-top: 30px;

      h3 {
        color: #648d18;
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        text-transform: uppercase;
      }

      .section {
        margin-bottom: 29px;

        .section-title {
          margin-bottom: 10px;
        }
      }
    }

    // lists
    .page-outline-list {
      padding-left: 30px;

      .submission-document-item {
        font-size: 20px;
        line-height: 26px;
        font-weight: 500;
        text-decoration: underline;
        margin-bottom: 10px;
      }

      .pages,
      .parts,
      .part-sections {
        margin-left: 60px;
      }

      .document-part-item,
      .document-page-item {
        font-weight: 400;
      }
    }

    .filing-blurbs {
      position: relative;
      padding-right: 288px;

      @media screen and (max-width: 1024px) {
        padding-right: 0;
      }

      .filing-blurbs__words-count {
        position: absolute;
        top: 36px;
        right: 0;
        width: 240px;

        @media screen and (max-width: 1024px) {
          position: static;
          top: 0;
          max-width: 100%;
          margin-top: 24px;
        }
      }

      .blurbs-navigation-links {
        display: flex;
        justify-content: space-between;
      }
    }

    .ac-list.filing-blurbs__excerpts {
      margin-top: 29px;

      .blurb {
        border: 1px solid #dadada;
        padding-top: 29px;
        padding-bottom: 29px;
        padding-left: 16px;

        &.blurb-list-header {
          padding-top: 10px;
          padding-bottom: 10px;
          font-weight: 500;
        }

        .highlight {
          background-color: #fff500;
        }
      }
    }

    .blurb-page-preview {
      .highlight {
        background-color: #fff500;
      }
    }

    // document preview
    $document-preview-offset-top: 56px + 32px + 76px + 35px; // header + breadcrumbs bar + title bar + page content padding top
    $bottom-offset: 10px;
    $xbrl-height: 56px;
    .document-preview {
      iframe {
        height: calc(100vh - #{$document-preview-offset-top} - #{$bottom-offset});
        width: 100%;
      }
    }

    [data-e2e='cg-field'] {
      ~ .document-preview {
        iframe {
          height: calc(100vh - #{$document-preview-offset-top} - #{$bottom-offset} - #{$xbrl-height});
          width: 100%;
        }
      }
    }
  }
}

