// Typography Mixins

///
/// Create a font face rule.
///
/// @param {String} $name - font name
/// @param {String} $path - path to font file
/// @param {String} $weight [null] - font-weight value
/// @param {String} $style [null] - font-style value
///
/// @example scss - Usage
///   @include font-face(Fira Sans, fonts/FiraSans, 200, italic);

@mixin wk-font-face($name, $path, $weight: null, $style: null) {
    @font-face {
        font-family: '#{$name}';
        font-style: $style;
        font-weight: $weight;
        src: url('#{$path}.woff2') format('woff2'),
            url('#{$path}.woff') format('woff');
        font-display: swap;
    }
}

/// Heading
@mixin wk-heading {
    color: $wk-heading-color;
    margin-bottom: $wk-heading-margin-bottom;
    margin-top: 0; // To prevent collapsing margins
}

/// h1 Mixin
@mixin wk-h1 {
    font-size: $wk-h1-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

/// h2 Mixin
@mixin wk-h2 {
    font-size: $wk-h2-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

/// h3 Mixin
@mixin wk-h3 {
    font-size: $wk-h3-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.3;
}

/// h4 Mixin
@mixin wk-h4 {
    font-size: $wk-h4-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.333333333333333;
}

/// h5 Mixin
@mixin wk-h5 {
    font-size: $wk-h5-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

/// h6 Mixin
@mixin wk-h6 {
    font-size: $wk-h6-font-size;
    font-weight: map-get($wk-font-weight, regular);
    line-height: 1.25;
}

/// Eyebrow Mixin
@mixin wk-eyebrow {
    font-size: $wk-size-1;
    font-weight: map-get($wk-font-weight, medium);
    letter-spacing: wk-rem(1px);
    line-height: 1.333333333333333;
    text-transform: uppercase;
}

/// Display 1
@mixin wk-display-1 {
    font-size: $wk-display-1-font-size;
    font-weight: map-get($wk-font-weight, light);
    line-height: 1.263157894736842;
}

/// Display 2
@mixin wk-display-2 {
    font-size: $wk-display-2-font-size;
    font-weight: map-get($wk-font-weight, light);
    line-height: 1.25;
}

/// Display 3
@mixin wk-display-3 {
    font-size: $wk-display-3-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.277777777777778;
}

/// Display 4
@mixin wk-display-4 {
    font-size: $wk-display-4-font-size;
    font-weight: map-get($wk-font-weight, medium);
    line-height: 1.25;
}

/// Blockquote Mixins
@mixin wk-blockquote {
    border-left-color: $wk-blockquote-border-left-color;
    border-left-style: $wk-blockquote-border-left-style;
    border-left-width: $wk-blockquote-border-left-width;
    padding-left: $wk-blockquote-padding-left;
}

@mixin wk-blockquote-footer {
    color: $wk-blockquote-footer-color;
    font-size: $wk-blockquote-footer-font-size;

    &::before {
        content: '\2014 \00A0';
    }
}
