@import '../../../../../styles/variables';

.page-header {
  font-weight: 500;
  font-size: 36px;
  line-height: 46px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.ten-k-lookup-search-page {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;

  & &__spinner-background {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }

  & &__spinner {
    position: sticky;
    top: 50%;
    margin: 100px;
  }

  & &__content {
    padding-left: 34px;
  }

  & &__error-message {
    margin-top: 44px;
  }
}

.ten-k-lookup-search-filters {
  & &__button-container {
    margin-top: 20px;
    margin-bottom: 36px;
  }

  & &__filters-container {
    > *:not(:last-child) {
      margin-bottom: 30px;
    }

    :global {
      [data-e2e='cg-field'].cg-field-secondary .cg-search-field input {
        padding-right: 3rem;
      }

      .complex-filter-truncated__title {
        font-weight: 500;
        font-size: 14px;
        line-height: 1;
        color: $wk-primary-gray-shade1;
        margin-bottom: 15px;
        letter-spacing: unset;
      }
    }
  }

  :global {
    .complex-filter-trancated .complex-filter-trancated__title,
    .flat-list-filter .ac-list__caption {
      color: #353535;
      font-size: 14px;
      line-height: 18px;
      font-weight: 500;
      margin-bottom: 6px;
      letter-spacing: unset;
    }
  }
}

:global {
  .tenk-matrix-filter-wrapper {
    padding: 8px 0 13px 24px;
  }
  .search-lookup-filters__group-title {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: $wk-primary-gray-shade1;
    margin-bottom: 15px;
  }

  [data-e2e-widget-id='geographicalAreaFilterGroup'] {
    [data-e2e='cg-field-body'] > div {
      width: 100%;
    }
  }
}
