@import '../../../../../../../styles/variables';

.search-10k-lookup-sidebar {
  height: 100%;
  display: flex;
  flex-direction: column;

  &__title {
    font-size: 20px;
    color: $wk-primary-gray-shade1;
    margin-bottom: 14px;
  }

  .search-10k-lookup-sidebar__options-filter {
    padding-right: 65px;
    margin-bottom: 30px;
  }

  &.search-10k-lookup-sidebar--10-k-sections {
    .search-10k-lookup-sidebar__title {
      margin-bottom: 46px;
    }

    .search-10k-lookup-sidebar__filter-wrapper {
      :global {
        .complex-filter .complex-filter__filter-wrapper {
          width: 370px;
          max-width: 100%;
        }
        .complex-filter__grouped-item {
          &.complex-filter__grouped-item_not-expandable {
            margin-bottom: 22px;
          }

          .complex-filter__grouped-item-title {
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 13px;
          }

          .complex-filter__grouped-item__items-wrapper {
            margin-left: 0;
            .complex-filter__floating-item {
              overflow: unset;
              &:last-of-type {
                margin-bottom: 0;
              }
            }
            .complex-filter__floating-item__label {
              font-size: 14px;
              line-height: 18;
              .wk-field-choice {
                &:checked {
                  & + .wk-field-choice-text {
                    &:after {
                      width: 4px;
                      height: 4px;
                      border-radius: 50%;
                      border: none;
                      background-color: #fff;
                      top: 10px;
                      left: 8px;
                      transform: translate(-50%, -50%);
                    }
                  }
                }
              }

              .wk-field-choice-text {
                &::before {
                  width: 16px;
                  height: 16px;
                }
              }
            }
          }
        }
      }
    }
  }

  &__filter-wrapper {
    overflow-y: auto;

    & > *:not(:last-child) {
      margin-bottom: 34px;
    }
  }
}
