@import '../../../../../styles/variables';

:global {
  .document-action__view-archive {
    display: inline;
    margin-left: 8px;

    button {
      padding: 0;
    }
  }
}

.modal_title {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

.document-archive-modal-content {
  font-size: 1.2em;
  color: #505050;
  margin-left: -8px;
  padding-bottom: 2.25rem;

  :global {
    p {
      font-size: 1rem;
      line-height: calc(24 / 16);
      margin-top: 1rem;
      color: $wk-primary-gray-shade2;

      + h5.hP {
        &:before {
          content: '';
          display: block;
          width: 100%;
          height: 1px;
          background-color: $wk-primary-gray-tint4;
          margin: 1.125rem 0 1rem;
        }
      }
    }

    h3 {
      color: $wk-primary-black;
      font-size: 1.5rem;
      font-weight: 500;
    }

    .link {
      color: $wk-primary-blue;
      font-weight: 500;
    }

    h5 {
      &.docDisplay {
        font-weight: 600;
        font-size: 1.25rem;
        line-height: 1.625rem;
        color: $wk-primary-gray-shade1;
        margin-top: 1em;
        margin-bottom: 1em;
        display: inline;
      }

      ~ p {
        margin-top: 0.8125rem;
        font-size: 1.25rem;
        line-height: calc(21 / 16);
        color: $wk-primary-gray-shade1;

        .link {
          font-size: 1.25rem;
          margin-right: 5px;
        }
      }

      + p {
        margin-top: 2rem;
      }

      + h5 {
        margin-left: 5px;
      }
    }

    hr {
      margin-top: 18px;
      margin-bottom: 18px;
      border: 0;
      border-top: 1px solid #dadada;
    }
  }
}

.document-archive-modal__footer-spacer {
  min-height: 2.5rem;
}
