.dropdown-menu {
  padding-bottom: 2px;

  :global(.tabbed-view-header__dropdown--compact) & {
    .dropdown-menu__btn {
      padding: 7px 30px 7px 10px;
    }
  }

  .dropdown-menu__btn {
    position: relative;
    background-color: #007ac3;
    border: none;
    border-radius: 5px;
    padding: 7px 32px 7px 12px;
    height: 44px;
    color: white;

    .dropdown-menu__btn-text {
      font-family: 'Fira Sans', sans-serif;
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
    }

    :global(.dropdown-menu__btn-icon) {
      position: absolute;
      top: 10px;
      right: 10px;
    }

    &.dropdown-menu__btn_open {
      background-color: #005b92;

      :global(.dropdown-menu__btn-icon) {
        top: 17px;
      }
    }
  }

  :global {
    [data-e2e="cg-dropdown"] {
      [data-e2e="cg-dropdown-menu-area"] {
        display: flex;
        flex-direction: column;
        overflow-y: hidden;
        width: 449px;

        @media (max-width: 599px) {
          width: 100%;
        }
      }
    }
  }

  &__body {
    flex: 1;
    overflow-y: auto;
    max-height: 450px;
    width: 100%;
    min-width: 330px;

    @media (max-width: 900px) {
      width: calc(50vw - 5px);
    }
    @media (max-width: 599px) {
      width: calc(100vw - 5px);
    }

    &.dropdown-menu__body--no-filter {
      margin-bottom: 55px;
      height: calc(100% - 55px);
    }

    ul, li {
      margin: 0;
    }
  }

  &__active-item {
    font-weight: 500;
  }

  &__footer {
    width: 100%;

    @media (max-width: 900px) {
      width: calc(50vw - 5px);
    }
    @media (max-width: 599px) {
      width: calc(100vw - 5px);
    }
  }

  &__footer-link {
    padding: 16px 16px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
  }

  &__filters {
    border-top: 1px solid #dadada;
    border-bottom: 1px solid #dadada;
    padding: 6px 16px;

    :global(.cg-segmented-control-body) {
      width: 100%;
    }
  }

  .us-icon-wrapper {
    display: inline-block;
    position: relative;
    width: 24px;
    height: 13px;

    .us-icon {
      position: absolute;
      top: -2px;
      left: -5px;
      height: 19px;
      width: 27px;
      scale: 0.65;
    }
  }

  :global {
    .cg-dropdown-menu-wrapper {
      border-radius: 5px;
      min-width: 335px !important;
    }

    .level {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      .level-title {
        flex: 1;
        text-overflow: ellipsis;
        overflow: hidden;

        [data-e2e="cg-icon"] {
          margin-right: 0.5rem;
        }
      }
    }

    @for $i from 1 through 4 {
      .level.level-#{$i} {
        padding-left: calc(24px * #{$i});
      }
    }
  }

  :global(.wk-dropdown) {
    .dropdown-menu__list-toggle {
      padding: 0;
      background-color: transparent;
      border: none;
      cursor: pointer;
      margin-right: 8px;
    }
  }
}
