:global {
  .wk-breadcrumb {
    .wk-breadcrumb-list {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }

    span.wk-breadcrumb-link {
      cursor: default;
    }

    .wk-breadcrumb-link {
      &:hover {
        text-decoration: none;
      }

      [class*='wk-icon'] {
        vertical-align: text-top;

        &::before {
          margin: 0 0.2em;
          color: #005b92;
          font-size: 14px;
        }
      }

      .breadcrumb-title:hover {
        text-decoration: underline;
      }
    }
  }
}
