.sidebar-list {
  margin: 34px 10px 0 26px;

  .ac-list__caption {
    font-size: 18px;
    line-height: 24px;
    margin-left: 2px;
    margin-bottom: 28px;
  }

  .ac-list-item {
    height: 24px;
    margin: 14px 1px;

    [data-e2e-element-type='link'] {
      border: none;
      height: 24px;
    }

    &:not(:last-child) {
      margin: 14px 1px;
    }
  }
}
