// Scroll fade helpers variables

/// Scroll fade height
/// @type Unit
$wk-scroll-fade-height: wk-rem(64px) !default;

/// Scroll fade top background
/// @type Unit
$wk-scroll-fade-top-background: linear-gradient(
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
);

/// Scroll fade bottom background
/// @type Unit
$wk-scroll-fade-bottom-background: linear-gradient(
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1)
);
