@import '../../../../../../styles/variables';

.section-filter {
  & &__wrapper-title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }

  & &__title {
    line-height: 18px;
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    color: $wk-primary-gray-shade1;
  }

  & &__hint {
    margin-left: 10px;
  }

  & &__section {
    margin-bottom: 4px;
    max-width: 100%;
  }

  & &__section-items {
    margin: 6px 0 4px;
  }
}
