.search-results-table {
  .search-results-table__header {
    min-height: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    gap: 5px 10px;

    .search-results-table__header__title {
      font-family: 'Fira Sans', sans-serif;
      font-size: 24px;
      font-weight: 500;
      line-height: 30px;
    }
  }

  .search-results-table__table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 24px;

    .search-results-table__table__th-label-icon {
      // display: flex;
      // align-items: center;
      cursor: pointer;

      .search-results-table__table__th-icon {
        margin-left: 11px;
        line-height: 18px;
      }

      &:hover {
        color: #005b92;
      }
    }

    .search-results-table__table__th,
    .search-results-table__table__td {
      padding: 10px 10px 10px 17px;
      border: 1px solid #dddfe1;
      font-family: 'Fira Sans', sans-serif;
      font-size: 14px;
      line-height: 18px;
      text-align: left;

      &.search-results-table__table__td--compact {
        padding: 4px 10px 4px 17px;
      }
    }

    .search-results-table__table__thead {
      .search-results-table__table__th {
        font-weight: 500;
      }
    }
  }
  .search-results-table__record-author {
    display: block;
    font-size: 12px;
    font-weight: 300;
  }
}

.no-results {
  padding-top: 46px;
}

.no-results__title {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 8px;
}

.no-results__text {
  line-height: 20px;
}
