@import './../color/color.functions';
@import '../helpers/variables';

// Typography Variables

/// Default path to font(.woff, .ttf) files
///
/// @type String
$wk-font-path: './../assets/' !default;

/// Default font family stack
///
/// @type List
$wk-global-font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto',
    Arial, sans-serif !default;

/// Monospace font family stack
///
/// @type List
$wk-monospace-font-family: SFMono-Regular, Menlo, Monaco, Consolas,
    'Liberation Mono', 'Courier New', monospace !default;

/// Font weights
///
/// @type Map
///
/// @prop {Number} wk-font-weight.extralight [200] - extra light font weight
/// @prop {Number} wk-font-weight.light [300] - light font weight
/// @prop {Number} wk-font-weight.regular [400] - regular font weight
/// @prop {Number} wk-font-weight.bold [700] - bold font weight
///
$wk-font-weight: (
    extralight: 200,
    light: 300,
    regular: 400,
    medium: 500,
    bold: 700
) !default;

// Variables for Headings
// ============================================================================

/// Heading Color
///
/// @type Unit
$wk-heading-color: $wk-primary-gray-shade1 !default;

// Heading Margin Bottom
///
/// @type Unit
$wk-heading-margin-bottom: $wk-size-3 !default;

/// h1 Font Size
///
/// @type Unit
$wk-h1-font-size: $wk-size-7 !default;

/// h2 Font Size
///
/// @type Unit
$wk-h2-font-size: $wk-size-6 !default;

/// h3 Font Size
///
/// @type Unit
$wk-h3-font-size: $wk-size-5 !default;

/// h4 Font Size
///
/// @type Unit
$wk-h4-font-size: $wk-size-4 !default;

/// h5 Font Size
///
/// @type Unit
$wk-h5-font-size: $wk-size-3 !default;

/// h6 Font Size
///
/// @type Unit
$wk-h6-font-size: $wk-size-3 !default;

/// Display 1 Font Size
///
/// @type Unit
$wk-display-1-font-size: $wk-size-11 !default;

/// Display 2 Font Size
///
/// @type Unit
$wk-display-2-font-size: $wk-size-10 !default;

/// Display 3 Font Size
///
/// @type Unit
$wk-display-3-font-size: $wk-size-9 !default;

/// Display 4 Font Size
///
/// @type Unit
$wk-display-4-font-size: $wk-size-8 !default;

// Variables for Links
// ============================================================================

/// Link color
///
/// @type Color
///
$wk-link-color: $wk-primary-blue-shade1 !default;

/// Link hover color
///
/// @type Color
///
$wk-link-hover-color: $wk-primary-blue-shade2 !default;

/// Link Decorated color
///
/// @type Color
///
$wk-link-decorated-color: $wk-primary-blue-shade1 !default;

/// Link Decorated hover color
///
/// @type Color
///
$wk-link-decorated-hover-color: $wk-primary-blue-shade2 !default;

/// Link Decorated visited color
///
/// @type Color
///
$wk-link-decorated-visited-color: $wk-secondary-purple !default;

/// Link Muted Color
///
/// @type Color
///
$wk-link-muted-color: $wk-primary-gray-shade1 !default;

/// Link Muted Hover Color
///
/// @type Color
///
$wk-link-muted-hover-color: $wk-primary-blue-shade1 !default;

/// Link Nav Color
///
/// @type Color
///
$wk-link-nav-color: $wk-primary-gray-tint1 !default;

/// Link Nav Light Color
///
/// @type Color
///
$wk-link-nav-light-color: $wk-primary-gray-shade1 !default;

/// Link Nav Light Color on Hover
///
/// @type Color
///
$wk-link-nav-light-hover-color: $wk-primary-blue-shade1 !default;

/// Link Nav Regular Color
///
/// @type Color
///
$wk-link-nav-regular-color: $wk-primary-gray-shade1 !default;

/// Link Nav Regular Color on Hover
///
/// @type Color
///
$wk-link-nav-regular-hover-color: $wk-primary-blue-shade1 !default;

/// Link Nav Color on Active
///
/// @type Color
///
$wk-link-nav-active-color: $wk-primary-blue-shade1 !default;

// Variables for tag styles (blockquote, coode, etc.)
// ============================================================================

/// blockquote border left color
///
/// @type Color
$wk-blockquote-border-left-color: $wk-primary-gray-tint4 !default;

/// blockquote border left style
///
/// @type String
$wk-blockquote-border-left-style: solid !default;

/// blockquote border left width
///
/// @type Unit
$wk-blockquote-border-left-width: wk-rem(4px) !default;

/// blockquote padding left
///
/// @type Unit
$wk-blockquote-padding-left: $wk-size-3 !default;

/// blockquote footer color
///
/// @type Color
$wk-blockquote-footer-color: $wk-primary-gray-tint1 !default;

/// blockquote footer font size
///
/// @type Unit
$wk-blockquote-footer-font-size: $wk-size-2 !default;

/// The border of a standalone <code> element
///
/// @type Color
$wk-code-border: wk-rem(1px) solid $wk-primary-gray-tint5 !default;

/// `<mark>` background color
///
/// @type Color
$wk-mark-background-color: $wk-highlight-yellow !default;

/// `<kbd>` background color
///
/// @type Color
$wk-kbd-background-color: $wk-primary-gray-shade2 !default;

/// `<kbd>` text color
///
/// @type Color
$wk-kbd-text-color: $wk-primary-white !default;
