@import '../../../../../styles/variables';

.coupon-code-card {
  width: 448px;
  padding: 28px 24px;
  background: $wk-primary-gray-tint6;

  @media (max-width: 500px) {
    max-width: 448px;
    width: auto;
  }

  & &__title {
    color: $wk-primary-black;
    font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 24px;
  }

  & &__input-wrapper {
    display: flex;
    gap: 12px;

    @media (max-width: 500px) {
      flex-direction: column;
    }
  }

  & &__input {
    width: 100%;

    & > [class^='cg-field'] {
      margin: 0;
    }

    & > [class^='cg-field']:not([class^='cg-field-errored']) {
      margin-bottom: 24px;
    }
  }

  & &__button {
    position: relative;

    padding: 10px 36px;
  }

  & &__button-spinner {
    position: absolute;
    left: 0;

    width: 100%;
    height: 100%;
  }
}
