@import '../../../styles/helpers';

.page :global {
  .wk-row {
    width: 100%;
  }

  .ac-float-side-bar-mobile-only {
    z-index: var(--z-index-history-sidebar-mobile-only);
  }
}

.filterWidget + .filterWidget {
  margin-top: 16px;
}

.searchTitle {
  font-size: 28px;
  line-height: 36px;
  font-weight: normal;
  color: $wk-primary-gray;
  margin-bottom: 14px;
}

.headerWrapper {
  @include breakpoint(min-width $wk-tablet-breakpoint) {
    display: flex;
    justify-content: space-between;
  }

  @include breakpoint(max-width $wk-tablet-breakpoint) {
    :global {
      .wk-display-3 {
        font-size: 1.375rem;
      }
      .history-search {
        margin-top: 0.625rem;
      }
    }
  }
}

.aside {
  min-width: 192px;
  margin-right: 34px;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.body {
  width: 100%;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.aside,
.body {
  @include breakpoint(max-width $wk-tablet-breakpoint) {
    flex-basis: 100%;
    max-width: 100%;
  }
}

.aside,
.body {
  @include breakpoint(max-width $wk-laptop-breakpoint) {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}
