@import '../../styles/variables';

.tab-panel {
  position: relative;
  display: flex;
  background-color: transparent;
  border-bottom: 1px solid $wk-primary-gray-tint4;

  :global {
    .tab-button {
      position: relative;
      border: 1px solid transparent;
      font-size: 14px;
      background-color: transparent;
      color: $wk-primary-gray-shade1;
      cursor: pointer;
      line-height: 1;
      padding: 13.3px 15.33px 10.7px;

      &.tab-button__active {
        color: $wk-primary-blue-shade1;
        border-color: $wk-primary-gray-tint4;
        background-color: white;

        &:after {
          content: '';
          display: inline-block;
          position: absolute;
          left: 0;
          top: calc(100% - 1px);
          height: 3px;
          width: 100%;
          background-color: white;
        }
      }
    }
  }
}

.tab-panel__buttons {
  display: flex;
  margin-bottom: -1px;
}

.tooltip {
  :global([data-e2e='cg-tooltip'].cg-tooltip-description) {
    padding: 4px 8px;
    max-width: 300px;
  }
}
