.toc-menu {
  margin: 30px 5px 0;
  max-width: 172px;

  .toc-menu__item {
    background-color: #f6f6f6;
    font-family: 'Fira Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    padding: 14px 16px;
    margin-bottom: 1px;
    cursor: pointer;
    user-select: none;

    &:hover {
      background-color: #bfbfbf;
      color: white;
    }

    &.toc-menu__item__active {
      background-color: #bfbfbf;
      color: white;
    }
  }
}
