.esg-tool-search-page-header__title {
  font-weight: 500;
  font-size: 36px;
  line-height: 46px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.esg-tool-search-page {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;

  & &__content {
    padding-left: 34px;
  }

  & &__spinner-background {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }

  & &__spinner {
    position: sticky;
    top: 50%;
    margin: 100px;
  }
}

.esg-tool-search-filters {
  & &__button-container {
    margin-top: 20px;
    margin-bottom: 36px;
  }

  & &__filters-container > *:not(:last-child) {
    margin-bottom: 30px;
  }
}
