@import '../../styles/variables';

.info-banner {
  height: 470px;
  display: flex;
  align-items: center;
  padding-left: 76px;

  @media (max-width: 600px) {
    padding-left: 0;
  }

  background: url('../../assets/img/infoBannerImg.jpg') no-repeat center/100%;
  background-size: cover;

  & &__box {
    max-width: 590px;
    padding: 24px;

    background: rgba(0, 0, 0, 0.8);

    &__title {
      font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
      font-size: 42px;
      font-weight: 300;
      line-height: 40px;
      color: $wk-primary-white;
      padding-bottom: 24px;
    }

    &__content {
      font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, 'Roboto', Arial, sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      color: $wk-primary-white;
    }
  }
}
