$home-page-col1-width: 535px;
$home-page-col2-width: 315px;
$home-page-col3-width: 425px;

.admin-entry-container {
  padding: 20px 0;

  .delete-outer-control {
    font-size: 24px;
  }

  .configuration-box-open-icon,
  .delete-inner-control,
  .remove-control {
    font-size: 20px;
  }

  .configuration-box-open-icon {
    color: #005b92;
  }

  .delete-inner-control,
  .delete-outer-control,
  .remove-control {
    color: #e5202e;
  }

  .add-item-outer-card-wrapper {
    font-size: 18px;
    margin: 20px 0;
  }

  .add-item-inner-card-wrapper,
  .add-sub-section-inner-card-wrapper {
    font-size: 16px;
  }

  .add-sub-section-inner-card-wrapper {
    margin-top: 10px;
  }

  .add-icon-inner-card-wrapper {
    justify-content: flex-end;
    padding: 8px;
  }

  .add-item-outer-card-wrapper,
  .add-item-inner-card-wrapper,
  .add-icon-inner-card-wrapper,
  .add-sub-section-inner-card-wrapper,
  .add-association-control {
    color: #005b92;
    display: flex;
    justify-content: flex-end;
    line-height: normal;

    &.widget-title-header-actions {
      justify-content: center;
    }

    &:hover {
      color: #003d61;
      cursor: pointer;

      .add-item-icon {
        font-weight: 600;
      }
    }
  }

  .add-item-outer-card-wrapper_last {
    @extend .add-item-outer-card-wrapper;
    margin-bottom: 0;
  }

  .base-card-controls-section {
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .button-cancel {
      background-color: #bfbfbf;

      &:hover {
        background-color: #232323;
      }
    }
    .button-save {
      background-color: #85bc20;

      &:hover {
        background-color: #425e10;
      }
    }
  }

  .active-input {
    box-shadow: inset 0 0 0 0.0625rem #005b92;
    border-color: #005b92;
  }

  .card-footer {
    align-items: center;
    justify-content: flex-end;
  }
}

.disable-pointer-events {
  pointer-events: none;
}

.preview-container {
  &-plain-card {
    width: $home-page-col3-width;

    &_has_icon {
      width: $home-page-col1-width;
    }
  }

  &-sections-card {
    width: $home-page-col2-width;
  }

  &-sections-and-groups-card {
    width: $home-page-col3-width;
  }
}
