.return-to-search-btn {
  & > span:last-of-type {
    margin-left: 8px;
  }
}

.terms-count {
  margin-bottom: 60px;

  &.terms-count--open {
    margin-bottom: 38px;
  }
}

.terms-count__toggle {
  user-select: none;
  font-size: 14px;

  & > span:first-of-type {
    margin-right: 10px;
    color: #005b92;
    text-decoration: underline;
  }
}
.terms-count__term {
  padding: 15px 0 0 23px;
  font-size: 14px;
  color: #000000;
}
