@import '~@wk/fundamentals';

@include breakpoint(max-width $wk-tablet-breakpoint) {
  .ac-float-side-bar-mobile-only__widget {
    height: 0;
  }

  .ac-float-side-bar-mobile-only {
    position: fixed;
    top: 0;
    left: -100vw;
    width: 100vw;
    height: 100vh;
    transition: transform 0.3s ease-in-out;
    background-color: #fff;
    overflow: auto;
    z-index: 13; //.wk-banner.wk-banner-sticky .wk-banner-container has 10, search-page__search-panel-wrapper - 12
  }

  .ac-float-side-bar-mobile-only_is-opened {
    transform: translateX(100vw);
  }

  .ac-float-side-bar-mobile-only__close-button-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
  }

  .ac-float-side-bar-mobile-only__widget {
    .ac-float-side-bar-mobile-only__open-button-container
      button.ac-float-side-bar-mobile-only__button.ac-float-side-bar-mobile-only__button_open {
      bottom: 10px;
      left: 20px;
      z-index: 10;
    }

    .ac-float-side-bar-mobile-only__close-button-container
      .ac-float-side-bar-mobile-only__button.ac-float-side-bar-mobile-only__button_close {
      position: absolute;
      right: 0;
      top: 0;
      padding: 12px;
    }
  }

  .ac-float-side-bar-mobile-only__body {
    padding: 32px 18px 16px 16px;
  }
}

.ac-float-side-bar-mobile-only__close-button-container,
.ac-float-side-bar-mobile-only__open-button-container {
  @include breakpoint(min-width $wk-tablet-breakpoint) {
    display: none;
  }
}
