@import '../../../../styles/variables';

.checklist-error-message {
  & &__error {
    display: flex;
    flex-direction: column;
  }

  & &__error-text {
    margin-bottom: 8px;

    font-size: 20px;
    font-weight: 500;
    line-height: 26px;
    color: $wk-primary-gray-shade2;
  }

  & &__note-text {
    font-size: 16px;
    line-height: 20px;
    color: $wk-primary-gray-shade2;
  }
}
