@import '@wk/components-react16/dist/tag.css';
@import '@wk/fundamentals/color/color.variables';

.list-item__link {
  display: flex;

  [class*='icon'] {
    color: $wk-primary-blue-shade1;
    margin: 0;
  }

  .cg-tag-gray {
    margin-right: 8px;
    margin-left: 4px;
  }
}

.list-item__link_disabled {
  $disabled-link-color: #bfbfbf;
  @extend .list-item__link;
  color: $disabled-link-color;

  &:hover {
    color: $disabled-link-color;
    text-decoration: none;
    cursor: default;
  }
}

.list-item__link-title + [class*='icon'],
[class*='icon'] + .list-item__link-title {
  margin-left: 8px;
}

.list-item__link-title {
  font-family: inherit;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0ms;
  transition-property: color, background-color, border;
  appearance: none;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  touch-action: manipulation;
  vertical-align: middle;
}

.list-item__all-link-title,
.list-item__link-title {
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
