$secondary-search-in-dropdown-breakpoint: 900px;

.scoped-search-controls {
  position: absolute;
  width: 210px;
  display: flex;
  align-items: center;
  gap: 16px;

  @media (min-width: $secondary-search-in-dropdown-breakpoint) {
    right: 45px;
  }

  @media (max-width: $secondary-search-in-dropdown-breakpoint) {
    padding-top: 10px;
  }

  &_loading {
    @extend .scoped-search-controls;

    @media (min-width: $secondary-search-in-dropdown-breakpoint) {
      right: 88px;
    }
  }

  &__control-text {
    font-size: 14px;
    line-height: 18px;
  }

  .wk-field-choice-label:first-child {
    padding: 8px 0;
  }
}
