@import '~@wk/fundamentals';
@import './search-page-layout/search-page-layout';
@import './wide-header-page-layout/wide-header-page-layout.scss';

div[class^='wk-row'].layout-row {
  margin-left: 0;
  margin-right: 0;
}

.admin-card-row {
  align-items: center;
  margin-bottom: 0;
}

.admin-card-label-col {
  display: flex;
  justify-content: center;
  padding: 0;

  &_icon-field {
    @extend .admin-card-label-col;
    justify-content: flex-end;
  }
}

.clear-padding-right {
  padding-right: 0;
}

.admin-card-header-add-icon-col {
  padding-right: 0;
  align-self: center;
}

@include breakpoint(max-width $wk-tablet-breakpoint) {
  div[class^='wk-col'].home-page_tablet-size,
  div[class^='wk-col'].search-page_tablet-size {
    flex-basis: 100%;
    max-width: 100%;
    min-width: 100%;
  }
}

@include breakpoint(max-width $wk-smartpad-breakpoint) {
  div[class^='wk-col'].home-page_mobile-size,
  div[class^='wk-col'].search-page_mobile-size {
    flex-basis: 100%;
    max-width: 100%;
    min-width: 100%;
  }
}
