@import '../../../../../../styles/variables';

.item-synonyms-setting-modal {
  [class^='cg-modal-header-'] {
    z-index: 2;

    [data-e2e="cg-tooltip"] {
      width: 304px;
    }
  }
  [class^='cg-modal-body-'] {
    padding: 0;
    overflow: hidden;
  }
}

.modal-body-content {
  position: relative;
  height: 100%;
  overflow-y: auto;
  padding: 0 16px 0 32px;
}

.synonyms-setting-body {
  display: flex;
  flex-direction: column;

  & &__list {
    flex-grow: 1;
  }

  & &__input-wrapper {
    display: flex;

    margin-top: 31px;
    padding-bottom: 24px;

    &__input {
      flex-grow: 1;

      & > [class^='cg-field'] {
        margin-bottom: 5px;
      }

      & [class^='cg-input-field-container'] {
        .synonyms-setting-body__input-wrapper__input-error {
          border: 2px solid #e5202e;
        }
      }
    }

    &__error {
      font-size: 14px;
      color: #e5202e;
      line-height: 18px;
    }

    &__button {
      margin-left: 10px;
    }
  }

  .synonyms-setting-control__wrapper {
    position: sticky;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: $wk-primary-white;
    border-top: 1px solid $wk-primary-gray-tint4;
    padding: 20px 0 16px;

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: -1px;
      right: -1px;
      top: 0;
      bottom: 0;
      z-index: -1;
      background-color: inherit;
    }
  }

  .synonyms-setting-control {
    display: flex;
    align-items: center;
  }

  .synonyms-setting-control__text {
    font-size: 14px;
  }
}

.synonyms-setting-footer {
  display: flex;
  justify-content: flex-end;
  padding-left: 16px;

  & &__text {
    font-size: 14px;
  }

  & &__button {
    margin-right: 16px;
  }
}

.tooltip__actions {
  width: 100%;
  display: flex;
  align-items: stretch;
  justify-content: flex-end;
}

.tooltip__body {
  font-weight: 400;
}

.tooltip__body-title {
  font-weight: 700;
}
