.move-control {
  &_size_small,
  &_size_medium,
  &_size_big {
    color: #005b92;
  }

  &_size_small_disabled {
    color: #a3a3a3;
    cursor: not-allowed;
  }

  &_size_medium,
  &_size_big {
    font-size: 24px;

    &_disabled {
      font-size: 24px;
      @extend .move-control_size_small_disabled;
    }
  }
}
