.document-search-terms-control_container {
  border-bottom: 1px solid #dadada;

  width: 100%;
  min-height: 36px;
  background: white;
  z-index: 1040;

  display: flex;
  align-items: center;
  justify-content: center;

  &.document-search-terms-control_sticky-container {
    position: fixed;
  }

    .document-search-terms-control {
        display: flex;
        flex-wrap: wrap;
        max-width: 83rem;
        flex-grow: 1;
    }

    .back-to-search-link_container {
        display: flex;
        align-items: center;
        padding-left: 24px;
        margin-right: auto;

        @media screen and (max-width: 768px) {
          padding-left: 0.5rem;
        }

      ~ .search-terms_toggle-container {
        @media screen and (max-width: 768px) {
          justify-content: center;
          padding-bottom: 0.5rem;
          margin-left: 0;
          flex: 0 0 100%;
          order: 3;
        }
      }

      ~ .search-terms-navigation {
        @media screen and (max-width: 768px) {
          margin-left: auto;
          padding-right: 0.5rem;
        }
      }
    }

    .back-to-search-link_link {
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;

        color: #005b92;
        border: none;
        background: none;
        padding: 0;

        .back-to-search-link_icon {
            font-size: 16px;
            line-height: 14px;
            margin-right: 10px;
            height: 12px;
            width: 15px;
            text-align: center;
            overflow: hidden;
        }

        .back-to-search-link_label {
            font-size: 14px;
            line-height: 12px;
            padding: 2px 0;
        }

        &:hover {
            color: #005b92;
            .back-to-search-link_label {
                text-decoration: underline;
            }
        }
    }

    .search-terms-navigation__label {
        white-space: nowrap;
        color: #353535;
        font-size: 14px;
        line-height: 14px;
        padding: 2px 0.5rem;
        margin-right: 5px;
        margin-left: 5px;
    }

    .search-terms-navigation {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    .search-terms_toggle-container {
        margin-left: auto;
        display: flex;
        align-items: center;
        padding: 0 0.5rem;
    }

    .search-terms_toggle-label {
        display: block;
        position: relative;

        cursor: pointer;
        font-size: 10px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

        margin-right: 12px;

        .search-terms_toggle {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }
        .search-terms_toggle-icon {
            position: absolute;
            top: 0;
            left: 0;

            translate: -50% -50%;

            height: 16px;
            width: 16px;
            background-color: #ffffff;
            color: transparent;
            border: 1px solid #000000;

            &::before {
                position: absolute;
                top: 3px;
                left: 2px;
            }
        }

        .search-terms_toggle:checked ~ .search-terms_toggle-icon {
            background-color: #005b92;
            color: #ffffff;
            border: 1px solid transparent;
            text-decoration: none;
        }

        .search-terms_toggle:disabled ~ .search-terms_toggle-icon {
            text-decoration: line-through;
        }

        &:hover .search-terms_toggle ~ .search-terms_toggle-icon,
        &:focus .search-terms_toggle ~ .search-terms_toggle-icon {
            outline: 2px solid #232323;
        }
    }

    .search-terms-navigation_buttons-container {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .search-terms-navigation_button {
        width: 32px;
        height: 32px;
        margin: 2px 1px;

        border: none;
        background: #005b92;

        display: flex;
        align-items: center;
        justify-content: center;

        & .icon {
            color: #ffffff;
            font-size: 14px;
            line-height: 14px;
        }
        &.disabled {
            background: #a6d1ea;

            .icon {
                color: #353535;
            }
        }
    }
}

.document-search-terms-control_spacer {
    height: 32px;
}
