@use '/app-react/src/styles/mixins' as mixins;

.summary-tab {
  &:global(.page-content) {
    :global {
      @media (max-width: 1023px) {
        flex-direction: column-reverse;

        .document-area {
          max-width: 100%;
        }
      }
    }
  }

  :global(.header_infoBannerVisible ~ .main-container) & :global {
    .summary-tab__document-actions {
      top: 290px;
    }
  }

  :global {
    .summary-tab__document-actions {
      position: sticky;
      top: 209px;
      z-index: 1;
      padding: 4px 0;

      @media (max-width: 1023px) {
        z-index: 0;
      }
    }

    .summary-tab__document-container {
      @include mixins.search-terms;
    }
  }
}
