.beyond-container {
  --beyond-size-1: 13px;
  --beyond-size0: 1rem;
  --beyond-size1: 20px;
  --beyond-size2: 28px;
  --beyond-size3: 32px;

  --beyond-answer-margin: 0 10px 16px 10px;
  --beyond-answer-padding: 15px 33px 15px 14px;
  --beyond-header-font-size: 1rem;
  --beyond-answer-border: 1px solid #bfbfbf;
  --beyond-answer-accordion-border: 1px solid #dadada;
  --beyond-answer-border-radius: 0;
  --wk-primary-green: #85bc20;
  --wk-primary-green-shade2: #425e10;
  --wk-secondary-orange: #ea8f00;
  --wk-primary-gray-tint1: #757575;
  --wk-primary-blue-shade1: #005b92;
  --wk-primary-purple: #af4995;
  --beyond-curated-label-color: var(--wk-primary-green-shade2);
  --beyond-ai-gen-label-color: var(--wk-secondary-orange);
  --beyond-community-curated-label-color: var(--wk-primary-purple);
}

/* All */
.beyond-answer {
  margin-bottom: 16px;
  border: var(--beyond-answer-border);
  border-radius: var(--beyond-answer-border-radius);
  background: white;
  overflow: hidden;
  text-wrap: pretty; /* not supported in every browser. */
}

.beyond-answer .beyond-source {
  color: var(--wk-primary-blue-shade1);
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
}

.beyond-box {
  padding: var(--beyond-size3);
  overflow: visible;
}

/*
  AnwserConnect decreases paddings of their top result item on small viewport
  beyond-answer needs to match it.

  should be extracted as a workspace customisation, currently our customisation
  is implemented on an extension level, which should be further reconsidered
*/
@media (max-width: 74.9375rem) {
  .beyond-box {
    padding: var(--beyond-size1);
  }
}

.beyond-collapsible {
  padding: var(--beyond-size-1) 0;
}

.beyond-container h3 {
  display: flex;
  gap: 8px;
  justify-content: space-between;
  align-items: center;
}

.beyond-answer h3 {
  margin: 0;
  font-size: var(--beyond-header-font-size);
}

.beyond-answer .beyond-question {
  margin-bottom: 0.6rem;
}

.beyond-answer .beyond-main-question {
  font-size: 19px;
  line-height: 26px;
  font-weight: 500;
}

.beyond-alternatives .beyond-question {
  font-size: 16px;
  line-height: 24px;
}

.beyond-content p {
  margin-bottom: 1.2rem;
  font-size: 16px;
  line-height: 24px;
}

.beyond-answer .beyond-pill,
.beyond-alternatives .beyond-pill {
  cursor: default;
  flex-shrink: 0;
  margin-bottom: 0.8rem;
  max-height: 100px;
  transition: all 0.3s ease-out;
  color: white;
}

.beyond-collapsible .beyond-pill {
  display: none;
}

.beyond-answer .beyond-pill-approved,
.beyond-alternatives .beyond-pill-approved {
  color: var(--beyond-curated-label-color);
}

.beyond-answer .beyond-pill-ai {
  color: var(--beyond-ai-gen-label-color);
}

.beyond-answer .beyond-pill-community {
  color: var(--beyond-community-curated-label-color) !important;
}

.beyond-answer ol {
  padding-left: 0.8rem;
}

.beyond-answer li {
  margin: 0;
}

.beyond-accordion .beyond-content {
  overflow: hidden;
  transition: all 0.3s ease-out;
}

.beyond-alternatives {
  border: var(--beyond-answer-border);
  margin: 0;
  margin-bottom: 16px;
}

.beyond-alternatives > h3 {
  margin-bottom: 1rem;
}

.beyond-accordion .beyond-accordion-item {
  margin: 0;
  border-radius: 0;
  border: none;
  border-bottom: var(--beyond-answer-accordion-border);
}

.beyond-accordion .beyond-accordion-item:first-child {
  border-top-left-radius: var(--beyond-answer-border-radius);
  border-top-right-radius: var(--beyond-answer-border-radius);
  padding-top: 0;
}

.beyond-accordion .beyond-accordion-item:last-child {
  border-bottom-left-radius: var(--beyond-answer-border-radius);
  border-bottom-right-radius: var(--beyond-answer-border-radius);
}

.beyond-accordion * + * {
  margin-top: 0;
  border-top: none;
}

.beyond-answer .beyond-pill,
.beyond-alternatives .beyond-pill {
  max-width: fit-content;
  border-radius: 1.5rem;
  size: auto;
  white-space: nowrap;
  font-size: 14px;
  text-transform: capitalize;
}

/* #region collapsible */

.beyond-collapsible.isCollapsed .beyond-content {
  max-height: 0;
  transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
}

.beyond-collapsible .beyond-content {
  max-height: 1000px;
  transition: max-height 1s ease-in-out;
}

.beyond-appear {
  animation: appearance 0.5s ease-out;
}

@keyframes appearance {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.beyond-collapsible-button:hover {
  cursor: pointer;
}

.beyond-collapsible-chevron {
  display: block;
  box-sizing: border-box;
  position: relative;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px;
}

.beyond-collapsible-chevron::after {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid;
  border-right: 2px solid;
  transition: transform 0.3s ease;
  left: 4px;
  top: 2px;
}

:not(.isCollapsed) .beyond-collapsible-chevron::after {
  transform: rotate(-135deg);
}

.isCollapsed .beyond-collapsible-chevron::after {
  transform: rotate(45deg);
}

.beyond-answer.isCollapsed .beyond-question {
  margin: 0;
}

/* #endregion */

/* #region tooltip */
.beyond-tooltip-holder:hover {
  cursor: pointer;
}

.beyond-tooltip-holder:hover .beyond-tooltip {
  display: block;
  visibility: visible;
  opacity: 1;
}

.beyond-tooltip {
  padding: 10px;
  color: rgba(0, 0, 0, 0.85);
  background: white;
  border-radius: 2px;
  border: 1px solid #bfbfbf;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  text-transform: none;
  font-weight: 400;
  letter-spacing: normal;
  max-width: 300px;
}

.beyond-answer .wk-icon-filled-check-circle {
  color: var(--beyond-curated-label-color);
}

.beyond-answer .wk-icon-filled-caution,
.beyond-answer .wk-icon-filled-check-circle {
  margin-left: auto;
}

.beyond-answer .wk-icon-filled-caution {
  color: var(--beyond-ai-gen-label-color);
}

.beyond-tooltip::after {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 10px;
  margin-top: -7px;
  transform: translateX(-50%) rotate(45deg);
  left: 50%;
  top: 2px;
  background: linear-gradient(to bottom right, white 50%, white 1%, white 49%);
  box-shadow: -1px -1px 2px rgba(0, 0, 0, 0.12);
  border-top: 1px solid #bfbfbf;
  border-left: 1px solid #bfbfbf;
}

.beyond-tooltip-text {
  display: block;
  max-width: 300px;
  white-space: normal;
}

/* #endregion */

.beyond-sources ul {
  padding-left: 0;
}

.beyond-answer .beyond-sources li {
  margin-top: 0.5rem;
}

.beyond-answer .beyond-oneline-text {
  display: block;
}

.beyond-answer .beyond-oneline-text > *:first-child {
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 100%;
}

.beyond-answer .beyond-sources-section-label {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 11px;
  line-height: 16px;
  color: var(--wk-primary-gray-tint1);
  letter-spacing: 1px;
}

.beyond-sources {
  max-width: 80%;
}

/* #region likes */
.beyond-likes {
  display: flex;
  margin: 0px;
  padding: 0px;
  > * {
    margin: auto !important;
    padding: 0px;
    margin-left: 2px !important;
    margin-right: 2px !important;
  }
  > span {
    cursor: pointer;
  }
}

.beyond-footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

/* #endregion */
