@import '../../../../../../styles/variables';

.input-suggestions {
  & > [class^='cg-field'] {
    margin-bottom: 0px;
  }

  & &__container {
    position: relative;
  }

  & &__list {
    position: absolute;
    top: 0;
    z-index: 1;

    width: 100%;
    max-height: 400px;
    padding: 4px 0;
    background-color: $wk-primary-white;
    border: 1px solid $wk-primary-gray-tint4;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
    overflow: auto;
  }

  & &__item {
    margin: 0;
    padding: 4px 16px;

    font-size: 14px;
    line-height: 23px;
    word-break: break-word;

    &:hover,
    &:focus {
      background-color: $wk-primary-blue-tint6;
      cursor: pointer;
    }
  }
}
