@import './../grid/grid.mixins';
/// Displays content for screen readers only.
///
/// @author [Gaël Poupard](https://gist.github.com/ffoodd/000b59f431e3e64e4ce1a24d5bb36034) - Modified by [*/dxg](https://dxg.wolterskluwer.io/)
/// @example scss - Usage
///   .element {
///       @include wk-sr-only;
///   }
@mixin wk-sr-only {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: wk-rem(1px);
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

/// Use in conjunction with `wk-sr-only` to display content when focused.
/// @author [Gaël Poupard](https://gist.github.com/ffoodd/000b59f431e3e64e4ce1a24d5bb36034) - Modified by [*/dxg](https://dxg.wolterskluwer.io/)
/// @example scss - Usage
///   .element {
///       @include wk-sr-only;@if $wk-rem-px-only
///       @include wk-sr-only-focusable;
///   }
///

@mixin wk-sr-only-focusable {
    &:active,
    &:focus {
        clip: auto;
        clip-path: none;
        height: auto;
        overflow: visible;
        white-space: normal;
        width: auto;
    }
}

/// Link mixin. Easy link styles for elements
///
/// @param {Color} $color [$wk-link-color]
/// @param {Color} $hover-color [$wk-link-hover-color]
///
/// @example scss - Usage
///   .element {
///       @include wk-link(#0668a9, #64a137);
///   }

@mixin wk-link($color: $wk-link-color, $hover-color: $wk-link-hover-color) {
    color: $color;

    &:hover {
        color: $hover-color;
    }
}

/// Removes default styling from lists
///
/// @param {Bool} $child-blocks [false] - make child `li` and `a` elements display block
///
/// @example scss - Usage
///   .some-list {
///     @include wk-unstyle-list;
///   }
///

@mixin wk-unstyle-list($child-blocks: false) {
    list-style: none;
    margin: 0;
    padding: 0;

    @if $child-blocks {
        > li {
            display: block;

            > a {
                display: block;
            }
        }
    }
}

/// Basic clearfix mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-clearfix;
///   }
///
@mixin wk-clearfix {
    &::after {
        clear: both;
        content: ' ';
        display: table;
    }
}

/// Select children each `$num`.
/// @content [Write the style you want to apply to the children, and it will be added within the @content directive]
/// @param {number} $num - id of the child
/// @author [Family.scss](https://lukyvj.github.io/family.scss/)
@mixin wk-each($num) {
    &:nth-child(#{$num}n) {
        @content;
    }
}

/// Creates a multiline box with ellipsis text
///
/// @param {Unit} $line-count Max line count before text is being started cutting
/// @param {Unit} $line-height The box line height (Required by IE11 only)
/// @param {Unit} $font-size The box font size (Required by IE11 only)
/// @param {Color} $bg-color Background color for the rest content (Required by IE11 only)
@mixin wk-line-clamp(
    $line-count: 1,
    $line-height: 1.2,
    $font-size: 1rem,
    $bg-color: white
) {
    overflow: hidden;
    display: -webkit-box;

    /* autoprefixer: ignore next */
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line-count;

    // IE11 doesn't support line-clamp built-in
    @media all and (-ms-high-contrast: none) {
        position: relative;
        line-height: $line-height;
        max-height: $line-height * $font-size * $line-count;
        text-align: justify;
        margin-right: -1em;
        padding-right: 1em;

        &::before {
            content: '...';
            position: absolute;
            right: 0;
            bottom: 0;
        }

        &::after {
            content: '';
            position: absolute;
            right: 0;
            width: 1em;
            height: 1em;
            margin-top: 0.2em;
            background: $bg-color;
        }
    }
}

@mixin wk-full-width-bg($color) {
    $max-width: map-get($wk-page-container-max-width, hd);
    $hd-plus-offset: calc(-50vw + #{$max-width/2});

    position: relative;

    &::before {
        background-color: $color;
        content: '';
        height: 100%;
        left: 0;
        right: 0;
        position: absolute;
        top: 0;
        z-index: -1;

        @include from(hd-plus) {
            left: $hd-plus-offset;
            right: $hd-plus-offset;
        }
    }
}

@mixin wk-half-width-right-bg($color) {
    $max-width: map-get($wk-page-container-max-width, hd);
    $hd-plus-offset: calc(-50vw + #{$max-width/2});

    &::after {
        background-color: $color;
        content: '';
        height: 100%;
        right: 0;
        position: absolute;
        top: 0;
        width: 50vw;
        z-index: -1;

        @include from(hd-plus) {
            right: $hd-plus-offset;
        }
    }
}

/// Generate colors classes based on set and prefix
///
/// @param {Map} $set-name - color set on from existing ($wk-primary-colors, $wk-secondary-colors, $wk-highlight-colors)
/// @param {String} $prefix - for generated classes e.g (wk-primary)
///
/// @example scss - Usage
/// @include generateColorClasses($wk-primary-colors, 'wk-primary')
///
/// @output css
/// .wk-primary-black {
///    color: #000
/// }
/// .wk-primary-red-shade2 {
///    color: #721017
/// }
/// etc....

@mixin generateColorClasses($set-name, $prefix) {
    @each $color-name, $set in $set-name {
        @each $variation, $hex in $set {
            @if ($variation == 'base') {
                // type casting of color-name is required
                .#{$prefix}-#{'' + $color-name} {
                    color: $hex;
                }
            } @else {
                // type casting of color-name is required
                .#{$prefix}-#{'' + $color-name}-#{$variation} {
                    color: $hex;
                }
            }
        }
    }
}

/// Generate background colors classes based on set and prefix
///
/// @param {Map} $set-name - color set on from existing ($wk-primary-colors, $wk-secondary-colors, $wk-highlight-colors)
/// @param {String} $prefix - for generated classes e.g (wk-bg-primary)
///
/// @example scss - Usage
/// @include generateColorClasses($wk-primary-colors, 'wk-bg-primary')
///
/// @output css
/// .wk-bg-primary-black {
///    background-color: #000
/// }
/// .wk-bg-primary-red-shade2 {
///    background-color: #721017
/// }
/// etc....

@mixin generateBackgroundColorClasses($set-name, $prefix) {
    @each $color-name, $set in $set-name {
        @each $variation, $hex in $set {
            @if ($variation == 'base') {
                // type casting of color-name is required
                .#{$prefix}-#{'' + $color-name} {
                    background-color: $hex;
                }
            } @else {
                // type casting of color-name is required
                .#{$prefix}-#{'' + $color-name}-#{$variation} {
                    background-color: $hex;
                }
            }
        }
    }
}

/// Fallback for Edge & IE 11
///
/// @param {Bool} $edge [false] - is for Edge
/// @param {Bool} $ie [false] - is for IE 11
///
/// @example scss - Usage
///   .element {
///     @include wk-fallback-ms($edge: true, $ie: true) {
///       outline: 0;
///     }
///   }
///
@mixin wk-fallback-ms($edge: false, $ie: false) {
    // Fallback for Edge
    @if $edge {
        @supports (-ms-ime-align: auto) {
            @content;
        }
    }

    // Fallback for IE 11
    @if $ie {
        @media screen and (-ms-high-contrast: active),
            (-ms-high-contrast: none) {
            @content;
        }
    }
}

/// Outline mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-outline;
///   }
///
@mixin wk-outline($color: $wk-outline-color, $offset: $wk-outline-offset) {
    @if $wk-outline-enabled {
        outline-color: $color;
        outline-offset: $offset;
        outline-style: $wk-outline-style;
        outline-width: $wk-outline-width;
    }
}

/// Inner outline mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-inner-outline;
///   }
///
@mixin wk-inner-outline {
    @if $wk-outline-enabled {
        outline-color: $wk-outline-color;
        outline-offset: $wk-inner-outline-offset;
        outline-style: $wk-outline-style;
        outline-width: $wk-outline-width;

        @include wk-fallback-ms($ie: true) {
            outline: 0;
            box-shadow: inset 0 0 0 $wk-outline-width $wk-outline-color;
        }
    }
}

/// Shadow outline mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-shadow-outline;
///   }
///
@mixin wk-shadow-outline($box-shadow: $wk-shadow-outline) {
    @if $wk-outline-enabled {
        box-shadow: $box-shadow;
        outline: 0;
    }
}

/// Force browser outline mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-force-browser-outline;
///   }
///
@mixin wk-force-browser-outline {
    @if not $wk-outline-enabled {
        outline: 1px dotted #212121;
        outline: 5px auto -webkit-focus-ring-color;
    }
}

/// Is disabled mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-is-disabled;
///   }
///
@mixin wk-is-disabled($opacity: $wk-disabled-opacity) {
    opacity: $opacity;
    pointer-events: none;
}

/// Is truncated mixin
///
/// @example scss - Usage
///   .element {
///       @include wk-is-truncated;
///   }
///
@mixin wk-is-truncated {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

/// Default WK Focus
///
/// @example scss - Usage
///   .element {
///       @include wk-focus;
///   }
///
@mixin wk-focus(
    $is-inner-outline: false,
    $outline-color: $wk-outline-color,
    $outline-offset: 0px
) {
    @if ($is-inner-outline) {
        &:focus {
            @include wk-inner-outline;
        }
    } @else {
        &:focus {
            @include wk-outline($outline-color, $outline-offset);
        }
    }

    &:focus:not(:focus-visible) {
        outline: 0;
    }
}

/// Default WK Focus with Shadow outline
///
/// @example scss - Usage
///   .element {
///       @include wk-focus-shadow();
///   }
///
@mixin wk-focus-shadow($box-shadow: $wk-shadow-outline) {
    &:focus {
        @include wk-shadow-outline($box-shadow);
    }

    &:focus:not(:focus-visible) {
        outline: 0;
        box-shadow: none;
    }
}
