@import '../../../styles/variables';
@import '/app-react/src/styles/mixins';

.browse-tree-page {
  :global(.header_infoBannerVisible ~ .main-container) & :global {
    .wide-header-page__header {
      top: calc(89px + 76px);

      @media (max-width: 600px) {
        top: 164px;
      }
    }

    .document-search-terms-control_container {
      top: calc(162px + 76px);

      @media (max-width: 600px) {
        top: 238px;
      }
    }
  }

  :global {
    .wide-header-page__main {
      padding-top: 0;
      padding-bottom: 0;
      display: block;
    }

    .hidden {
      display: none;
    }

    .wide-header-page__header {
      position: sticky;
      top: 89px;
      z-index: 7;

      @media (max-width: 767px) {
        top: 56px;
      }

      @media (max-width: 600px) {
        top: 48px;
      }

      > .wk-page-container:not(.wk-row) {
        @media screen and (min-width: 75rem) {
          max-width: unset;
          padding-left: 44px;
          padding-right: 44px;
        }
      }
    }

    .document-search-terms-control_container {
      position: sticky;
      top: 162px;

      .back-to-search-link_container {
        padding-left: 10px;
      }

      .document-search-terms-control {
        min-height: 36px;
      }

      @media screen and (max-width: 1024px) {
        .back-to-search-link_container {
          padding-left: 0;
        }

        padding-left: 25px;
        padding-right: 15px;
      }

      @media screen and (max-width: 600px) {
        top: 120px;
      }
    }
  }

  .browse-tree-page__content-container {
    &:global(.wk-page-container) {
      max-width: 100%;
      padding: 0;

      > :global(.wk-row) {
        display: block;
        margin: 0;
      }

      & > :global(.wk-row > .wk-page-container) {
        @media screen and (min-width: 75rem) {
          max-width: unset;
          padding-left: 44px;
          padding-right: 44px;
        }
      }
    }
  }
  .tabs-panel-wrapper {
    position: sticky;
    height: 60px;
    z-index: 2;
    overflow: hidden;

    .tabs-panel {
      padding-top: 20px;
      flex: 0 0 100%;
      width: 100%;

      position: static;
      background-color: #fff;

      & > :global(.wk-page-container) {
        max-width: unset;
        padding-left: 0;
        padding-right: 0;
      }

      :global(.tab-button) {
        white-space: nowrap;
      }
    }
  }
}

.browse-tree-with-document {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;

  .browse-tree {
    height: 100%;
    position: sticky;
    z-index: 1;
  }

  :global {
    .document-content {
      @include search-terms;
    }
  }

  .document-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    height: 100%;

    &.display-in-column {
      position: relative;
      z-index: 1;
      flex-direction: column-reverse;
      align-items: normal;
      justify-content: flex-end;

      .document-content {
        width: 100%;
      }
      .document-actions {
        z-index: 2;
        padding: 0 0 16px 38px;
        width: 100%;
        background-color: white;
        height: unset;
      }
    }

    .document-content {
      padding-left: 48px;
      padding-right: 20px;
      position: relative;
      width: calc(100% - 230px);
    }

    .document-actions {
      position: sticky;
      width: 230px;
      padding: 4px 4px 4px 0;
      top: 160px;

      :global(.document-actions) {
        flex-wrap: wrap;
        padding-bottom: 27px;
        padding-top: 4px;
      }
    }
  }
}
