@import '../../../../../styles/helpers';
@import '../ac-banner/ac-banner.scss';

$container-height_mobile: 40px;
$container-height: 56px;
$container-width: 169px;
$container-width_mobile: 155px;

.brand-logo__banner {
  @extend %banner;
}

.brand-logo__banner-container {
  @extend %banner-container;
}

.brand-logo__banner,
.brand-logo {
  &:global(.hide-from-tablet-breakpoint) {
    display: flex;

    @include breakpoint(min-width $wk-tablet-breakpoint) {
      display: none;
    }
  }

  &:global(.hide-till-tablet-breakpoint) {
    display: none;

    @include breakpoint(min-width $wk-tablet-breakpoint) {
      display: flex;
    }
  }
}

.brand-logo {
  height: $container-height_mobile;
  max-width: $container-width_mobile;
  width: fit-content;
  display: flex; //to make image responsive in container
  align-items: center;
  justify-content: center;
  overflow: hidden;

  @include breakpoint(min-width $wk-max-tablet-breakpoint) {
    height: $container-height;
    max-width: $container-width;
  }
}

.brand-logo__image {
  max-height: $container-height_mobile;

  @include breakpoint(min-width $wk-max-tablet-breakpoint) {
    max-height: $container-height;
  }
}
