@import '@wk/fundamentals';

.save-to-favorites-button {
  border: none;
  background: none;
  transition: color 0.3s ease-in-out, text-decoration-color 0.3s ease-in-out;

  &:disabled {
    cursor: default;
    color: $wk-primary-gray-tint4;
  }

  &:not(:disabled) .save-to-favorites-content:hover {
    .save-to-favorites-label,
    [class*='icon'] {
      color: $wk-primary-blue-shade2;
      text-decoration-color: $wk-primary-blue-shade2;
      cursor: pointer;
    }
  }
}

.save-to-favorites-button__icon {
  color: $wk-primary-gray-shade1;
  line-height: 13px;
  padding: 8px;
  border: 2px solid transparent;
  cursor: pointer;
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out, text-decoration-color 0.3s ease-in-out;

  &:hover:not(:disabled) {
    background: $wk-primary-blue-tint5;
  }

  &:focus:not(:disabled) {
    border-color: $wk-primary-gray-shade1;
  }

  &:active:not(:disabled),
  &.save-to-favorites-button__active {
    color: $wk-primary-blue-shade1;
    background-color: $wk-primary-blue-tint5;
  }
}

.save-to-favorites-button__active {
  color: $wk-primary-blue-shade1;
  background-color: $wk-primary-blue-tint5;
}

.save-to-favorites-label {
  color: $wk-primary-blue-shade1;
  text-decoration: underline;
  text-decoration-color: transparent;
  font-size: 14px;
  line-height: 18px;
  margin-left: 8px;
  transition: text-decoration-color 0.3s ease-in-out;
}
