@import '../../../../styles/variables';

.group {
  border-bottom: 1px solid $wk-primary-gray-tint4;
  padding: 20px 0 18px;

  ul {
    margin-top: 0;
  }
}

.group-wrapper {
  margin-top: 0;

  &:last-child .group {
    border-bottom: none;
  }
}

.group-title {
  font-family: 'Fira Sans', sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 26px;
  color: $wk-primary-gray;
}

.flat-list-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 0;

  a {
    font-weight: 400;
  }
}
