.home-checklists {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;

  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
    max-width: 1415px;
    width: 100%;
  }

  &__content-wrapper {
    position: relative;
  }

  &__spinner-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &__spinner {
    position: sticky;
    top: 50%;
    margin: 100px;
  }

  &__content-error {
    margin-top: 36px;
  }

  &__permissions-error {
    margin-top: 30px;
  }

  &__warning-message-wrapper {
    @media screen and (min-width: 600px) {
      display: none;
    }
  }

  & &__header {
    font-size: 20px;
    font-weight: 500;
    line-height: 26px;

    margin-bottom: 16px;
  }

  & &__filter-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 16px;

    &__search {
      flex-grow: 1;
    }
  }

  & &__filter-wrapper {
    display: flex;
    align-items: center;

    & > *:not(:last-child) {
      margin-right: 16px;
    }
  }

  @media (max-width: 1500px) {
    padding: 10px 32px;
  }
}
