@import '../../styles/helpers';

:global {
  .sub-header {
    border-bottom: 1px solid #dadada;

    &.wk-banner-sticky {
      top: 56px;
      position: sticky;
      background-color: $wk-primary-white;
      z-index: var(--z-index-subheader);
      display: block;
    }

    &,
    &.wk-banner-sticky {
      @include breakpoint(max-width $wk-tablet-breakpoint) {
        display: none;
      }
    }
  }

  .header_infoBannerVisible ~ .sub-header.wk-banner-sticky {
    top: 133px;
  }
}
