@import '../../../../../../styles/variables';

.site-info {
  padding-bottom: 38px;

  :global {
    .site-info-title {
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
      color: $wk-primary-gray-tint1;
    }

    .site-info-text {
      display: block;
      width: fit-content;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      color: $wk-primary-gray-tint1;
      padding-top: 14px;
    }
  }
}
