@import '../../../../filters-common-styles';

.grouped-list-filter-truncated {
  .grouped-list-filter-truncated__applied-filter-list {
    margin: 0;
  }

  .grouped-list-filter-truncated__applied-filter:last-of-type {
    margin-bottom: $vertical-space-between-filter-items-and-more-button;
  }

  .grouped-list-filter-truncated__applied-filter + .grouped-list-filter-truncated__applied-filter {
    margin-top: $vertical-space-between-filter-items;
  }

  .grouped-list-filter-truncated__applied-filter {
    @extend %filter-item;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    letter-spacing: 0;
    margin-top: 0;
    max-width: fit-content;
  }

  .grouped-list-filter-truncated__select-button {
    @extend %filter-button-show-more;
    padding: 0;
  }
}
