@import '../../../../../styles/variables';

.view360-main-listing {
  display: block;

  > :global(.wk-page-container) {
    > :global(.wk-row) {
      width: 100%;
    }
  }
}

.view360-listing {
  padding-left: 0;

  > ul > :global(.wk-page-container) {
    padding-left: 0;
  }
}

.view360-listing__group {
  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  h2 {
    font-family: 'Fira Sans', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: $wk-primary-black;
    margin-bottom: 12px;
  }
}

.view360-listing__node {
  &:not(:last-of-type) {
    margin-bottom: 12px;
  }

  a {
    font-family: 'Fira Sans', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: $wk-primary-blue-shade1;
  }
}
