@import '/app-react/src/styles/mixins';

:global {
  .multi-document {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    z-index: 1;
    align-items: flex-start;

    @include search-terms();

    .comb-document-actions {
      position: sticky;
    }

    &:not(.inline) {
      .comb-document-actions {
        padding: 0 0 16px 38px;
        width: 100%;
        background-color: white;
        height: unset;
      }
    }

    .comb-document-actions {
      z-index: 2;
      width: 100%;
      margin-left: 0;
      position: sticky;
      height: unset;
      background-color: $wk-primary-white;
      padding: 4px 0 16px 34px;
      top: 160px;

      &.without-selected-documents {
        display: none;
      }

      .document-actions {
        padding-bottom: 27px;
        padding-top: 0;
      }
    }

    &.inline {
      align-items: flex-start;
      justify-content: space-between;
      flex-direction: row;

      .comb-document-actions {
        width: 233px;
        padding: 4px 0;
        position: sticky;
        top: 160px;
        background-color: transparent;

        &.without-selected-documents {
          display: none;
        }
      }

      .scroll-container {
        width: calc(100% - 233px);
        top: 50px;
      }
    }

    .scroll-container {
      width: 100%;
      scroll-behavior: smooth;
      padding-left: 48px;
      padding-right: 20px;

      .scroll-item {
        width: auto;
        border-bottom: 1px solid $wk-primary-gray-tint5;
        padding: 24px 0 20px;
        min-height: 50vh;

        .document-content {
          margin-top: 0;
          margin-bottom: 0;

          @include search-terms();
        }

        .docCopyrightInfo {
          display: none;
        }

        &:last-child {
          .docCopyrightInfo {
            display: block;
          }

          margin-bottom: 10px;
        }

        > .document-content > .spinner,
        > .spinner {
          min-height: 100vh;
        }
      }
    }
  }
}
